import React from "react";

import CHANNEL from "../constants/channel";

import Grid from "./Grid";
import MonteCarlo from "./MonteCarlo";
import PlainValue from "./PlainValue";

function Value(props) {
  const {
    assignment,
    canZoomOut,
    contextMenu,
    disableRunButton,
    editActions,
    editingChannel,
    editingCoords,
    editingFormula,
    elem,
    elemDetails,
    formulaInput,
    // model,
    modelDetailsActions,
    assign,
    send,
    showLabel,
    showMessage,
    startEditingAreaLabel,
    uiActions,
    wideEditor,
    zoomIn,
    zoomOut,
  } = props;

  function finishEditingStatus() {
    if (!wideEditor) {
      editActions.resetEditingStatus(); // sets formula to undefined
    }
  }

  function requestFormulaToUpdate(expression, channel) {
    send("requestFormulaToUpdate", "formula(" + expression + ")", channel);
    // Next must use CHANNEL.SYSTEM not CHANNEL.ZS_VIZ (test editing cells in 3T to see why)
    send("requestFormulaToUpdate", "ltype(" + expression + ")", CHANNEL.SYSTEM);
  }

  // When the expression type is a lazy T, this component
  // dissapears to show the input to edit the formula
  function _Grid(formula, lazyArray, type) {
    const data = elem.value;

    return (
      <Grid
        data={data}
        canZoomOut={canZoomOut}
        channel={elem.channel} // !!
        gridName={elem.channel}
        expression={elem.expression}
        editingRowNumber={elemDetails.editingRowNumber}
        editingColumnNumber={elemDetails.editingColumnNumber}
        headerLabels={elemDetails.headerLabels}
        lowRow={elemDetails.subLimits[0]}
        uppRow={elemDetails.subLimits[1]}
        rowCount={elemDetails.rowCount}
        startEdition={elemDetails.startEdition}
        base={elemDetails.type.base}
        minimumBatchSize={elemDetails.minimumBatchSize}
        orientation={elemDetails.orientation}
        rtype={elemDetails.rtype}
        showIndexes={elemDetails.showIndexes}
        usesDataLayer={elemDetails.usesDataLayer}
        formula={formula}
        lazyArray={lazyArray}
        type={type}
        assignment={assignment}
        contextMenu={contextMenu}
        editAreaValue={uiActions.editAreaValue}
        editFormulaInput={uiActions.editFormulaInput}
        editingCoords={editingCoords}
        updateCurrentContextMenu={uiActions.updateCurrentContextMenu} // !! change name
        updateEditingCoords={uiActions.updateEditingCoords}
        finishEditingStatus={finishEditingStatus}
        formulaInput={formulaInput}
        modelDetailsActions={modelDetailsActions}
        toggleWideEditor={uiActions.toggleWideEditor}
        requestFormulaToUpdate={requestFormulaToUpdate}
        showMessage={showMessage}
        assign={assign}
        send={send}
        wideEditor={wideEditor}
        zoomIn={zoomIn}
        zoomOut={zoomOut}
      />
    );
  }

  function _MonteCarlo() {
    return (
      <MonteCarlo
        message={elem.value}
        variableName={elem.channel}
        disableRunButton={disableRunButton}
      />
    );
  }

  function _PlainValue(formula) {
    return (
      <PlainValue
        assignment={assignment}
        canZoomOut={canZoomOut}
        channel={elem.channel}
        editAreaValue={uiActions.editAreaValue}
        editFormulaInput={uiActions.editFormulaInput}
        expression={elem.expression}
        finishEditingStatus={finishEditingStatus}
        formula={formula}
        formulaInput={formulaInput}
        ltype={elemDetails.ltype}
        requestFormulaToUpdate={requestFormulaToUpdate}
        send={send}
        startEditingAreaLabel={startEditingAreaLabel}
        showMessage={showMessage}
        assign={assign}
        toggleWideEditor={uiActions.toggleWideEditor}
        value={elem.value}
        wideEditor={wideEditor}
        zoomOut={zoomOut}
      />
    );
  }

  function render() {
    const formula =
      editingChannel === elem.channel && editingFormula
        ? editingFormula
        : undefined;

    const type = elemDetails.type.visualizeAs;
    const lazyArray = type === "0-grid";

    let rowCount = elemDetails.rowCount;
    // ad-hoc handling of invalid values
    if (elem.value.startsWith("invalid(")) {
      // ugly ad-hoc management of invalid values
      // logger.debug("Value.js", "HORROR", elem);
      rowCount = 0;
    }

    let ItemValueElement = null;
    if (0 < rowCount && elem.value !== "" && elem.value !== "[]") {
      ItemValueElement = _Grid(formula, lazyArray, type);
    } else if (elem.expression === "@MC" && elem.value !== "") {
      ItemValueElement = _MonteCarlo();
    } else {
      ItemValueElement = _PlainValue(formula);
    }

    const paddingTop = showLabel ? "8px" : "16px";

    return (
      <div className="elem-value" style={{ paddingTop: paddingTop }}>
        {ItemValueElement}
      </div>
    );
  }

  return render();
}

export default Value;
