// Action types

const SET_DRAG_STATUS = "SET_DRAG_STATUS";

// Action constructors

export function setDragStatus(status) {
  return { type: SET_DRAG_STATUS, status };
}

// Reducer

const INITIAL_STATE = {
  dragStatus: false,
};

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case SET_DRAG_STATUS:
      return {
        dragStatus: action.status,
      };

    default:
      // console.error(`dragAndDrop.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
