// Action types
const ADD_TAB_ELEM = "ADD_TAB_ELEM";
const REMOVE_TAB_ELEM = "REMOVE_TAB_ELEM";
const EDIT_EXPRESSION = "EDIT_EXPRESSION";
const EDIT_ELEM_VALUE = "EDIT_ELEM_VALUE";
const EDIT_ELEM_STACK = "EDIT_ELEM_STACK";
const RESET_ELEM_PROPS = "RESET_ELEM_PROPS";

// Action constructors (a.k.a. action creators)
// functions that listen to DOM or server events and return JSON “Action” objects

export function addTabElem(channel, expression) {
  return { type: ADD_TAB_ELEM, channel, expression };
}

export function removeTabElem(channel) {
  return { type: REMOVE_TAB_ELEM, channel };
}

export function editExpression(channel, expression) {
  return { type: EDIT_EXPRESSION, channel, expression };
}

export function editElemValue(channel, value) {
  return { type: EDIT_ELEM_VALUE, channel, value };
}

export function editElemStack(channel, stack) {
  return { type: EDIT_ELEM_STACK, channel, stack };
}

export function resetElemProps(channel) {
  return { type: RESET_ELEM_PROPS, channel };
}

// Reducer

function reducer(state = {}, action) {
  switch (action.type) {
    case ADD_TAB_ELEM:
      const { channel, expression } = action;
      return {
        ...state,
        [channel]: {
          channel: channel,
          expression: expression,
          value: "",
          stack: [],
        },
      };

    // Filter the key that should be deleted then build a
    // new object from the remaining keys and the initial object.
    // https://github.com/erikras/react-redux-universal-hot-example/issues/962
    case REMOVE_TAB_ELEM:
      // Warning (painful debugging) key is of type string and action.channel is of type number
      // Temporary fix to allow use of !== is to convert action.channel to a string
      return Object.keys(state)
        .filter((key) => {
          // console.log("--------");
          // console.log(`key: ${typeof key} -> ${key}`);
          // console.log(`a.channel: ${typeof action.channel} -> ${a.channel}`);
          return key !== "" + action.channel;
        })
        .reduce((result, current) => {
          result[current] = state[current];
          return result;
        }, {});

    case EDIT_EXPRESSION:
      return {
        ...state,
        [action.channel]: {
          ...state[action.channel],
          ...action.expression,
        },
      };

    case EDIT_ELEM_VALUE:
      return {
        ...state,
        [action.channel]: {
          ...state[action.channel],
          ...action.value,
        },
      };

    case EDIT_ELEM_STACK:
      return {
        ...state,
        [action.channel]: {
          ...state[action.channel],
          ...action.stack,
        },
      };

    case RESET_ELEM_PROPS:
      return {
        ...state,
        [action.channel]: {
          channel: action.channel,
          expression: "",
          value: "",
          stack: [],
        },
      };

    default:
      // console.error(`tabContent.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
