import React from "react";
import styles from "./modal-styles";

function _filteredProps(props) {
  const filtered = Object.assign({}, props);
  const unwanted = [
    "containerStyle",
    "containerClassName",
    "closeOnOuterClick",
    "onClose",
    "show",
  ];
  for (const p of unwanted) {
    delete filtered[p];
  }
  return filtered;
}

class Modal extends React.Component {
  constructor(props) {
    super();
    this.hideOnOuterClick = this.hideOnOuterClick.bind(this);
  }

  hideOnOuterClick(event) {
    if (!this.props.closeOnOuterClick) return;
    if (event.target.dataset.modal && this.props.onClose instanceof Function) {
      this.props.onClose(event);
    }
  }

  render() {
    if (!this.props.show) {
      return null;
    }

    const containerStyle = Object.assign({}, styles, this.props.containerStyle);

    return (
      <div
        {..._filteredProps(this.props)}
        className="modal"
        onClick={this.hideOnOuterClick}
        data-modal="true"
      >
        <div className={this.props.containerClassName} style={containerStyle}>
          {this.props.children}
        </div>
      </div>
    );
  }
}

export default Modal;
