// const client = typeof window !== "undefined";

const debuggerEnabled = false;

const logger = {
  TRACE: -2,
  DEBUG: -1,
  INFO: 0,
  LOG: 0,
  WARNING: 1,
  ERROR: 2,

  OUTPUT_THRESHOLD: 0,

  level(lvl) {
    if (lvl) {
      return this.OUTPUT_THRESHOLD;
    }
    let old = this.OUTPUT_THRESHOLD;
    this.OUTPUT_THRESHOLD = lvl;
    return old;
  },

  trace(message) {
    if (this.OUTPUT_THRESHOLD <= this.TRACE) {
      console.trace(message);
    }
  },

  debug(message) {
    if (this.OUTPUT_THRESHOLD <= this.DEBUG) {
      console.debug(message);
    }
  },

  info(message) {
    if (this.OUTPUT_THRESHOLD <= this.INFO) {
      console.info(message);
    }
  },

  log(message) {
    if (this.OUTPUT_THRESHOLD <= this.LOG) {
      console.log(message);
    }
  },

  warn(message) {
    if (this.OUTPUT_THRESHOLD <= this.WARNING) {
      console.warn(message);
    }
  },

  error(message) {
    if (this.OUTPUT_THRESHOLD <= this.ERROR) {
      console.error(message);
      if (debuggerEnabled) {
        debugger;
      }
    }
  },

  fatal(message) {
    console.error(message);
    alert(message);
    if (debuggerEnabled) {
      debugger;
    } else {
      console.exception(message);
    }
  },
};

module.exports = logger;
