///
/// Popover.js
///
/// A simple popover window
///
/// Based on https://larsgraubner.com/handle-outside-clicks-react/
///
/// LRQS 5
///

import React from "react";

class Popover extends React.Component {
  constructor(props) {
    super(props);

    this.handleClick = this.handleClick.bind(this);
    this.handleOutsideClick = this.handleOutsideClick.bind(this);

    this.state = {
      popupVisible: this.props.widgetClicked,
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.widgetClicked !== prevProps.widgetClicked) {
      this.handleClick();
    }
  }

  handleClick() {
    if (!this.state.popupVisible) {
      // attach/remove event handler
      document.addEventListener("click", this.handleOutsideClick, false);
    } else {
      document.removeEventListener("click", this.handleOutsideClick, false);
    }

    this.setState(prevState => ({
      popupVisible: !prevState.popupVisible,
    }));
  }

  handleOutsideClick(e) {
    // ignore clicks on the component itself
    // the node is null if I removed an area from the tab panel
    if (this.node && this.node.contains(e.target)) {
      return;
    }

    this.handleClick();
  }

  render() {
    return (
      <div
        className="popover-container"
        ref={node => {
          this.node = node;
        }}
      >
        {this.state.popupVisible && (
          <div className="popover">{this.props.children}</div>
        )}
      </div>
    );
  }
}

export default Popover;
