///
/// Parser.js
///
/// Lilly parser
///
/// LRQS 7
///

/// Dual mode imports: Web browser and server

const root = typeof exports || window;

let TC = root.TC;
if (!TC && typeof require !== "undefined") {
  TC = require("./TokenCodes.js");
} else {
  TC = root.window.TC;
}

let AST = root.AST;
if (!AST && typeof require !== "undefined") {
  AST = require("./AST.js");
} else {
  AST = root.window.AST;
}

let Lexer = root.Lexer;
if (!Lexer && typeof require !== "undefined") {
  Lexer = require("./Lexer.js");
} else {
  Lexer = root.window.Lexer;
}

let logger = root.logger;
if (!logger && typeof require !== "undefined") {
  logger = require("./logger.js");
} else {
  logger = root.window.logger;
}

/// Parser

/*

	Grammar of Lilly Expressions

	Natural grammar with precedence and associativity

    Start -> Action $
    Start -> Exp01 $

    Action -> SymDef;
    Action -> Exp01 := Exp01;
    Action -> Exp01 :- Exp01;

    SymDef -> Type SYMBOL
    SymDef -> Type SYMBOL := Exp01
    SymDef -> Type SYMBOL :- Exp01

	Type -> bool
	Type -> int
	Type -> double
	Type -> string
  Type -> var
	Type -> SYMBOL
	Type -> ARRAY [ ExpL ] => Type
	Type -> STRUCT { DefL }
  Type -> FUNCTION Signature
  Type -> LAZY Type
  Type -> TYPE

	DefL -> SymDef; DefL
	DefL ->

  Par -> SYMBOL
  Par -> type SYMBOL
  Par -> type

  Signature -> LPAREN ParL RPAREN => ReturnType

  ReturnType ->
  ReturnType -> -> Type

	Exp01 -> Exp02 -> Exp02
	Exp01 -> Exp02 <- Exp02
	Exp01 -> Exp02

	Exp02 -> Exp02 || Exp03
	Exp02 -> Exp03

	Exp03 -> Exp03 && Exp04
	Exp03 -> Exp04

	Exp04 -> Exp05 < Exp05
	Exp04 -> Exp05 <= Exp05
	Exp04 -> Exp05 == Exp05
	Exp04 -> Exp05 = Exp05
	Exp04 -> Exp05 <> Exp05
	Exp04 -> Exp05 != Exp05
	Exp04 -> Exp05 >= Exp05
	Exp04 -> Exp05 > Exp05
	
	Exp05 -> Exp06 .. Exp06
	Exp05 -> Exp06

	Exp06 -> Exp06 + Exp07
	Exp06 -> Exp06 - Exp07
	Exp06 -> Exp06 | Exp07
	Exp06 -> Exp06 >< Exp07
	Exp06 -> Exp07

	Exp07 -> Exp07 * Exp08
	Exp07 -> Exp07 / Exp08
	Exp07 -> Exp07 % Exp08
	Exp07 -> Exp07 & Exp08
	Exp07 -> Exp08

	Exp08 -> Exp09 ^ Exp08
	Exp08 -> Exp09

	Exp09 -> + Exp10
	Exp09 -> - Exp10
	Exp09 -> ~ Exp10
	Exp09 -> ! Exp10
	Exp09 -> Exp10

	Exp10 -> Exp10 ! Exp11
	Exp10 -> Exp11

	Exp11 -> Exp12 : Exp12
	Exp11 -> Exp12

	Exp12 -> Exp12 . SYMBOL
	Exp12 -> Exp12 ( ExpL )
	Exp12 -> Exp12 [ ExpL ]
	Exp12 -> Exp12 { ExpL }
	Exp12 -> Exp13

	Exp13 -> BOOLEAN | INTEGER | DOUBLE | STRING
	Exp13 -> ' Exp01 '
	Exp13 -> ( ExpL )
	Exp13 -> [ ExpL ]
	Exp13 -> { ExpL }
	Exp13 -> SYMBOL

  //
  // TODO: abstract ArgL and ExpL using higher order parsing
  //

	ParL -> XParL	// non-empty Par list
	ParL ->

	XParL -> Par RParL

	RParL -> , Par RParL
	RXParL ->

	ExpL -> XExpL	// non-empty Expression list
	ExpL ->

	XExpL -> Exp01 RExpL

	RExpL -> , Exp01 RExpL
	RXExpL ->

	===========
	LL1 grammar										Relevant directors
	===========

  Start -> Action $

  Action -> SymDef                                { dir(Type) - SYMBOL }
  Action -> Exp01 RAction0
    
  RAction0 -> SYMBOL RSymDef                      { SYMBOL }    // inherited attribute must be a type expression
  RAction0 -> Assign                              { :=, :- }
  RAction0 ->                                     { ;, }

	Type -> bool
	Type -> int
	Type -> double
	Type -> string
  Type -> var
	Type -> SYMBOL
	Type -> ARRAY [ ExpL ] => Type
	Type -> STRUCT { DefL }
  Type -> FUNCTION Signature
  Type -> LAZY Type
  Type -> TYPE

	DefL -> SymDef DefL
	DefL ->

  SymDef -> Type;
  SymDef -> Type SYMBOL RSymDef;

  RSymDef -> := Exp01;
  RSymDef -> :- Exp01;
  RSymDef -> ;

  Assign -> := Exp01;
  Assign -> :- Exp01;

  Par -> SYMBOL
  Par -> type SYMBOL
  Par -> type

  Signature -> LPAREN ParL RPAREN => ReturnType

  ReturnType ->
  ReturnType -> -> Type

	Exp01 -> Exp02 RExp01

	RExp01 -> -> Exp02								{ -> }
	RExp01 -> <- Exp02								{ <- }
	RExp01 ->

	Exp02 -> Exp03 RExp02

	RExp02 -> || Exp03 RExp02						{ || }
	RExp02 ->

	Exp03 -> Exp04 RExp03

	RExp03 -> && Exp04 RExp03						{ && }
	RExp03 ->

	Exp04 -> Exp05 RExp04

	RExp04 -> < Exp05								{ < }
	RExp04 -> <= Exp05								{ <= }
	RExp04 -> = Exp05								{ = }
	RExp04 -> <> Exp05								{ <> }
	RExp04 -> != Exp05								{ != }
	RExp04 -> >= Exp05								{ >= }
	RExp04 -> > Exp05								{ > }
	RExp04 -> 

	Exp05 -> Exp06 RExp05

	RExp05 -> .. Exp06								{ .. }
	RExp05 ->
	Exp06 -> Exp07 RExp06

	RExp06 -> + Exp07 RExp06 						{ + }
	RExp06 -> - Exp07 RExp06 						{ - }
	RExp06 -> >< Exp07 RExp06 						{ >< }
	RExp06 -> | Exp07 RExp06						{ | }
	RExp06 ->

	Exp07 -> Exp08 RExp07

	RExp07 -> * Exp08 RExp07 						{ * }
	RExp07 -> / Exp08 RExp07						{ / }
	RExp07 -> % Exp08 RExp07						{ % }
	RExp07 -> & Exp08 RExp07						{ & }
	RExp07 ->

	Exp08 -> Exp09 RExp08

	RExp08 -> ^ Exp09 RExp08						{ ^ }
	RExp08 ->

	Exp09 -> - Exp10								{ - }
	Exp09 -> + Exp10								{ + }
	Exp09 -> ~ Exp10								{ ~ }
	Exp09 -> ! Exp10								{ ! }
	Exp09 -> Exp10

	Exp10 -> Exp11 RExp10

	RExp10 -> ! Exp11 RExp10						{ ! }
	RExp10 ->

	Exp11 -> Exp12 RExp11

	RExp11 -> : Exp12								{ : }
	RExp11 ->

	Exp12 -> Exp13 RExp12

	RExp12 -> . SYMBOL RExp12						{ . }
	RExp12 -> ( ExpL ) RExp12						{ ( }
	RExp12 -> [ ExpL ] RExp12						{ [ }
	RExp12 -> { ExpL } RExp12						{ { }
	RExp12 ->

	Exp13 -> BOOLEAN | INTEGER | DOUBLE | STRING
	Exp13 -> ' Exp01 '
	Exp13 -> ( ExpL )
	Exp13 -> [ ExpL ]
	Exp13 -> { ExpL }
	Exp13 -> SYMBOL

	ParL -> XParL	// non-empty Par list
	ParL ->											{ ->, ) }

	XParL -> Par RParL

	RParL -> , Par RParL                            { , }
	RXParL ->

	ExpL -> XExpL
	ExpL ->											{ ), ], } }

	XExpL -> Exp01 RXExpL

	RXExpL  -> , Exp01 RExpL 						{ , }
	RXExpL ->

*/

function _parse(lexer) {
  let closing = (function () {
    let map = {};

    map[TC.LPAREN] = TC.RPAREN;
    map[TC.LBRACKET] = TC.RBRACKET;
    map[TC.LBRACE] = TC.RBRACE;
    map[TC.SQUOTE] = TC.SQUOTE;

    return map;
  })();

  // XExpL -> Exp01 RXExpL
  // XExpL -> RXExpL                          { , }

  // RXExpL  -> , Exp01 RExpL                 { , }
  // RXExpL ->

  // ExpL -> XExpL
  // ExpL ->									{ ), ], } }

  function ExpL(empty) {
    logger.trace("ExpL: " + lexer.next().value);
    let list = [];
    let first = true;
    let inull = !empty;
    for (;;) {
      let next = lexer.next();
      switch (next.code) {
        case TC.COMMA:
          if (inull || first) {
            list.push(AST.Null());
          }
          lexer.match(TC.COMMA);
          first = false;
          inull = true;
          continue;
        case TC.RPAREN:
        case TC.RBRACKET:
        case TC.RBRACE:
          if (inull) {
            list.push(AST.Null());
          }
          return AST.List(list);
        default: {
          let elem = Exp01();
          list.push(elem);
          first = false;
          inull = false;
        }
      }
    }
  }

  // Exp13 -> BOOLEAN | INTEGER | DOUBLE | STRING
  // Exp13 -> SYMBOL
  // Exp13 -> ' Exp01 '
  // Exp13 -> ( ExpL )
  // Exp13 -> [ ExpL ]
  // Exp13 -> { ExpL }
  // Exp13 -> Type

  function Exp13() {
    logger.trace("Exp13: " + lexer.next().value);
    let next = lexer.next();
    switch (next.code) {
      case TC.QUESTION:
        lexer.match(next.code);
        return AST.Null(next.value);
      case TC.BOOLEAN:
        lexer.match(next.code);
        return AST.Boolean(next.value);
      case TC.INTEGER:
        lexer.match(next.code);
        return AST.Integer(next.value);
      case TC.DOUBLE:
        lexer.match(next.code);
        return AST.Double(next.value);
      case TC.STRING:
        lexer.match(next.code);
        return AST.String(next.value);
      case TC.SYMBOL: {
        lexer.match(next.code);
        let symbol = next.value;
        return AST.Identifier(symbol);
      }
      case TC.ERROR: {
        lexer.match(next.code);
        let error = next.value;
        return AST.Error(error);
      }
      case TC.SQUOTE: {
        lexer.match(TC.SQUOTE);
        let elems = AST.List([Exp01()]);
        lexer.match(TC.SQUOTE);
        return AST.SQuoted(elems);
      }
      case TC.LPAREN: {
        lexer.match(next.code);
        let elems = ExpL(true);
        lexer.match(closing[next.code]);
        let ast = AST.Paren(elems);
        return ast;
      }
      case TC.LBRACKET: {
        lexer.match(next.code);
        let elems = ExpL(true);
        lexer.match(closing[next.code]);
        let ast = AST.Array(elems);
        return ast;
      }
      case TC.LBRACE: {
        lexer.match(next.code);
        let elems = ExpL(true);
        lexer.match(closing[next.code]);
        let ast = AST.Set(elems);
        return ast;
      }
      case TC.LAMBDA: {
        lexer.match(next.code);
        lexer.match(TC.LPAREN);
        let parlist = ParL();
        lexer.match(TC.RPAREN);
        let rettype = ReturnType();
        lexer.match(TC.RARROW);
        let exp = Exp01();
        return AST.Lambda(parlist, rettype, exp);
      }
      case TC.BOOL_T:
      case TC.INT_T:
      case TC.DOUBLE_T:
      case TC.STRING_T:
      case TC.VAR_T:
      case TC.ARRAY_T:
      case TC.STRUCT_T:
      case TC.FUNCTION_T:
      case TC.LAZY_T: {
        let type = Type();
        if (next.code === TC.LPAREN) {
          lexer.match(TC.LPAREN);
          let arg = ExpL(false);
          lexer.match(TC.RPAREN);
          return AST.Call(type, arg);
        }
        return type;
      }
      default:
        throw AST.Error("#SYNTAX!", "Expression Expected");
    }
  }

  // Exp12 -> Exp13 RExp12
  // RExp12 -> . SYMBOL RExp12						{ . }
  // RExp12 -> ( ExpL ) RExp12						{ ( }
  // RExp12 -> [ ExpL ] RExp12						{ [ }
  // RExp12 -> { ExpL } RExp12						{ { }
  // RExp12 ->

  function Exp12() {
    logger.trace("Exp12: " + lexer.next().value);
    let ast = Exp13();
    for (;;) {
      let next = lexer.next();
      let constructor = null;
      switch (next.code) {
        case TC.DOT: {
          lexer.match(next.code);
          next = lexer.next();
          lexer.match(TC.SYMBOL);
          let symbol = next.value;
          ast = AST.Dotted(ast, AST.Identifier(symbol));
          continue;
        }
        case TC.LPAREN:
          constructor = AST.Call;
          break;
        case TC.LBRACKET:
          constructor = AST.Sub;
          break;
        case TC.LBRACE:
          constructor = AST.Brace;
          break;
        default:
          return ast;
      }
      lexer.match(next.code);
      let rhs = ExpL(true);
      lexer.match(closing[next.code]);
      ast = constructor(ast, rhs);
      continue;
    }
  }

  // Exp11 -> Exp12 RExp11
  // RExp11 -> : Exp12								{ : }
  // RExp11 ->

  function Exp11() {
    logger.trace("Exp11: " + lexer.next().value);
    let ast = Exp12();
    let next = lexer.next();
    if (next.code === TC.COLON) {
      lexer.match(next.code);
      let rhs = Exp12();
      ast = AST.LV_Range(ast, rhs);
    }
    return ast;
  }

  // Exp10 -> Exp11 RExp10
  // RExp10 -> ! Exp11 RExp10							{ ! }
  // RExp10 ->

  function Exp10() {
    logger.trace("Exp10: " + lexer.next().value);
    let ast = Exp11();
    for (;;) {
      let next = lexer.next();
      if (next.code === TC.EXCLAMATION) {
        lexer.match(next.code);
        let rhs = Exp11();
        ast = AST.WorksheetRef(ast, rhs);
      } else {
        break;
      }
    }
    return ast;
  }

  // Exp09 -> + Exp10									{ + }
  // Exp09 -> - Exp10									{ - }
  // Exp09 -> ~ Exp10									{ ~ }
  // Exp09 -> ! Exp10									{ ! }
  // Exp09 -> Exp10

  function Exp09() {
    logger.trace("Exp09: " + lexer.next().value);
    let next = lexer.next();
    let constructor = null;
    switch (next.code) {
      case TC.PLUS: // unary plus
        constructor = AST.UnaryPlus;
        break;
      case TC.MINUS: // unary minus
        constructor = AST.UnaryMinus;
        break;
      case TC.BWNOT: // bitwise not
        constructor = AST.BwNot;
        break;
      case TC.EXCLAMATION: // logical not
        constructor = AST.LogicalNot;
        break;
      default:
        return Exp10();
    }
    lexer.match(next.code);
    let arg = Exp10();
    let ast = constructor(arg);
    return ast;
  }

  // RExp08 -> ^ Exp09 RExp08							{ ^ }
  // RExp08 ->

  // Note the recursion pattern for right associative binary infix operators
  // Exponentiation (x^y) is right associative
  // hence we recur first and assemble the tree on return

  function rExp08(ast) {
    logger.trace("rExp08: " + ast + " " + lexer.next().value);
    let next = lexer.next();
    if (next.code === TC.RAISED) {
      lexer.match(next.code);
      let Exp = Exp09();
      let rhs = rExp08(Exp);
      ast = AST.Power(ast, rhs);
    }
    return ast;
  }

  // Exp08 -> Exp09 RExp08

  function Exp08() {
    logger.trace("Exp08: " + lexer.next().value);
    let lhs = Exp09();
    let ast = rExp08(lhs);
    return ast;
  }

  // Exp07 -> Exp08 RExp07
  // RExp07 -> * Exp08 RExp07  { * }
  // RExp07 -> / Exp08 RExp07  { / }
  // RExp07 -> % Exp08 RExp07  { % }
  // RExp07 -> & Exp08 RExp07  { & }
  // RExp07 ->

  function Exp07() {
    logger.trace("Exp07: " + lexer.next().value);
    let ast = Exp08();
    for (;;) {
      let next = lexer.next();
      let constructor = null;
      switch (next.code) {
        case TC.STAR:
          constructor = AST.Mult;
          break;
        case TC.SLASH:
          constructor = AST.Div;
          break;
        case TC.PERCENT:
          constructor = AST.Mod;
          break;
        case TC.BWAND:
          constructor = AST.BwAnd;
          break;
        default:
          return ast;
      }
      if (constructor != null) {
        lexer.match(next.code);
        let rhs = Exp08();
        ast = constructor(ast, rhs);
      }
    }
  }

  // Exp06 -> Exp07 RExp06
  // RExp06 -> + Exp07 RExp06 						{ + }
  // RExp06 -> - Exp07 RExp06 						{ - }
  // RExp06 -> >< Exp07 RExp06 						{ >< }
  // RExp06 -> | Exp07 RExp06							{ | }
  // RExp06 ->

  // Note the implicit recursion pattern for left associative binary infix operators
  // first we parse the next priority level Expression (lhs candidate) storing the corresponding AST in ast
  // while the next token in the input is an infix binary operator at this level ...
  // ... we parse the immediate right hand side (rhs candidate, also at the next priority level)
  // ... and redefine ast as a tree that has the former ast as the left hand side (left associative)
  // note that ast can be seen as the tree representing the "addition so far"

  function Exp06() {
    logger.trace("Exp06: " + lexer.next().value);
    let ast = Exp07();
    for (;;) {
      let next = lexer.next();
      let constructor = null;
      switch (next.code) {
        case TC.PLUS:
          constructor = AST.BinaryPlus;
          break;
        case TC.MINUS:
          constructor = AST.BinaryMinus;
          break;
        case TC.BWXOR:
          constructor = AST.BwXor;
          break;
        case TC.BWOR:
          constructor = AST.BwOr;
          break;
        default:
          // no further operators means we have the whole "additive" Expression in ast
          return ast;
      }
      if (constructor != null) {
        lexer.match(next.code);
        let rhs = Exp07();
        ast = constructor(ast, rhs);
      }
    }
  }

  // Exp05 -> Exp06 RExp05
  // RExp05 -> .. Exp06								{ .. }
  // RExp05 ->

  function Exp05() {
    logger.trace("Exp05: " + lexer.next().value);
    let ast = Exp06();
    let next = lexer.next();
    if (next.code === TC.DOTDOT) {
      lexer.match(next.code);
      let rhs = Exp06();
      ast = AST.RV_Range(ast, rhs);
    }
    return ast;
  }

  // Exp04 -> Exp05 RExp04
  // RExp04 -> < Exp05								{ < }
  // RExp04 -> <= Exp05								{ <= }
  // RExp04 -> = Exp05								{ = }
  // RExp04 -> <> Exp05								{ <> }
  // RExp04 -> != Exp05								{ != }
  // RExp04 -> >= Exp05								{ >= }
  // RExp04 -> > Exp05								{ > }
  // RExp04 ->

  function Exp04() {
    logger.trace("Exp04: " + lexer.next().value);
    let ast = Exp05();
    let next = lexer.next();
    let constructor = null;
    switch (next.code) {
      case TC.LT:
        constructor = AST.LT;
        break;
      case TC.LE:
        constructor = AST.LE;
        break;
      case TC.EQ:
        constructor = AST.EQ;
        break;
      case TC.NE:
        constructor = AST.NE;
        break;
      case TC.GE:
        constructor = AST.GE;
        break;
      case TC.GT:
        constructor = AST.GT;
        break;
      default:
      // skip
    }
    if (constructor != null) {
      lexer.match(next.code);
      let rhs = Exp05();
      ast = constructor(ast, rhs);
    }
    return ast;
  }

  // Exp03 -> Exp04 RExp03
  // RExp03 -> && Exp04 RExp03							{ && }
  // RExp03 ->

  function Exp03() {
    logger.trace("Exp03: " + lexer.next().value);
    let ast = Exp04();
    for (;;) {
      let next = lexer.next();
      if (next.code === TC.AND) {
        lexer.match(TC.AND);
        let rhs = Exp04();
        ast = AST.LogicalAnd(ast, rhs);
        continue;
      }
      return ast;
    }
  }

  // Exp02 -> Exp03 RExp02
  // RExp02 -> || Exp03 RExp02							{ || }
  // RExp02 ->

  function Exp02() {
    logger.trace("Exp02: " + lexer.next().value);
    let ast = Exp03();
    for (;;) {
      let next = lexer.next();
      if (next.code === TC.OR) {
        lexer.match(TC.OR);
        let rhs = Exp03();
        ast = AST.LogicalOr(ast, rhs);
        continue;
      }
      return ast;
    }
  }

  // Exp01 -> Exp02 RExp01

  function Exp01() {
    logger.trace("Exp01: " + lexer.next().value);
    let ast = Exp02();
    return RExp01(ast);
  }

  // RExp01 -> -> Exp02								{ -> }
  // RExp01 -> <- Exp02								{ <- }
  // RExp01 ->

  function RExp01(ast) {
    logger.trace("RExp01: " + lexer.next().value);
    let next = lexer.next();
    switch (next.code) {
      case TC.LARROW: {
        lexer.match(next.code);
        let rhs = Exp02();
        ast = AST.LArrow(ast, rhs);
        break;
      }
      case TC.RARROW: {
        lexer.match(next.code);
        let rhs = Exp02();
        ast = AST.RArrow(ast, rhs);
        break;
      }
      default:
      // skip
    }
    return ast;
  }

  // DefL -> SymDef DefL
  // DefL ->

  function DefL(closing) {
    logger.trace("DefL: " + lexer.next().value);
    let list = [];
    for (;;) {
      let next = lexer.next();
      if (next.code === closing) {
        break;
      }
      let def = SymDef();
      list.push(def);
    }
    return AST.List(list);
  }

  // Par -> type SYMBOL
  // Par -> type
  // Par -> SYMBOL

  function Par() {
    logger.trace("Par: " + lexer.next().value);
    let next = lexer.next();
    let type = Type();

    next = lexer.next();
    if (next.code === TC.SYMBOL) {
      lexer.match(next.code);
      let name = AST.Identifier(next.value);
      return AST.ParDef(type, name);
    }

    if (type.is_type()) {
      let name = AST.Null();
      return AST.ParDef(type, name);
    } else {
      let name = type;
      type = AST.VarType();
      return AST.ParDef(type, name);
    }
  }

  // ParL -> XParL
  // ParL ->											{ ->, ) }

  // XParL -> Par RParL

  // RParL -> , Par RParL								{ , }
  // RXParL ->

  function ParL() {
    logger.trace("ParL: " + lexer.next().value);
    let list = [];
    let next = lexer.next();
    if (next.code !== TC.RARROW && next.code !== TC.RPAREN) {
      for (;;) {
        let par = Par();
        list.push(par);
        next = lexer.next();
        if (next.code !== TC.COMMA) {
          break;
        }
        lexer.match(next.code);
      }
    }
    return AST.List(list);
  }

  function ReturnType() {
    logger.trace("ReturnType: " + lexer.next().value);
    let next = lexer.next();
    switch (next.code) {
      case TC.YIELDS: {
        lexer.match(next.code);
        let type = Type();
        return type;
      }
      default:
        return AST.VarType();
    }
  }

  function Signature() {
    logger.trace("Signature: " + lexer.next().value);
    lexer.match(TC.LPAREN);
    let parlist = ParL();
    lexer.match(TC.RPAREN);
    let rettype = ReturnType();
    return AST.FunctionType(parlist, rettype);
  }

  // Type -> bool
  // Type -> int
  // Type -> double
  // Type -> string
  // Type -> var
  // Type -> SYMBOL
  // Type -> ARRAY [ ExpL ] Type
  // Type -> STRUCT { DefL }
  // Type -> FUNCTION Signature
  // Type -> LAZY Type
  // Type -> TYPE

  function Type() {
    logger.trace("Type: " + lexer.next().value);
    let next = lexer.next();
    switch (next.code) {
      case TC.COLONCOLON:
        lexer.match(next.code);
        return AST.InferType("");
      case TC.COLON: {
        lexer.match(TC.COLON);
        let symbol = lexer.match(TC.SYMBOL);
        lexer.match(TC.COLON);
        return AST.InferType(symbol.value);
      }
      case TC.BOOL_T:
        lexer.match(next.code);
        return AST.BoolType();
      case TC.INT_T:
        lexer.match(next.code);
        return AST.IntegerType();
      case TC.DOUBLE_T:
        lexer.match(next.code);
        return AST.DoubleType();
      case TC.STRING_T:
        lexer.match(next.code);
        return AST.StringType();
      case TC.VAR_T:
        lexer.match(next.code);
        return AST.VarType();
      case TC.SYMBOL: {
        lexer.match(TC.SYMBOL);
        let symbol = AST.Identifier(next.value);
        return symbol;
      }
      case TC.ARRAY_T: {
        lexer.match(next.code);
        lexer.match(TC.LBRACKET);
        let expl = ExpL(false);
        let nd = expl.length();
        if (nd === 0) {
          nd = 1;
        }
        lexer.match(TC.RBRACKET);
        lexer.match(TC.YIELDS);
        let type = Type();
        let ast = AST.ArrayType(nd, type);
        return ast;
      }
      case TC.STRUCT_T: {
        lexer.match(next.code);
        lexer.match(TC.LBRACE);
        let defl = DefL(TC.RBRACE);
        lexer.match(TC.RBRACE);
        let ast = AST.StructType(defl);
        return ast;
      }
      case TC.FUNCTION_T: {
        lexer.match(next.code);
        let signature = Signature();
        return signature;
      }
      case TC.LAZY_T: {
        lexer.match(next.code);
        let type = Type();
        let ast = AST.LazyType(type);
        return ast;
      }
      case TC.TYPE:
        lexer.match(TC.TYPE);
        return AST.TypeType();
      default:
        throw AST.Error("#SYNTAX!", "Type expression expected");
    }
  }

  // Assign -> := Exp01;
  // Assign -> :- Exp01;

  function Assign(lhs) {
    logger.trace("Assign(" + lhs + "):" + lexer.next().value);

    let next = lexer.next();
    switch (next.code) {
      case TC.ASSIGN: {
        lexer.match(TC.ASSIGN);
        let exp = Exp01();
        return AST.Assign(lhs, false, exp);
      }
      case TC.DEFINE: {
        lexer.match(TC.DEFINE);
        let exp = Exp01();
        return AST.Assign(lhs, true, exp);
      }
      default:
        throw AST.Error("#SYNTAX!", "Assignment operator expected");
    }
  }

  // RSymDef -> := Exp01
  // RSymDef -> :- Exp01
  // RSymDef ->

  function RSymDef(type, symbol) {
    logger.trace(
      "RSymDef(" + type + ", " + symbol + "): " + lexer.next().value
    );

    let next = lexer.next();
    switch (next.code) {
      case TC.ASSIGN: {
        lexer.match(TC.ASSIGN);
        let exp = Exp01();
        return AST.SymDef(type, symbol, false, exp);
      }
      case TC.DEFINE: {
        lexer.match(TC.DEFINE);
        let exp = Exp01();
        return AST.SymDef(type, symbol, true, exp);
      }
      default:
        return AST.SymDef(type, symbol, false, AST.Null());
    }
  }

  // RAction0 -> SYMBOL RSymDef                      { SYMBOL }    // inherited attribute must be a type expression
  // RAction0 -> Assign                              { :=, :- }
  // RAction0 ->

  function RAction0(ast) {
    logger.trace("RAction0(" + ast + "):" + lexer.next().value);
    let next = lexer.next();
    switch (next.code) {
      case TC.SYMBOL: {
        lexer.match(TC.SYMBOL);
        let symbol = AST.Identifier(next.value);
        ast = RSymDef(ast, symbol);
        break;
      }
      case TC.ASSIGN:
      case TC.DEFINE: {
        ast = Assign(ast);
        break;
      }
      case TC.SEMICOLON:
        break;
      default:
        return ast;
    }
    lexer.match(TC.SEMICOLON);
    return ast;
  }

  // SymDef -> Type;
  // SymDef -> Type SYMBOL RSymDef;

  function SymDef() {
    logger.trace("SymDef: " + lexer.next().value);

    let type = Type();

    let next = lexer.next();
    if (next.code === TC.SYMBOL) {
      lexer.match(TC.SYMBOL);
      let symbol = AST.Identifier(next.value);
      let ast = RSymDef(type, symbol);
      lexer.match(TC.SEMICOLON);
      return ast;
    }

    if (next.code === TC.LPAREN) {
      lexer.match(TC.LPAREN);
      let arg = ExpL(false);
      lexer.match(TC.RPAREN);
      return AST.Call(type, arg);
    }

    if (next.code === TC.SEMICOLON) {
      let ast = AST.SymDef(type, AST.Null(), false, AST.Null());
      lexer.match(TC.SEMICOLON);
      return ast;
    }

    return type;
  }

  // Action -> SymDef                                { dir(Type) - SYMBOL }
  // Action -> Exp01 RAction0

  function Action() {
    logger.trace("Action: " + lexer.next().value);
    let next = lexer.next();
    switch (next.code) {
      case TC.COLONCOLON:
      case TC.COLON:
        return SymDef();
      default: {
        let lhs = Exp01();
        let ast = RAction0(lhs);
        return ast;
      }
    }
  }

  // Start -> Action [=> result] $

  function Start() {
    logger.trace("Start: " + lexer.next().value);
    let ast = Action();
    let next = lexer.next();
    if (next.code === TC.META_YIELDS) {
      lexer.match(TC.META_YIELDS);
      let result = Exp01();
      ast = AST.MetaYields(ast, result);
    }
    lexer.match(TC.END);
    return ast;
  }

  return Start();
}

const lexer = new Lexer();

function parse(str, caller) {
  try {
    lexer.start(str);
    const ast = _parse(lexer);
    return ast;
  } catch (error) {
    const message = `from ${caller}: error parsing ${str}: ${error}`;
    logger.warn(message);
    return error;
  }
}

/// Dual mode exports: Web browser and server

module.exports = parse;
