import { bindActionCreators } from "redux";
import bindIndexToActionCreators from "./bindIndexToActionCreators";
import * as tabContentActions from "../../state/tabContent";

function panel(index) {
  return function (dispatch) {
    const bound = bindActionCreators(
      bindIndexToActionCreators(tabContentActions, index),
      dispatch
    );
    return bound;
  };
}

export default panel;
