/// PaletteItems.js
///
/// Renders the list of items
///
/// LRQS 7
///

import React from "react";

import Draggable from "../gui/Draggable";

function PaletteItems(props) {
  function _onDragStart() {
    props.dragActions.setDragStatus(true);
  }

  function _onDragEnd() {
    props.dragActions.setDragStatus(false);
  }

  const selected = props.selected;

  return (
    <ul className="vertical-icon-menu">
      {props.paletteItems.map((item, index) => {
        return (
          <Draggable
            key={index}
            type="definition"
            data={item.type}
            onDragStart={_onDragStart}
            onDragEnd={_onDragEnd}
          >
            <li className={selected === index ? "active" : ""}>
              {item.icon}
              <div style={{ fontSize: "9px" }}>{item.name}</div>
            </li>
          </Draggable>
        );
      })}
    </ul>
  );
}

export default PaletteItems;
