import buildGrid from "./buildGrid";
import getColumnCount from "./getColumnCount";

import elems from "zll/elems";
import parse from "zll/Parser";

import logger from "../../logger/logger";
import UI from "../../constants/UI";

function getCoord(coord, orientation, row, column) {
  if (orientation === "horizontal") {
    return coord === "row" ? column : row;
  } else {
    return coord === "row" ? row : column;
  }
}

function get_subgrid(data, orientation, lowRow, uppRow, usesDataLayer) {
  const ast = parse(data, "grid::get_subgrid 24");
  const initialData = elems("get_subgrid", ast);

  if (!initialData) {
    // 20200728 this error still happens
    logger.error(`get_subgrid(${data}, ...): initialData is undefined`);
    // !! Note: return null fails the sheibe.sym test
    return []; // return null; //
  }

  const columnCount = getColumnCount(initialData);

  const rows =
    orientation === "vertical"
      ? usesDataLayer
        ? uppRow - lowRow + 1
        : initialData.length
      : columnCount;

  const columns =
    orientation === "vertical"
      ? columnCount
      : usesDataLayer
      ? uppRow - lowRow + 1
      : initialData.length;

  const maxI = orientation === "vertical" ? rows : columns;
  const maxJ = orientation === "vertical" ? columns : rows;

  const subGrid = buildGrid(initialData, maxI, maxJ, orientation);

  return subGrid;
}

function update_data_layer(
  dataLayer,
  startIndex,
  stopIndex,
  startColIndex,
  stopColIndex,
  subGrid
) {
  let newDataLayer = Object.assign({}, dataLayer);
  // logger.debug(startIndex, stopIndex);
  // logger.debug(startColIndex, stopColIndex);
  // logger.debug(subGrid[0], subGrid[0][0]);

  if (stopColIndex !== 0) {
    // Update data layer with the new cells
    let row = 0;
    for (let i = startIndex; i <= stopIndex; ++i) {
      if (!subGrid[row]) {
        logger.debug(`subGrid[${row}] is undefined`);
        return dataLayer;
      }

      let col = 0;
      for (let j = startColIndex; j < stopColIndex; ++j) {
        // !! This always causes problems (Monica said that!)
        newDataLayer[i][j] = subGrid[row][col];
        col = col + 1;
      }

      row = row + 1;
    }
  }

  return newDataLayer;
}

// Description: Deletes from the data layer object, the loadedRowsMap object
// the elems between the given bounds. It also uses the sub/unsub API to
// unmon the variables that are invisible now.
function get_invisible_indexes(
  prevLowRow,
  startIndex,
  minimumBatchSize,
  dataLayer
) {
  let indexes = [];
  if (prevLowRow < startIndex) {
    // user scrolled down
    if (startIndex - minimumBatchSize >= 0) {
      for (let index in dataLayer) {
        if (index < startIndex) indexes.push(Number(index));
      }
    }
  } else if (prevLowRow > startIndex) {
    // user scrolled up
    const deleteLowerBound = startIndex + minimumBatchSize * 2;
    for (let index in dataLayer)
      if (index >= deleteLowerBound) {
        indexes.push(Number(index));
      }
  }
  // logger.debug("window: ", indexes[0], indexes[indexes.length-1]);
  return indexes;
}

// Get min data layer value
// prevRowStart control variable to know if the user is scrolling down or up
function get_min_dl_value(startIndex, minimumBatchSize, prevRowStart) {
  if (prevRowStart < startIndex) {
    // If the user scrolls down
    return startIndex - minimumBatchSize >= 0
      ? startIndex - minimumBatchSize
      : startIndex;
  } else if (prevRowStart > startIndex) {
    // If the user scrolls up
    return startIndex;
  }
}

// Get max data layer value
function get_max_dl_value(
  startIndex,
  stopIndex,
  minimumBatchSize,
  prevRowStart
) {
  if (prevRowStart < startIndex)
    return startIndex - minimumBatchSize >= 0
      ? stopIndex
      : stopIndex + minimumBatchSize;
  else if (prevRowStart > startIndex) return stopIndex + minimumBatchSize;
}

function set_grid_width(
  orientation,
  columnsWidth,
  uppRow,
  minimumBatchSize,
  gridName // !! warning: deliberatly set to 'undefined' in some calls
) {
  const SCROLL_BAR_WIDTH = 21;
  let visible = 7; // Maximun number of visible columns

  let width = 0;
  if (orientation === "vertical") {
    for (let value of columnsWidth) {
      width += value;
    }
  } else {
    if (uppRow < minimumBatchSize) {
      visible = uppRow;
    }
    width += UI.MIN_CELL_WIDTH * visible;
  }
  if (uppRow > 10) {
    width += SCROLL_BAR_WIDTH; // !! 10 is a magic number
  }
  const MARGIN_WIDTH = 70;

  // Check if the container width of the grid is greater than the grid width itself. If not, reduce grid width to the container width
  let container =
    document.getElementById(gridName) != null
      ? document.getElementById(gridName)
      : document.getElementsByClassName("tabs-content")[0];

  const containerWidth =
    container.offsetWidth -
    2 * MARGIN_WIDTH -
    UI.ROW_INDEX_WIDTH -
    UI.FA_MARGIN;

  if (containerWidth < width) {
    width = containerWidth;
  }

  // !! ?? this looks scary
  /*
  let container = document.getElementsByClassName("elem-value").length !== 0
    ? document.getElementsByClassName("elem-value")[0]
    : document.getElementsByClassName("tabs-content")[0];

  let containerWidth = container.offsetWidth - 2 * MARGIN_WIDTH - UI.ROW_INDEX_WIDTH - UI.FA_MARGIN;
  if (width > containerWidth ) {
    width = containerWidth;
  }
  */

  return width;
}

function get_columns_width(value) {
  const ast = parse(value, "grid::get_columns_width 207");
  logger.debug(`get_columns_width: ${value}`);
  logger.debug(`get_columns_width: ${ast}`);

  const initialData = elems("get_columns_width", ast);

  if (!initialData) {
    logger.error(`get_columns_width(${value}): initialData is undefined`);
    return [UI.CELL_WIDTH_DEFAULT];
  }

  let columnCount = getColumnCount(initialData);
  let rowMaxCol = 0; // Contains the row with the max number of columns
  let rows = 0;

  if (columnCount > 1) {
    for (let i = 0; i < initialData.length; ++i) {
      const elements = elems("get_columns_width", initialData[i]);
      if (elements) {
        if (rows < elements.length) {
          rows = elements.length;
          rowMaxCol = i;
        }
      }
    }
  }

  let types = [];
  let widthSizes = [];
  const initial = initialData[rowMaxCol];

  if (!initial) {
    logger.error(
      `get_columns_width(${value}): initialData[${rowMaxCol}] is undefined`
    );
    return [UI.CELL_WIDTH_DEFAULT];
  }

  if (initial.args) {
    if (initial.args.elems) {
      for (let e of elems("get_columns_width", initial)) {
        types.push(e.tree());
      }
    } else {
      types.push(initial.tree());
    }
  } else {
    // only one row
    types.push(initial.tree());
  }

  for (let i = 0; i < columnCount; ++i) {
    const elem = types[i] || "";

    switch (true) {
      case elem.indexOf("Id") !== -1:
      case elem.indexOf("Apply") !== -1:
        widthSizes.push(UI.CELL_WIDTH_FORMULA);
        break;
      case elem.indexOf("Boolean") !== -1:
        widthSizes.push(UI.CELL_WIDTH_BOOLEAN);
        break;
      case elem.indexOf("Integer") !== -1:
        widthSizes.push(UI.CELL_WIDTH_INTEGER);
        break;
      case elem.indexOf("Double") !== -1:
        widthSizes.push(UI.CELL_WIDTH_DOUBLE);
        break;
      case elem.indexOf("String") !== -1:
        widthSizes.push(UI.CELL_WIDTH_STRING);
        break;
      default:
        widthSizes.push(UI.CELL_WIDTH_DEFAULT);
    }
  }

  return widthSizes;
}

function isFloat(n) {
  return !isNaN(n) && Number(n) % 1 !== 0;
}

function isInt(n) {
  return !isNaN(n) && Number(n) % 1 === 0;
}

function is_container(value) {
  // @@ Looks wrong: the decision should depend on the type, not the value
  logger.debug(`is_container: value => ${value}`);
  logger.debug(`is_container: typeof value => ${typeof value}`);

  let isAContainer = false;
  let cellValue = value;

  if (typeof value === "string") {
    cellValue =
      !value.startsWith("(") && !value.startsWith("[")
        ? '"' + value + '"'
        : value;
  } else if (typeof value === "number") {
    cellValue = String(value);
  }

  if (cellValue) {
    const ast = parse(cellValue, "grid::is_container 312");
    isAContainer = !!ast.is_container;
  }

  logger.debug(`is_container: isAContainer => ${isAContainer}`);
  return isAContainer;
}

function get_grid_height(rows, visibleRowNum) {
  const MAX_GRID_HEIGHT = UI.ROW_HEIGHT * visibleRowNum;
  const SCROLL_BAR_HEIGHT = 21;
  return rows * UI.ROW_HEIGHT < MAX_GRID_HEIGHT
    ? rows * UI.ROW_HEIGHT + SCROLL_BAR_HEIGHT
    : MAX_GRID_HEIGHT + SCROLL_BAR_HEIGHT;
}

// !! looks like ad-hoc code for one or two dimensions
function get_cell_index(row, column, orientation, type, columns) {
  row = getCoord("row", orientation, row, column);
  column = getCoord("column", orientation, row, column);
  let cell = undefined;

  if (type === "1-grid" || columns === 1) {
    cell = `[${row}]`;
  } else {
    if (columns > 1) {
      const separator = type === "multi-dim-grid" ? ", " : "][";
      cell = `[${row}${separator}${column}]`;
    }
  }

  return cell;
}

function cell_is_composite(row, column, dataLayer) {
  const cellValue = dataLayer[row][column];
  // @@ Looks wrong: the decision should depend on the type, not the value
  return is_container(cellValue);
}

const stuff = {
  getCoord: getCoord,
  get_subgrid: get_subgrid,
  update_data_layer: update_data_layer,
  get_invisible_indexes: get_invisible_indexes,
  get_min_dl_value: get_min_dl_value,
  get_max_dl_value: get_max_dl_value,
  set_grid_width: set_grid_width,
  get_columns_width: get_columns_width,
  getColumnCount: getColumnCount,
  isFloat: isFloat,
  isInt: isInt,
  get_grid_height: get_grid_height,
  get_cell_index: get_cell_index,
  cell_is_composite: cell_is_composite,
};

export default stuff;
