import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as areaLabelActions from "../state/areaLabel";
import * as tabLabelsActions from "../state/tabs";
import * as modelDetailsActions from "../state/modelDetails";

import TabContent from "./TabContent";

function mapStateToProps(state) {
  return {
    tabs: state.tabs,
    selected: state.tabSelected.selected,
    allTabsContent: state.tabContentCollection,
    dragStatus: state.dragAndDrop.dragStatus,
    contentAction: state.ui["contentAction"],
    contextMenu: state.ui["info"].currentContextMenu,

    areaLabel: state.areaLabel,

    editing: state.editing.editing,
    editingChannel: state.editing.channel,
    editingFormula: state.editing.formula,

    zoomOutChannel: state.ui["info"].zoomOutChannel,
    modelDetails: state.modelDetails,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    areaLabelActions: bindActionCreators(areaLabelActions, dispatch),
    tabLabelsActions: bindActionCreators(tabLabelsActions, dispatch),
    modelDetailsActions: bindActionCreators(modelDetailsActions, dispatch),
    dispatch,
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(TabContent);
