import React from "react";

function Draggable(props) {
  function onDragStart(e) {
    if (typeof props.onDragStart === "function") {
      props.onDragStart(e);
    }
    e.dataTransfer.setData(props.type, props.data);
  }

  function onDragEnd(e) {
    if (typeof props.onDragEnd === "function") {
      props.onDragEnd(e);
    }
  }

  let properties = Object.assign({}, props);
  const enabled = props.enabled || true;
  if (enabled) {
    properties.draggable = "true";
    properties.onDragEnd = onDragEnd;
    properties.onDragStart = onDragStart;
  }

  return <div {...properties}>{props.children}</div>;
}

export default Draggable;
