/*
 * This file implements the tab labels for the tab panel of the VIEWS section.
 * LABEL CREATION:
 * Labels can be created by clicking on the gray PLUS (+) icon.
 *
 * LABEL EDITING:
 * Label names can be edited by double clicking on the target label.
 * The new label name can't collapse with the other existing labels nor
 * can have a length greater than 30 letters.
 *
 * LABEL DELETION:
 * Labels can be deleted by clicking on the TIMES (x) icon placed at
 * the right hand side of the tab label name.
 */

import React from "react";
import ASCII from "../constants/ascii.js";

import labelsAux from "./tabLabelsX";

const CR = ASCII.CR;

export default class TabLabels extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      labelWidth: 0,
      editLabel: undefined,
    };

    this._createTab = this._createTab.bind(this);
    this._renameTabLabel = this._renameTabLabel.bind(this);
  }

  componentDidMount() {
    this.props.createDefaultTab();
  }

  componentDidUpdate(prevProps) {
    const { labelAction, setCreateMC, createMC } = this.props;
    const { editLabel } = this.state;

    if (editLabel) {
      const inputID = document.getElementById(editLabel + "-input");
      inputID.focus();
    }

    if (createMC) {
      setCreateMC(false);
      this._createTab("Monte Carlo");
    }

    if (labelAction !== prevProps.labelAction) {
      const label = labelAction.label;
      const action = labelAction.action;
      switch (action) {
        case "add":
          this._createTab(label);
          break;
        case "switch":
          this._switchActiveTab(label);
          break;
        case "remove":
          this._removeTab(label);
          break;
        default:
          break;
      }
    }
  }

  _viewsToSuspend(childrenViews) {
    for (let index in childrenViews) {
      const channel = childrenViews[index];
      this.props.suspendView(channel);
    }
  }

  _viewsToResume(childrenViews, newSelected) {
    const { allTabsContent, model } = this.props;

    for (let i in childrenViews) {
      // The code patch below suggests that the childrenViews contains removed areas
      const channel = childrenViews[i];
      // see also uxm.js -> patch: testing for falsy allTabsContent[newSelected][channel]
      const xchan = allTabsContent[newSelected][channel];
      if (xchan) {
        // warning: appears to assume there is only one subscribed expression per view
        const expression = xchan.expression;
        const rtype = model[expression] ? model[expression].rtype : "";
        // note: user defined types are not suscribed to updates
        if (rtype !== "type") {
          this.props.resumeView(channel);
        }
      }
    }
  }

  _viewsToRemove(childrenViews, removedIndex) {
    const { allTabsContent, removeView } = this.props;

    for (let i in childrenViews) {
      // The code patch below suggests that the childrenViews contains removed areas
      const channel = String(childrenViews[i]);
      // see also uxm.js -> patch: testing for falsy allTabsContent[newSelected][channel]
      const xchan = allTabsContent[removedIndex][channel];
      if (xchan) {
        // warning: appears to assume there is only one subscribed expression per view
        const expression = xchan.expression;
        removeView(channel, expression);
      }
    }
  }

  _switchActiveTab(label) {
    const { tabs, selected, selectedActions } = this.props;

    const index = labelsAux.getTabIndex(label, tabs);
    if (index === -1) return;

    selectedActions.editSelected(index);

    if (selected !== -1) {
      this._viewsToSuspend(tabs[selected].childrenViews);
    }

    if (tabs[index]) {
      this._viewsToResume(tabs[index].childrenViews, index);
    }
  }

  _createTab(viewName) {
    const {
      tabs,
      selected,
      tabLabelsActions,
      selectedActions,
      tabContentsActions,
    } = this.props;

    const create = labelsAux.getTabIndex(viewName, tabs) === -1;

    if (create) {
      tabLabelsActions.addLabel(viewName);
      tabContentsActions.addTabContent();
      selectedActions.editSelected(tabs.length);
      if (selected !== -1) {
        this._viewsToSuspend(tabs[selected].childrenViews);
      }
    }
  }

  _removeTab(label) {
    const {
      tabs,
      selected,
      tabLabelsActions,
      tabContentsActions,
      selectedActions,
    } = this.props;

    const removedIndex = labelsAux.getTabIndex(label, tabs);
    if (removedIndex === -1) return;

    let newSelectedIndex = labelsAux.newSelected(
      tabs.length,
      selected,
      removedIndex
    );

    this._viewsToRemove(tabs[removedIndex].childrenViews, removedIndex);
    tabLabelsActions.deleteLabel(label);
    tabContentsActions.deleteTabContent(removedIndex);

    selectedActions.editSelected(newSelectedIndex);

    if (newSelectedIndex !== -1) {
      if (removedIndex <= newSelectedIndex) {
        newSelectedIndex = newSelectedIndex + 1;
      }
      const newActiveTab = tabs[newSelectedIndex];

      if (newActiveTab.label !== label) {
        this._viewsToResume(newActiveTab.childrenViews, newSelectedIndex);
      }
    }
  }

  _showInput(label) {
    const labelDOM = document.getElementById(label);
    const labelWidth = labelDOM.offsetWidth; // width, in pixels, of the label to be edited
    this.setState({ editLabel: label, labelWidth: labelWidth });
  }

  _renameTabLabel(event) {
    const { selected, showMessage, tabLabelsActions, tabs } = this.props;

    if (event.keyCode === CR) {
      // if enter key is pressed, try to rename label
      // let oldTabLabel = tabs[selected].label;
      const tabID = tabs[selected].id;
      const newTabLabel = this.refs.tabLabelInput.value;

      if (newTabLabel.length > 32) {
        showMessage("Tab label name is too long");
      } else if (newTabLabel.length === 0) {
        showMessage("Tab label name cannot be empty");
      } else {
        let create = true;
        for (const tab of tabs) {
          if (tab.label === newTabLabel) {
            create = false;
            break;
          }
        }

        if (create) {
          tabLabelsActions.editLabel(tabID, newTabLabel);
        } else {
          showMessage("Tab label name '" + newTabLabel + "' already exists");
        }
      }

      this.setState({ editLabel: undefined, labelWidth: 0 });
    }
  }

  render() {
    const { tabs, selected } = this.props;

    return (
      <ul
        className="tabs-labels"
        // role="tablist"
      >
        {tabs.map((tab, index) => {
          let activeClass = selected === index ? "active" : "";
          return (
            <li
              // role="tab"
              key={tab.label}
              // aria-controls={`panel${index}`}
              className={activeClass}
            >
              {this.state.editLabel !== tab.label ? (
                <button
                  id={tab.label}
                  className="tabs-labels"
                  onClick={() => this._switchActiveTab(tab.label)}
                  onDoubleClick={this._showInput.bind(this, tab.label)}
                >
                  {tab.label}
                </button>
              ) : (
                <input
                  id={`${tab.label}-input`}
                  ref={"tabLabelInput"}
                  className="editing-label"
                  defaultValue={tab.label}
                  style={{ width: String(this.state.labelWidth) + "px" }}
                  onBlur={() =>
                    this.setState({ editLabel: undefined, labelWidth: 0 })
                  }
                  onKeyUp={this._renameTabLabel}
                />
              )}
              <i
                className="fa fa-times"
                onClick={this._removeTab.bind(this, tab.label)}
              />
            </li>
          );
        })}

        <li>
          <button
            className="tabs-labels"
            onClick={() => this._createTab(labelsAux.nextLabelName(tabs))}
          >
            <i className="fa fa-plus" />
          </button>
        </li>

      </ul>
    );
  }
}
