import React from "react";
import ReactModal from "react-modal";
import customStyles from "../gui/custom-styles";

class FileDialog extends React.Component {
  render() {
    const { show, action, modelName, onClose, onAction } = this.props;

    return (
      <ReactModal
        isOpen={show}
        containerClassName="modal-dialog"
        onAfterOpen={null}
        onRequestClose={onClose}
        style={customStyles}
        ariaHideApp={false}
      >
        <i
          onClick={onClose}
          id="close-modal-fa-icon"
          className="fa fa-times"
          style={{ float: "right", color: "white" }}
        />

        <p style={{ textAlign: "center" }}>
          {action === "save" ? "Save Model" : "Load Model"}
        </p>

        <br />

        <form className="modal-body">
          <label htmlFor="filename" style={{ color: "white" }}>
            {"Model: "}
          </label>

          <input
            type="text"
            autoFocus={true}
            ref="name"
            defaultValue={modelName}
            id="filename"
            width="40px"
          />

          <div className="modal-button">
            <button
              type="submit"
              style={{ float: "right" }}
              onClick={event => {
                event.preventDefault();
                onAction(this.refs.name.value);
              }}
            >
              {action}
            </button>
          </div>
        </form>
      </ReactModal>
    );
  }
}

export default FileDialog;
