import React from "react";
import classnames from "classnames";

import Popover from "../gui/Popover";
import Comment from "../gui/Comment";

import AreaMenu from "./AreaMenu";
import DefinedArea from "./DefinedArea";

import logger from "../logger/logger";

class Area extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      renderValue: false,
      showConfigItems: false,
      showLabel: true,
      showMenuIcon: true,
    };

    this.updates = 0;

    this._hideMenuIcon = this._hideMenuIcon.bind(this);
    this._showMenuIcon = this._showMenuIcon.bind(this);

    this.startEditingAreaLabel = this.startEditingAreaLabel.bind(this);
    this.stopEditingAreaLabel = this.stopEditingAreaLabel.bind(this);
    this.toggleConfigMenu = this.toggleConfigMenu.bind(this);
    this.toggleShowLabel = this.toggleShowLabel.bind(this);
    this.updateAreaLabel = this.updateAreaLabel.bind(this);
  }

  // Merdex 6908 - componentDidMount

  componentDidUpdate() {
    const { elem, elemDetails } = this.props;

    ++this.updates;

    if (Object.keys(elemDetails.type).length > 0 && !this.state.renderValue) {
      this.setState({ renderValue: true });
    }

    if (elem.expression === "@MC" && !this.state.renderValue) {
      this.setState({ renderValue: true });
    }
  }

  _showMenuIcon() {
    this.setState({ showMenuIcon: true });
  }

  _hideMenuIcon() {
    this.setState({ showMenuIcon: false });
  }

  toggleConfigMenu(event) {
    this.setState({ showConfigItems: !this.state.showConfigItems });
  }

  updateAreaLabel(oldExpression, newExpression, channel) {
    logger.debug(`Area oldExpression ${oldExpression}`);
    logger.debug(`Area newExpression ${newExpression}`);
    this.stopEditingAreaLabel();
    this.props.changeExpressionInArea(oldExpression, newExpression, channel);
    this.props.send("updateAreaLabel", "sys.tick();", channel);
  }

  startEditingAreaLabel() {
    const { elem, areaLabelActions } = this.props;
    areaLabelActions.toggleEditingStatus(true, elem.channel);
    this.props.uiActions.editFormulaInput(elem.expression);
  }

  stopEditingAreaLabel() {
    this.props.areaLabelActions.resetState();
  }

  toggleShowLabel() {
    this.setState({ showLabel: !this.state.showLabel });
  }

  render() {
    const {
      areaLabel,
      elem,
      elemDetails,
      disableRunButton,
      formulaInput,
      model,
      modelDetailsActions,
      removeArea,
      assign,
      selected,
      send,
      tabs,
      uiActions,
      zoomIn,
      zoomOut,
    } = this.props;

    // Merdex U7062

    if (!elem) return null; // aguanta marrano!

    let AreaContent = null;
    const state = model[elem.expression] ? model[elem.expression].state : "";

    if (state !== "F") {
      AreaContent = (
        <DefinedArea
          areaLabel={areaLabel}
          disableRunButton={disableRunButton}
          elem={elem}
          elemDetails={elemDetails}
          formulaInput={formulaInput}
          modelDetailsActions={modelDetailsActions}
          removeArea={removeArea}
          assign={assign}
          selected={selected}
          send={send}
          tabs={tabs}
          uiActions={uiActions}
          zoomIn={zoomIn}
          zoomOut={zoomOut}
          renderValue={this.state.renderValue}
          showConfigItems={this.state.showConfigItems}
          showLabel={this.state.showLabel}
          showMenuIcon={this.state.showMenuIcon}
          showMessage={this.props.showMessage}
          startEditingAreaLabel={this.startEditingAreaLabel}
          stopEditingAreaLabel={this.stopEditingAreaLabel}
          toggleConfigMenu={this.toggleConfigMenu}
          toggleShowLabel={this.toggleShowLabel}
          updateAreaLabel={this.updateAreaLabel}
        />
      );
    }

    return (
      <div
        // note: we need to keep the height or suffer  vertical Lambada
        style={{ height: "100%", paddingBottom: "15px" }}
        className={classnames("elem-wrapper", "popover")}
        onContextMenu={event => {
          event.preventDefault();
          this.toggleConfigMenu(event);
        }}
      >
        <Popover widgetClicked={this.state.showConfigItems}>
          <Comment> menu to zoom-out, remove the area, etc. </Comment>
          <AreaMenu
            canZoomOut={elem.stack.length > 0}
            channel={elem.channel}
            editElemDetails={modelDetailsActions.editElemDetails}
            elemDetails={elemDetails}
            handleRemove={() =>
              removeArea(tabs[selected].label, elem.expression, elem.channel)
            }
            showLabel={this.state.showLabel}
            toggleShowLabel={this.toggleShowLabel}
            zoomIn={zoomIn}
            zoomOut={zoomOut}
          />
        </Popover>
        {AreaContent}
      </div>
    );
  }
}

export default Area;
