// Action types

const EDIT_ELEM_DETAILS = "EDIT_ELEM_DETAILS";
const DELETE_ELEM_DETAILS = "DELETE_ELEM_DETAILS";
const RESET_ELEM_DETAILS = "RESET_ELEM_DETAILS";
const RESET_ELEM_VALUE_DETAILS = "RESET_ELEM_VALUE_DETAILS";

// Action creators

export function editElemDetails(channel, detail) {
  return { type: EDIT_ELEM_DETAILS, channel, detail };
}

export function deleteElemDetails(channel) {
  return { type: DELETE_ELEM_DETAILS, channel };
}

export function resetElemDetails(channel) {
  return { type: RESET_ELEM_DETAILS, channel };
}

export function resetElemValueDetails(channel) {
  return { type: RESET_ELEM_VALUE_DETAILS, channel };
}

// Reducer

function reducer(state = {}, action) {
  switch (action.type) {
    case EDIT_ELEM_DETAILS:
      return {
        ...state,
        [action.channel]: {
          ...state[action.channel],
          ...action.detail,
        },
      };

    case DELETE_ELEM_DETAILS:
      return Object.keys(state)
        .filter((key) => key !== action.channel)
        .reduce((result, current) => {
          result[current] = state[current];
          return result;
        }, {});

    case RESET_ELEM_DETAILS:
      return {
        ...state,
        [action.channel]: {
          ltype: undefined,
          type: {},

          rowCount: undefined,

          headerLabels: undefined,
          minimumBatchSize: undefined,
          orientation: undefined,
          showIndexes: undefined,

          subLimits: [0, 0], // current rowStart and rowStop
          editingRowNumber: -1,
          editingColumnNumber: -1,
          startEdition: false,
        },
      };

    case RESET_ELEM_VALUE_DETAILS:
      return {
        ...state,
        [action.channel]: {
          ltype: state[action.channel].ltype,
          type: state[action.channel].type,

          rowCount: undefined,

          headerLabels: undefined,
          minimumBatchSize: undefined,
          orientation: undefined,
          showIndexes: undefined,

          subLimits: [0, 0],
          editingRowNumber: -1,
          editingColumnNumber: -1,
          startEdition: false,
        },
      };

    default:
      // console.error(`modelDetails.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
