import React from "react";

import VariableDefinition from "../dialogs/VariableDefinition";

import VariantIcon from "./icons/VariantIcon";
import CheckBoxIcon from "./icons/CheckBoxIcon";
// import RadioButtonIcon from "./icons/RadioButtonIcon";
// import SwitchIcon from "./icons/SwitchIcon";
import IntegerIcon from "./icons/IntegerIcon";
import DoubleIcon from "./icons/DoubleIcon";
import StringIcon from "./icons/StringIcon";
import ArrayIcon from "./icons/ArrayIcon";
import MatrixIcon from "./icons/MatrixIcon";
import FunctionIcon from "./icons/FunctionIcon";

import PaletteItems from "./PaletteItems.js";

import UI from "../constants/UI";

const paletteItems = [
  {
    name: "Variant",
    type: "lazy var",
    level: UI.BEGINNER,
    icon: <VariantIcon />,
  },
  {
    name: "Boolean",
    type: "lazy bool",
    level: UI.INTERMEDIATE,
    icon: <CheckBoxIcon />,
  },
  /*
  {
    name: "Boolean",
    type: "lazy bool",
    level: UI.DEVELOPER,
    icon: <RadioButtonIcon />,
  },
  {
    name: "Boolean",
    type: "lazy bool",
    level: UI.DEVELOPER,
    icon: <SwitchIcon />,
  },
  */
  {
    name: "Integer",
    type: "lazy int",
    level: UI.INTERMEDIATE,
    icon: <IntegerIcon />,
  },
  {
    name: "Double",
    type: "lazy double",
    level: UI.INTERMEDIATE,
    icon: <DoubleIcon />,
  },
  {
    name: "String",
    type: "lazy string",
    level: UI.INTERMEDIATE,
    icon: <StringIcon />,
  },
  {
    name: "1D Array",
    type: "array[] => lazy var",
    level: UI.INTERMEDIATE,
    icon: <ArrayIcon />,
  },
  {
    name: "2D Array",
    type: "array[,] => lazy var",
    level: UI.INTERMEDIATE,
    icon: <MatrixIcon />,
  },
  {
    name: "Worksheet",
    type: "Worksheet",
    level: UI.INTERMEDIATE,
    icon: <MatrixIcon />,
  },
  {
    name: "Function",
    type: "::",
    level: UI.EXPERT,
    icon: <FunctionIcon />,
  },
];

// This component is called from the Center Component which is placed in "Main" folder
class Palette extends React.Component {
  constructor(props) {
    super(props);

    this.state = { selected: -1, hideMessageToClient: true, errorMessage: "" };

    // this._setActive = this._setActive.bind(this);
    this._hideMessage = this._hideMessage.bind(this);

    this.onSubmit = this.onSubmit.bind(this);
    // this.toggleDefinition = this.toggleDefinition.bind(this);
    this.showMessage = this.showMessage.bind(this);
  }

  /*
  _setActive(index) {
    if (this.state.selected === index) {
      this.setState({ selected: -1 });
    } else {
      this.setState({ selected: index });
    }
  }
  */

  _hideMessage() {
    this.setState({ hideMessageToClient: true });
  }

  /*
  toggleDefinition() {
    this._setActive(this.state.selected);
  }
  */

  showMessage(message) {
    this.setState({ hideMessageToClient: false, errorMessage: message });
    setTimeout(this._hide, 4096);
  }

  ///
  /// Variable Definition Dialog
  ///

  // @@ Move this to Main!
  onSubmit(payload, addArea) {
    const { symbol, definition } = payload;
    if (definition) {
      this.props.addDefinition(symbol, definition, addArea);
    }
    // this.toggleDefinition();
  }

  render() {
    const selected = this.state.selected;

    const items = paletteItems.filter(x => x.level <= UI.MODE);

    const element =
      0 <= selected ? (
        <VariableDefinition
          showMessage={this.showMessage}
          model={this.props.model}
          type={items[selected].type}
          onClose={this.toggleDefinition}
          onSubmit={this.onSubmit}
          addArea={false}
        />
      ) : null;

    return (
      <div id="object-palette">
        <PaletteItems
          selected={selected}
          dragActions={this.props.dragActions}
          paletteItems={items}
        />

        {element}
      </div>
    );
  }
}

export default Palette;
