function pick(names, model) {
  for (const symbol of names) {
    if (!model[symbol]) {
      return symbol;
    }
  }
  for (let i = 0; i < 4; ++i) {
    for (const name of names) {
      const symbol = name + i;
      if (!model[symbol]) {
        return symbol;
      }
    }
  }
  return "";
}

function suggestedSymbol(type, model) {
  if (type === "bool" || type === "lazy bool") {
    return pick(["flag"], model);
  }
  if (type === "int" || type === "lazy int") {
    return pick(["i", "j", "k", "l", "m", "n"], model);
  }
  if (type === "double" || type === "lazy double") {
    return pick(["x", "y", "z"], model);
  }
  if (type === "string" || type === "lazy string") {
    return pick(["s"], model);
  }
  if (type === "array[] => lazy var") {
    return pick(["vector"], model);
  }
  if (type === "array[] => double" || type === "array[] => lazy double") {
    return pick(["vector"], model);
  }
  if (type.startsWith("array[]") || type.startsWith("lazy array[]")) {
    return pick(["a"], model);
  }
  if (type === "Worksheet") {
    return pick(["WS"], model);
  }
  if (type === "array[,] => lazy var") {
    return pick(["matrix"], model);
  }
  if (type === "array[,] => double" || type === "array[,] => lazy double") {
    return pick(["matrix"], model);
  }
  if (type.startsWith("array[,]") || type.startsWith("lazy array[,]")) {
    return pick(["a"], model);
  }
  if (type.startsWith("array[,] => struct {")) {
    return pick(["table"], model);
  }
  if (type.startsWith("::")) {
    return pick(["f", "g", "h"], model);
  }
  return pick(["variable"], model);
}

function defaultValue(type) {
  if (type === "var" || type === "lazy var") {
    return "uniform()";
  } else if (type === "bool" || type === "lazy bool") {
    return "false";
  } else if (type === "int" || type === "lazy int") {
    return "0";
  } else if (type === "double" || type === "lazy double") {
    return "0.0";
  } else if (type === "string" || type === "lazy string") {
    return '""';
  } else if (type.startsWith("array")) {
    return "[]";
  } else if (type.startsWith("::")) {
    return "fn(x) -> x";
  } else {
    return "?";
  }
}

function isLazy(type) {
  return type === "var" || type.indexOf("lazy") === 0;
}

export default { defaultValue, isLazy, suggestedSymbol };

