///
/// VariableDefinition.js
///
/// Brute force dispatch of variable definition dialog
///
/// LRQS 8
///

import React from "react";

import SVD from "./SimpleVar-Dialog";
import Array1D from "./Array1D-Dialog";
import Array2D from "./Array2D-Dialog";

function VariableDefinition(props) {
  const type = props.type;

  if (type === "lazy var") {
    return <SVD {...props} />;
  }
  if (type === "bool" || type === "lazy bool") {
    return <SVD {...props} />;
  }
  if (type === "int" || type === "lazy int") {
    return <SVD {...props} />;
  }
  if (type === "double" || type === "lazy double") {
    return <SVD {...props} />;
  }
  if (type === "string" || type === "lazy string") {
    return <SVD {...props} />;
  }
  if (type === "array[] => lazy var") {
    return <Array1D {...props} nrow="6" />;
  }
  if (type === "array[,] => lazy var") {
    return <Array2D {...props} nrow="6" ncol="6" />;
  }
  if (type === "Worksheet") {
    return <Array2D {...props} nrow="12" ncol="8" />;
  }
  if (type === "::") {
    return <SVD {...props} />;
  }
}

export default VariableDefinition;
