import React from "react";
import classnames from "classnames";

import { InfiniteLoader, Grid, ScrollSync } from "react-virtualized";

import UI from "../constants/UI";
// import logger from "../logger/logger";

class ByColumns extends React.Component {
  constructor(props) {
    super(props);
    this._renderHeaderCell = this._renderHeaderCell.bind(this);
  }

  componentDidUpdate(prevProps) {
    const { formula, gridName } = this.props;

    // logger.debug(`ByRows::componentDidUpdate(${prevProps})`);
    const grid = document.getElementById(gridName + "-grid-horizontal");
    if (!grid) {
      return; // !! ad-hoc patch to prevent grid.style crash
    }
    grid.style.width = this.props.gridWidth + "px";

    // Do not update the grid when editing a formula
    if (formula) return;

    this.refs.scrollSyncComponent.forceUpdate();
  }

  _renderHeaderCell({ columnIndex, key, rowIndex, style }) {
    return this._renderLeftHeaderCell({ columnIndex, key, rowIndex, style });
  }

  _renderLeftHeaderCell({ columnIndex, key, rowIndex, style }) {
    return (
      <div className="headerCell" key={key} style={style}>
        {`${columnIndex}`}
      </div>
    );
  }

  render() {
    const { rows, columns, gridHeight, gridWidth } = this.props;

    const display = this.props.showIndexes ? "inline" : "none";
    return (
      <ScrollSync ref="scrollSyncComponent">
        {({ onScroll, scrollLeft }) => (
          <div className="Table">
            <div
              className={classnames({ lazyEdit: this.props.editLazyMode })}
              style={{
                height: "30px",
                width: gridWidth,
                display: display,
              }}
            >
              <Grid
                className="HeaderGrid"
                rowCount={1}
                columnCount={columns}
                cellRenderer={this._renderHeaderCell}
                columnWidth={UI.MIN_CELL_WIDTH}
                height={UI.ROW_HEIGHT}
                rowHeight={UI.ROW_HEIGHT}
                width={gridWidth}
                scrollLeft={scrollLeft}
              />
            </div>
            <div className={"bottom-grid"}>
              <InfiniteLoader
                // ref="infiniteLoader"
                isRowLoaded={this.props._isRowLoaded}
                loadMoreRows={this.props._loadMoreRows}
                rowCount={rows}
                minimumBatchSize={this.props.minimumBatchSize}
              >
                {({ onRowsRendered, registerChild }) => (
                  <Grid
                    onScroll={onScroll}
                    ref={registerChild}
                    height={gridHeight}
                    width={gridWidth}
                    rowHeight={UI.ROW_HEIGHT}
                    columnCount={columns}
                    columnWidth={this.props._getColumnWidth}
                    overscanRowCount={0}
                    cellRenderer={this.props._cellRenderer}
                    rowCount={rows}
                    onSectionRendered={(section) =>
                      this.props._onGridRowsRendered(section, onRowsRendered)
                    }
                  />
                )}
              </InfiniteLoader>
            </div>
          </div>
        )}
      </ScrollSync>
    );
  }
}

export default ByColumns;
