///
/// PlainValue.js
///
/// Component created by Value
///
/// Description: shows the value of an expression in an area of the tab panel
///
/// LRQS 3
///

import React from "react";
import classnames from "classnames";
import TextInput from "./TextInput";
import parse from "zll/Parser";
import CHANNEL from "../constants/channel";

export default class PlainValue extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isEditing: this.props.formula !== undefined,
      programmaticInputValue: "",
    };

    this._changeInputValue = this._changeInputValue.bind(this);
    this._finishEditing = this._finishEditing.bind(this);
    this._handleSave = this._handleSave.bind(this);
    this._onEditGesture = this._onEditGesture.bind(this);
  }

  componentDidUpdate(prevProps) {
    const { assignment, channel, editAreaValue } = this.props;

    if (assignment !== prevProps.assignment && assignment.channel === channel) {
      let action = assignment.action;
      let value = assignment.value;

      if (action === "select") {
        this._onEditGesture();
      } else if (action === "edit") {
        this._changeInputValue(value);
      } else if (action === "commit") {
        this._handleSave(value);
        editAreaValue({
          action: "",
          label: "",
          expression: "",
          channel: CHANNEL.NONE,
          rowIndex: -1,
          columnIndex: -1,
          value: "",
        });
      }
    }
  }

  _onEditGesture() {
    const {
      send,
      channel,
      expression,
      ltype,
      requestFormulaToUpdate,
      startEditingAreaLabel,
    } = this.props;

    if (ltype === "type") {
      alert("types cannot be changed");
      return;
    } else if (
      ltype.startsWith("invalid(") ||
      ltype === "?" ||
      ltype === "#TYPE!"
    ) {
      startEditingAreaLabel(); // not working well in general ... see issue #58
    } else {
      this.setState({ isEditing: true });
      requestFormulaToUpdate(expression, channel);
      // suspend asynchronous updates
      send(
        "_onEditGesture",
        `sys.view("${channel}", "suspend", ?, ?);`,
        channel
      );
      /*
      send(
        "_onEditGesture",
        `sys.view("${channel}", "update", ['${expression}'], []);`,
        channel
      );
      */
    }
  }

  _changeInputValue(value) {
    this.setState({ programmaticInputValue: value });
  }

  _finishEditing() {
    const { channel, expression, finishEditingStatus, send } = this.props;
    finishEditingStatus();
    this.setState({ isEditing: false });
    // request first paint ...
    send("_finishEditing", `${expression}`, channel);
    // ... and resume asynchronous updates
    send("_finishEditing", `sys.view("${channel}", "resume", ?, ?);`, channel);
    /*
    send(
      "_finishEditing",
      `sys.view("${channel}", "update", [], ['${expression}']);`,
      channel
    );
    */
  }

  // if the expression value is updated
  _handleSave(rhs) {
    const { channel, expression: lhs, assign, value } = this.props;

    rhs = rhs.trim();
    if (rhs !== value) {
      assign(channel, lhs, rhs);
    }

    this._finishEditing();
  }

  render() {
    let { formula, value } = this.props;

    const { isEditing, programmaticInputValue } = this.state;

    // special rendering of invalid values: show error code
    if (value.startsWith("invalid(")) {
      const ast = parse(value, "PlainValue::render 126");
      value = ast.args.elems[0].value;
    }

    return (
      <div className={classnames({ editing: isEditing })}>
        {isEditing && formula ? (
          <TextInput
            inputID={"plain-input"}
            formulaInput={this.props.formulaInput}
            editFormulaInput={this.props.editFormulaInput}
            onEscape={this._finishEditing}
            onSave={(text) => this._handleSave(text)}
            placeholder={"formula"}
            showMessage={this.props.showMessage}
            text={programmaticInputValue || formula}
            toggleWideEditor={this.props.toggleWideEditor}
            wideEditor={this.props.wideEditor}
          />
        ) : (
          <div onClick={this._onEditGesture}>{value}</div>
        )}
      </div>
    );
  }
}
