function getTabIndex(label, tabs) {
  let index = 0;
  for (const tab of tabs) {
    if (tab.label === label) {
      return index;
    }
    ++index;
  }
  return -1;
}

function nextLabelName(tabs) {
  for (const i in tabs) {
    const label = "View " + i;
    let found = false;
    for (const tab of tabs) {
      if (label === tab.label) {
        found = true;
        break;
      }
    }
    if (!found) {
      return label;
    }
  }
  return "View " + tabs.length;
}

function newSelected(tabsLength, selected, removed) {
  if (selected < removed) {
    return selected;
  }
  if (removed < selected) {
    return selected - 1;
  }
  if (removed !== 0) {
    return selected - 1;
  }
  // this also "cleverly" returns -1 when the last tab is deleted
  return tabsLength - 2;
}

const stuff = {
  nextLabelName: nextLabelName,
  getTabIndex: getTabIndex,
  newSelected: newSelected,
};

export default stuff;
