import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as uiActions from "../state/ui";

import Header from "./Header";

function mapStateToProps(state) {
  return {
    autoRefresh: state.ui["toggle"].autoRefresh,
    logC2S: state.ui["toggle"].logC2S,
    logS2C: state.ui["toggle"].logS2C,
    model: state.model,
  };
}

// Uses the approach of pre-bind action creator to automatically call dispatch
// http://blog.isquaredsoftware.com/2016/10/idiomatic-redux-why-use-action-creators/
function mapDispatchToProps(dispatch) {
  return {
    uiActions: bindActionCreators(uiActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);

// The function mapDispatchToProps wraps all the action creators
// with the dispatch method and passes them to the component.
