// Action types

const GET_SERVER_MESSAGE = "GET_SERVER_MESSAGE";

// Action constructors

export function editServerMessage(message) {
  return { type: GET_SERVER_MESSAGE, message };
}

// Reducer

const INITIAL_STATE = {
  serverMessage: "",
};

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case GET_SERVER_MESSAGE:
      return {
        serverMessage: action.message,
      };

    default:
      // console.error(`serverMessage.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
