/// ContextMenu.jsx
///
/// Experimental Context Menu (based on react-contextmenu)
///
/// LRQS 9
///

import React from "react";
import { ContextMenu, MenuItem } from "react-contextmenu";

function ZContextMenu(props) {
  const { id, handler, menu } = props;
  return (
    <ContextMenu id={id}>
      {menu.map((i, key) =>
        i.divider ? (
          <MenuItem key={key} divider />
        ) : (
          <MenuItem key={key} data={{ item: i.item }} onClick={handler}>
            {i.description}
          </MenuItem>
        )
      )}
    </ContextMenu>
  );
}

export default ZContextMenu;
