let logger = root.logger;
if (!logger && typeof require !== "undefined") {
  logger = require("./logger.js");
} else {
  logger = root.window.logger;
}

// return ast.args.elems, if present
export default function elems(from, ast) {
  if (!ast) {
    logger.debug(`sheiBe in elems(${from}, ${ast}): ast is undefined!`);
    return undefined;
  }

  if (!ast.args) {
    logger.debug(`sheiBe in elems(${from}, ${ast}): ast.args is undefined!`);
    return undefined;
  }

  return ast.args.elems;
}
