///
/// Lexer.js
///
/// lexical analyzer
///
/// LRQS 7 (check possibly excessive use of functions in function lexer)
///

/// Dual mode imports: Web browser and server

// const root = typeof exports !== "undefined" && exports !== null ? exports : window;
const root = typeof exports || window;

let TC = root.TC;
if (!TC && typeof require !== "undefined") {
  TC = require("./TokenCodes.js");
} else {
  TC = root.window.TC;
}

let Token = root.Token;
if (!Token && typeof require !== "undefined") {
  Token = require("./Token.js");
} else {
  Token = root.window.Token;
}

let AST = root.AST;
if (!AST && typeof require !== "undefined") {
  AST = require("./AST.js");
} else {
  AST = root.window.AST;
}

let logger = root.logger;
if (!logger && typeof require !== "undefined") {
  logger = require("./logger.js");
} else {
  logger = root.window.logger;
}

const debugging = false;

const keywords = {
  array: TC.ARRAY_T,
  bool: TC.BOOL_T,
  double: TC.DOUBLE_T,
  false: TC.BOOLEAN,
  fn: TC.LAMBDA,
  int: TC.INT_T,
  lazy: TC.LAZY_T,
  string: TC.STRING_T,
  struct: TC.STRUCT_T,
  fun: TC.FUNCTION_T,
  true: TC.BOOLEAN,
  var: TC.VAR_T,
};

function Lexer() {
  const self = this;

  self.skippable = function (character) {
    let ascii = character.charCodeAt(0);
    let result = ascii <= 32 || ascii === 127 || ascii >= 255;
    return result;
  };

  self.match_to = function (character) {
    if (self.index >= self.input.length) {
      return false;
    } else if (self.input[self.index] === character) {
      ++self.index;
      return true;
    } else {
      return false;
    }
  };

  self.digit = function () {
    if (self.index >= self.input.length) {
      return false;
    } else {
      let character = self.input[self.index];
      let ascii = character.charCodeAt(0);
      let result = 47 < ascii && ascii < 58;

      return result;
    }
  };

  self.integer = function () {
    let result = "";

    while (self.digit()) {
      result += self.input[self.index++];
    }

    return result;
  };

  self.floating_point = function () {
    let result = "";

    if (self.input[self.index] === ".") {
      if (self.input[self.index + 1] !== ".") {
        ++self.index;
        result += ".";
        let fractional = self.integer(self.index);
        result += fractional;
      }
    }

    return result;
  };

  self.string = function () {
    let result = "";

    while (self.match_to('"') === false && self.index < self.input.length) {
      if (self.match_to("\\")) {
        if (self.match_to('"')) {
          result += '"';
        } else {
          result += "\\";
          if (self.match_to("\\") === false) {
            result += self.input[self.index++];
          }
        }
      } else {
        result += self.input[self.index++];
      }
    }

    if (self.input[self.index - 1] === '"') {
      return new Token(TC.STRING, result);
    } else {
      return self.fail("unclosed string : " + result);
    }
  };

  self.number = function () {
    let result = self.integer();
    let extra = self.floating_point();
    result += extra;

    if (self.finished()) {
      if (extra.length === 0) {
        return new Token(TC.INTEGER, parseInt(result));
      } else {
        return new Token(TC.DOUBLE, parseFloat(result));
      }
    }

    let exponentiated = self.match_to("e") || self.match_to("E");

    if (exponentiated === true) {
      result += "E";

      if (self.match_to("+")) {
        result += "+";
      } else if (self.match_to("-")) {
        result += "-";
      }

      let exponent = self.integer();
      exponent += self.floating_point();

      result += exponent;

      if (exponent.length !== 0 && self.finished()) {
        return new Token(TC.DOUBLE, parseFloat(result));
      }
    }

    while (self.finished() === false) {
      result += self.input[self.index++];
    }

    return self.fail("malformed number : " + result);
  };

  self.symbol = function () {
    let result = "";
    while (self.finished() === false) {
      result += self.input[self.index++];
    }

    let keyword = keywords[result];

    if (keyword) {
      return new Token(keyword, result);
    } else {
      return new Token(TC.SYMBOL, result);
    }
  };

  self.fail = function (message) {
    return function () {
      throw AST.Error("#SYNTAX!", message);
    };
  };

  self.read = function () {
    while (self.index < self.input.length) {
      let character = self.input[self.index];

      if (self.skippable(character)) {
        self.index++;
      } else {
        return self.lex();
      }
    }

    return new Token(TC.END);
  };

  self.next = function () {
    let actual = self.token;
    return actual;
  };

  self.match = function (code) {
    let actual = self.token;
    if (debugging) {
      logger.debug("matching " + actual.code + " with expected " + code);
    }
    if (code === actual.code) {
      self.token = self.read();
      return actual;
    } else {
      throw AST.Error(
        "#SYNTAX!",
        "failed to match " + actual.code + " with expected " + code
      );
    }
  };

  //
  // special symbols section
  //

  self.specials = {};

  self.specials[">"] = function () {
    if (self.match_to("=")) {
      return new Token(TC.GE, ">=");
    } else if (self.match_to("<")) {
      return new Token(TC.BWXOR, "><");
    } else {
      return new Token(TC.GT, ">");
    }
  };

  self.specials["<"] = function () {
    if (self.match_to("=")) {
      return new Token(TC.LE, "<=");
    } else if (self.match_to(">")) {
      return new Token(TC.NE, "<>");
    } else if (self.match_to("-")) {
      return new Token(TC.LARROW, "<-");
    } else {
      return new Token(TC.LT, "<");
    }
  };

  self.specials["!"] = function () {
    if (self.match_to("=")) {
      return new Token(TC.NE, "!=");
    } else {
      return new Token(TC.EXCLAMATION, "!");
    }
  };

  self.specials["&"] = function () {
    if (self.match_to("&")) {
      return new Token(TC.AND, "&&");
    } else {
      return new Token(TC.BWAND, "&");
    }
  };

  self.specials["|"] = function () {
    if (self.match_to("|")) {
      return new Token(TC.OR, "||");
    } else {
      return new Token(TC.BWOR, "|");
    }
  };

  self.specials["."] = function () {
    if (self.match_to(".")) {
      return new Token(TC.DOTDOT, "..");
    } else {
      return new Token(TC.DOT, ".");
    }
  };

  self.specials["="] = function () {
    if (self.match_to(">")) {
      return new Token(TC.YIELDS, "=>");
    } else if (self.match_to("=") && self.match_to(">")) {
      return new Token(TC.META_YIELDS, "==>");
    } else {
      return new Token(TC.EQ, "=");
    }
  };

  self.specials["-"] = function () {
    if (self.match_to(">")) {
      return new Token(TC.RARROW, "->");
    } else {
      return new Token(TC.MINUS, "-");
    }
  };

  self.specials[":"] = function () {
    if (self.match_to("=")) {
      return new Token(TC.ASSIGN, ":=");
    } else if (self.match_to("-")) {
      return new Token(TC.DEFINE, ":-");
    } else if (self.match_to(":")) {
      return new Token(TC.COLONCOLON, "::");
    } else {
      return new Token(TC.COLON, ":");
    }
  };

  self.specials["/"] = function () {
    if (self.match_to(".")) {
      return new Token(TC.LAMBDA, "/.");
    } else {
      return new Token(TC.SLASH, "/");
    }
  };

  self.specials["#"] = function () {
    let result = "#";
    let everything_ok = false;

    while (self.index < self.input.length) {
      result += self.input[self.index++];

      if (result.substr(-1) === "!" || result.substr(-1) === "?") {
        everything_ok = true;
        break;
      }
    }

    if (everything_ok) {
      return new Token(TC.ERROR, result);
    }

    return self.fail("Syntax error while parsing constant");
  };

  self.specials['"'] = function () {
    return self.string();
  };

  self.specials["+"] = function () {
    return new Token(TC.PLUS, "+");
  };

  self.specials["*"] = function () {
    return new Token(TC.STAR, "*");
  };

  self.specials["%"] = function () {
    return new Token(TC.PERCENT, "%");
  };

  self.specials["^"] = function () {
    return new Token(TC.RAISED, "^");
  };

  self.specials["~"] = function () {
    return new Token(TC.BWNOT, "~");
  };

  self.specials["'"] = function () {
    return new Token(TC.SQUOTE, "'");
  };

  self.specials["("] = function () {
    return new Token(TC.LPAREN, "(");
  };

  self.specials["["] = function () {
    return new Token(TC.LBRACKET, "[");
  };

  self.specials["{"] = function () {
    return new Token(TC.LBRACE, "{");
  };

  self.specials[")"] = function () {
    return new Token(TC.RPAREN, ")");
  };

  self.specials["]"] = function () {
    return new Token(TC.RBRACKET, "]");
  };

  self.specials["}"] = function () {
    return new Token(TC.RBRACE, "}");
  };

  self.specials[","] = function () {
    return new Token(TC.COMMA, ",");
  };

  self.specials["?"] = function () {
    return new Token(TC.QUESTION, "?");
  };

  self.specials[";"] = function () {
    return new Token(TC.SEMICOLON, ";");
  };

  //
  // end of special symbols section
  //

  self.finished = function () {
    if (self.index >= self.input.length) {
      return true;
    } else {
      let character = self.input[self.index];

      if (self.skippable(character)) {
        self.index++;
        return true;
      } else if (self.specials[character] != null) {
        return true;
      } else {
        return false;
      }
    }
  };

  self.lex = function () {
    let character = self.input[self.index];
    let special = self.specials[character];

    if (special == null) {
      if (self.digit()) {
        return self.number();
      } else {
        return self.symbol();
      }
    } else {
      self.index++;
      return special();
    }
  };

  self.start = function (input) {
    self.input = input;
    self.index = 0;
    self.token = self.read();
  };
}

module.exports = Lexer;
