///
/// ui.js
///
/// LRQS 3
///

import CHANNEL from "../constants/channel";

// autoRefresh:
// true when the ticker is not active but I want to get the server response
// false when we don't want the client to update automatically when receiving a non-async message
// showC2S: false,
// showS2C: false,
// showResponseBar: false

// Action types

const RESET_UI_VALUES = "RESET_UI_VALUES";
const TOGGLE_LOG_C2S = "TOGGLE_LOG_C2S";
const TOGGLE_LOG_S2C = "TOGGLE_LOG_S2C";
const TOGGLE_RESPONSE_BAR = "TOGGLE_RESPONSE_BAR";
const TOGGLE_MODEL_LIST_OPTIONS = "TOGGLE_MODEL_LIST_OPTIONS";
const RUN_MC_BUTTON = "RUN_MC_BUTTON";
const TOGGLE_WIDE_EDITOR = "TOGGLE_WIDE_EDITOR";

const EDIT_COMMANDLINE_RESPONSE = "EDIT_COMMANDLINE_RESPONSE";
const EDIT_TAB_LABEL = "EDIT_TAB_LABEL";
const EDIT_TAB_CONTENT = "EDIT_TAB_CONTENT";
const EDIT_AREA_VALUE = "EDIT_AREA_VALUE";
const RESET_AREA_VALUE = "RESET_AREA_VALUE";
const UPDATE_DEFAULT_DELAY = "UPDATE_DEFAULT_DELAY";
const UPDATE_ZOOM_OUT_CHANNEL = "UPDATE_ZOOM_OUT_CHANNEL";
const CURRENT_CONTEXT_MENU = "CURRENT_CONTEXT_MENU";
const EDITING_LTYPE = "EDITING_LTYPE";
const EDITING_COORDS = "EDITING_COORDS";
const UPDATE_MODEL_NAME = "UPDATE_MODEL_NAME";
const EDIT_FORMULA_INPUT = "EDIT_FORMULA_INPUT";

const EDIT_BUCKETS = "EDIT_BUCKETS";
const EDIT_LOWERBOUND = "EDIT_LOWERBOUND";
const EDIT_UPPERBOUND = "EDIT_UPPERBOUND";
const EDIT_ITERATIONS = "EDIT_ITERATIONS";

// Action constructors

export function resetToDefaultValues() {
  return { type: RESET_UI_VALUES };
}

export function toggleLogC2S() {
  return { type: TOGGLE_LOG_C2S };
}

export function toggleLogS2C() {
  return { type: TOGGLE_LOG_S2C };
}

export function toggleResponseBar(bool) {
  return { type: TOGGLE_RESPONSE_BAR, bool };
}

export function toggleModelListOptions() {
  return { type: TOGGLE_MODEL_LIST_OPTIONS };
}

export function runMCButton(bool) {
  return { type: RUN_MC_BUTTON, bool };
}

export function toggleWideEditor(bool) {
  return { type: TOGGLE_WIDE_EDITOR, bool };
}

export function editCommandLineResponse(commandLineResponse) {
  return { type: EDIT_COMMANDLINE_RESPONSE, commandLineResponse };
}

export function tabLabelAction(labelAction) {
  return { type: EDIT_TAB_LABEL, labelAction };
}

export function tabContentAction(contentAction) {
  return { type: EDIT_TAB_CONTENT, contentAction };
}

export function editAreaValue(areaValueAction) {
  return { type: EDIT_AREA_VALUE, areaValueAction };
}

export function resetAreaValue() {
  return { type: RESET_AREA_VALUE };
}

export function updateDefaultDelay(seconds) {
  return { type: UPDATE_DEFAULT_DELAY, seconds };
}

export function updateZoomOutChannel(channel) {
  return { type: UPDATE_ZOOM_OUT_CHANNEL, channel };
}

export function updateCurrentContextMenu(channel) {
  return { type: CURRENT_CONTEXT_MENU, channel };
}

export function updateEditingLtype(ltype) {
  return { type: EDITING_LTYPE, ltype };
}

export function updateEditingCoords(coords) {
  return { type: EDITING_COORDS, coords };
}

export function updateModelName(modelName) {
  return { type: UPDATE_MODEL_NAME, modelName };
}

export function editFormulaInput(formula) {
  return { type: EDIT_FORMULA_INPUT, formula };
}

export function editBuckets(buckets) {
  return { type: EDIT_BUCKETS, buckets };
}

export function editLowerBound(lowerBound) {
  return { type: EDIT_LOWERBOUND, lowerBound };
}

export function editUpperBound(upperBound) {
  return { type: EDIT_UPPERBOUND, upperBound };
}

export function editIterations(iterations) {
  return { type: EDIT_ITERATIONS, iterations };
}

// Reducer

let INITIAL_STATE = {
  toggle: {
    logC2S: false,
    logS2C: false,
    showResponseBar: false,
    showModelListOptions: false,
    runMC: false,
    wideEditor: false,
  },

  info: {
    commandLineResponse: "",
    labelAction: { label: "", action: "" },
    defaultDelay: 200,
    zoomOutChannel: CHANNEL.NONE,
    currentContextMenu: -1,
    editingLtype: "",
    editingCoords: "",
    modelName: "",
    formula: "",
  },

  contentAction: {
    label: "",
    action: "",
    expression: "",
    channel: CHANNEL.NONE,
    coordinates: [],
  },

  assignment: {
    action: "", // 1: enable input with formula, 2: update input with new value, 3: save changes
    label: "",
    expression: "",
    channel: CHANNEL.NONE,
    rowIndex: 0,
    columnIndex: 0,
    value: "",
  },

  montecarlo: {
    expression: "",
    buckets: 40,
    lowerBound: 0.0,
    upperBound: 1.0,
    iterations: 10000,
  },
};

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case TOGGLE_LOG_C2S:
      return {
        ...state,
        toggle: {
          ...state["toggle"],
          ...{ logC2S: !state["toggle"].logC2S },
        },
      };

    case TOGGLE_LOG_S2C:
      return {
        ...state,
        toggle: {
          ...state["toggle"],
          ...{ logS2C: !state["toggle"].logS2C },
        },
      };

    case TOGGLE_RESPONSE_BAR:
      return {
        ...state,
        toggle: {
          ...state["toggle"],
          ...{ showResponseBar: action.bool },
        },
      };

    case TOGGLE_MODEL_LIST_OPTIONS:
      return {
        ...state,
        toggle: {
          ...state["toggle"],
          ...{ showModelListOptions: !state["toggle"].showModelListOptions },
        },
      };

    case RUN_MC_BUTTON:
      return {
        ...state,
        toggle: {
          ...state["toggle"],
          ...{ runMC: action.bool },
        },
      };

    case TOGGLE_WIDE_EDITOR:
      return {
        ...state,
        toggle: {
          ...state["toggle"],
          ...{ wideEditor: action.bool },
        },
      };

    case EDIT_COMMANDLINE_RESPONSE:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ commandLineResponse: action.commandLineResponse },
        },
      };

    case EDIT_TAB_LABEL:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ labelAction: action.labelAction },
        },
      };

    case EDIT_TAB_CONTENT:
      return {
        ...state,
        contentAction: {
          label: action.contentAction.label,
          action: action.contentAction.action,
          expression: action.contentAction.expression,
          channel: action.contentAction.channel,
          coordinates: action.contentAction.coordinates,
        },
      };

    case EDIT_AREA_VALUE:
      return {
        ...state,
        assignment: {
          action: action.areaValueAction.action,
          label: action.areaValueAction.label,
          expression: action.areaValueAction.expression,
          channel: action.areaValueAction.channel,
          rowIndex: action.areaValueAction.rowIndex,
          columnIndex: action.areaValueAction.columnIndex,
          value: action.areaValueAction.value,
        },
      };

    case RESET_AREA_VALUE:
      return {
        ...state,
        assignment: {
          action: "",
          label: "",
          expression: "",
          channel: CHANNEL.NONE,
          rowIndex: 0,
          columnIndex: 0,
          value: "",
        },
      };

    case UPDATE_DEFAULT_DELAY:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ defaultDelay: action.seconds },
        },
      };

    case UPDATE_ZOOM_OUT_CHANNEL:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ zoomOutChannel: action.channel },
        },
      };

    case CURRENT_CONTEXT_MENU:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ currentContextMenu: action.channel },
        },
      };

    case EDITING_LTYPE:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ editingLtype: action.ltype },
        },
      };

    case EDITING_COORDS:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ editingCoords: action.coords },
        },
      };

    case UPDATE_MODEL_NAME:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ modelName: action.modelName },
        },
      };

    case EDIT_FORMULA_INPUT:
      return {
        ...state,
        info: {
          ...state["info"],
          ...{ formula: action.formula },
        },
      };

    case EDIT_BUCKETS:
      return {
        ...state,
        montecarlo: {
          ...state["montecarlo"],
          ...{ buckets: action.buckets },
        },
      };

    case EDIT_LOWERBOUND:
      return {
        ...state,
        montecarlo: {
          ...state["montecarlo"],
          ...{ lowerBound: action.lowerBound },
        },
      };

    case EDIT_UPPERBOUND:
      return {
        ...state,
        montecarlo: {
          ...state["montecarlo"],
          ...{ upperBound: action.upperBound },
        },
      };

    case EDIT_ITERATIONS:
      return {
        ...state,
        montecarlo: {
          ...state["montecarlo"],
          ...{ iterations: action.iterations },
        },
      };

    default:
      // console.error(`ui.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
