import React from "react";
import CHANNEL from "../constants/channel";

import TabLabelsContainer from "./TabLabelsContainer";
import TabContentContainer from "./TabContentContainer";

export default class TabPanel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      errorMessage: "",
      hideMessage: true,
      showGridBorders: false,
    };

    this.hideMessage = this.hideMessage.bind(this);
    this.showMessage = this.showMessage.bind(this);
    this.createView = this.createView.bind(this);
    this.resumeView = this.resumeView.bind(this);
    this.suspendView = this.suspendView.bind(this);
    this.removeView = this.removeView.bind(this);
  }

  // Hide and show messages

  hideMessage() {
    this.setState({ hideMessage: true });
  }

  showMessage(message) {
    this.setState({ errorMessage: message, hideMessage: false });
    setTimeout(this.hideMessage, 4000);
  }

  createView(viewName) {
    const { send } = this.props;
    const channel = viewName;
    send(
      "createView",
      `sys.view("${viewName}", "create", ${channel}, ?);`,
      CHANNEL.SYSTEM
    );
  }

  resumeView(viewName) {
    const { send } = this.props;

    send(
      "resumeView",
      `sys.view("${viewName}", "resume", ?, ?);`,
      CHANNEL.SYSTEM
    );

    send("resumeView", `sys.tick();`, CHANNEL.SYSTEM);
  }

  suspendView(viewName) {
    const { send } = this.props;

    send(
      "suspendView",
      `sys.view("${viewName}", "suspend", ?, ?);`,
      CHANNEL.SYSTEM
    );
  }

  removeView(viewName, removedExpr) {
    const { send } = this.props;

    send(
      "removeView",
      `sys.view("${viewName}", "remove", ?, ?);`,
      CHANNEL.SYSTEM
    );
  }

  render() {
    const {
      model,
      send,
      runMonteCarlo,
      addDefinition,
      changeExpressionInArea,
      createDefaultTab,
      handleSubscriptions,
      showGridBorders,
      setCreateMC,
      createMC,
      suppressUX,
    } = this.props;

    if (suppressUX) {
      return (
        <img
          src="4004.png"
          alt="ZenSheet is currently unavailable: our next launch is planned for July"
          className="center"
        />
      );
    }

    return (
      <div className={"tabs"}>
        <div
          className={
            this.state.hideMessage
              ? "messages-to-client messages-to-client-hidden"
              : "messages-to-client messages-to-client-displayed"
          }
        >
          {this.state.errorMessage}
        </div>

        <TabLabelsContainer
          createDefaultTab={createDefaultTab}
          createMC={createMC}
          setCreateMC={setCreateMC}
          createView={this.createView}
          removeView={this.removeView}
          resumeView={this.resumeView}
          showMessage={this.showMessage}
          suspendView={this.suspendView}
        />

        <TabContentContainer
          addDefinition={addDefinition}
          assign={this.props.assign}
          changeExpressionInArea={changeExpressionInArea}
          createDefaultTab={createDefaultTab}
          createView={this.createView}
          handleSubscriptions={handleSubscriptions}
          model={model}
          removeView={this.removeView}
          resumeView={this.resumeView}
          runMonteCarlo={runMonteCarlo}
          send={send}
          showGridBorders={showGridBorders}
          showMessage={this.showMessage}
          suspendView={this.suspendView}
        />
      </div>
    );
  }
}
