///
/// MonteCarloSetUp.js
///
/// Monte Carlo Parameters and Run Function called by TabContent.js component
///
/// LRQS 5
///

import React from "react";
import CHANNEL from "../constants/channel";

export default class MonteCarloSetUp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      running: false,
    };

    this._editBuckets = this._editBuckets.bind(this);
    this._editLowerBound = this._editLowerBound.bind(this);
    this._editUpperBound = this._editUpperBound.bind(this);
    this._editIterations = this._editIterations.bind(this);
    this._requestMonteCarlo = this._requestMonteCarlo.bind(this);
    this._runMonteCarlo = this._runMonteCarlo.bind(this);
  }

  checkIfParametersOK(selected) {
    const { showMessage, allTabsContent } = this.props;

    const activePanel = allTabsContent[selected];

    let counter = 0;
    let channel = null;

    for (const key of Object.keys(activePanel)) {
      const area = activePanel[key];
      if (area.expression !== "@MC") {
        const parsedValue = parseFloat(area.value);
        if (isNaN(parsedValue)) {
          showMessage(`expression ${area.expression} does not yield a number`);
          return CHANNEL.NONE;
        } else {
          ++counter;
          channel = area.channel;
        }
      }
    }

    if (!channel) {
      // assert counter === 0
      showMessage("Add an expression to the panel area to run Monte Carlo");
      return CHANNEL.NONE;
    } else if (counter > 1) {
      showMessage("Only one expression is currently allowed in MC simulations");
      return CHANNEL.NONE;
    }

    // assert counter === 1 && channel !== null
    return channel;
  }

  _runMonteCarlo(send, channel, N) {
    // !! ad-hoc delay heuristic while we wait for proper asynch handling
    // send requests in chunks of K iterations
    let K = 100;

    let total = 0;
    const run = () => {
      send("_requestMonteCarlo", `mc.run("${channel}", ${K}, ${K})`, channel);
      total += K;
      if (total < N) {
        K = Math.floor(1.2 * K);
        K = K < 1000 ? K : 1000;
        setTimeout(run, K / 2);
      } else {
        // We are done!
        this.setState({ running: false });
      }
    };
    // Let's go!
    setTimeout(run, K);
    this.setState({ running: true });
  }

  _requestMonteCarlo() {
    const {
      buckets,
      iterations,
      lowerBound,
      upperBound,
      selected,
      send,
      runMonteCarlo,
    } = this.props;

    runMonteCarlo({ buckets, iterations, lowerBound, upperBound });
    const channel = this.checkIfParametersOK(selected);
    if (channel === CHANNEL.NONE) return;

    // Monte Carlo graph parameters
    let nb = parseInt(buckets);
    let lb = lowerBound;
    let ub = upperBound;

    // Monte Carlo run parameters
    let N = parseInt(iterations); // desired number of iterations

    // send MC configuration
    send(
      "_requestMonteCarlo",
      `mc.params("${channel}", ${nb}, ${lb}, ${ub})`,
      channel
    );

    this._runMonteCarlo(send, channel, N);
  }

  _editBuckets() {
    this.props.uiActions.editBuckets(this.refs.buckets.value);
  }

  _editLowerBound() {
    this.props.uiActions.editLowerBound(this.refs.lowerBound.value);
  }

  _editUpperBound() {
    this.props.uiActions.editUpperBound(this.refs.upperBound.value);
  }

  _editIterations() {
    this.props.uiActions.editIterations(this.refs.iterations.value);
  }

  render() {
    const { buckets, iterations, lowerBound, upperBound } = this.props;

    return (
      <div className="MC-setup-wrapper">
        <div className="MC-setup">
          <label htmlFor="buckets">Num buckets</label>
          <input
            type="number"
            ref="buckets"
            defaultValue={buckets}
            onChange={this._editBuckets}
          />
        </div>

        <div className="MC-setup">
          <label htmlFor="lowerBound">Lower bound</label>
          <input
            type="number"
            ref="lowerBound"
            defaultValue={lowerBound}
            onChange={this._editLowerBound}
          />
        </div>

        <div className="MC-setup">
          <label htmlFor="upperBound">Upper bound</label>
          <input
            type="number"
            ref="upperBound"
            defaultValue={upperBound}
            onChange={this._editUpperBound}
          />
        </div>

        <div className="MC-setup">
          <label htmlFor="iterations">Number of runs</label>
          <input
            type="number"
            ref="iterations"
            min="1"
            defaultValue={iterations}
            onChange={this._editIterations}
          />
        </div>

        <button
          disabled={this.state.running}
          onClick={this._requestMonteCarlo}
          className={this.state.running ? "mc-button-disabled" : ""}
        >
          Run
        </button>
      </div>
    );
  }
}
