///
/// interpretValue.js
///
/// Auxiliary function used in Main.js
///
/// OK Millennial: sorry,  but this code is ... a perversion of sound engineering
/// CFL languages can't be handled with regex rules in general
///
/// In particular, check the messy ad-hoc code in interpretValue
/// But the entire thing is screaming "replace this with something sound!"
///
/// Finally working on this to get out of the ad-hoc string based rules.
/// Warning: it's going to be a long road involving more than this file.
///
/// LRQS 2 - needs more review
///

import logger from "../../logger/logger";
import parse from "zll/Parser";

const sheiBeMode = false;

/**
 * @description
 * To modify and return the cell value given by the ast:
 * adding quotation marks to indicate if the expression is a string
 * or removing the header id from the cell
 *
 * @param {object} e: ast structure of the cell
 * @returns string containing the value of the expression
 */
function _get_cell_value(e) {
  logger.debug("_get_cell_value");
  let cell = e.regen();
  logger.debug("cell: " + cell);
  let regexp = /(struct {([A-Z]|[a-z]|[0-9]|_| |;)+})+/;
  let headStr = cell.match(regexp);

  if (headStr != null) {
    cell = cell.replace(headStr[0], "");
  }

  if (e.tree().startsWith("String")) {
    cell = '"' + cell + '"';
  }

  logger.debug("45 _get_cell_value => " + cell);
  return cell;
}

function _build_aux_value(ast) {
  logger.debug("_build_aux_value");
  logger.debug("ast: " + ast.regen());

  let val = [];
  let v = "[";
  let oneDimArray = ast.args.elems.map((e) => {
    return e.args === undefined;
  });
  let isJaggedArray = !oneDimArray.includes(true);

  for (let e of ast.args.elems) {
    if (isJaggedArray) {
      v += "[";
      val.push(
        e.args.elems.map((e) => {
          const result = _get_cell_value(e);
          logger.debug("_build_aux_value => " + result);
          return result;
        })
      );
      v += val.join(",");
      v += "],";
      val = [];
    } else {
      v += _get_cell_value(e);
      v += ",";
    }
  }

  if (v.slice(-1) === ",") {
    v = v.substring(0, v.length - 1);
  }

  v += "]";

  logger.debug("85 _build_aux_value => " + v);
  return v;
}

function _build(ast, s, count) {
  logger.debug("_build");
  logger.debug("ast: " + ast.regen());
  logger.debug("s: " + s);
  logger.debug("count: " + count);

  const args = ast.args.elems;

  for (let i in args) {
    if (args[i].value) {
      let type = args[i].tree();
      s +=
        type.indexOf("String") !== -1
          ? '"' + String(args[i].value) + '"'
          : String(args[i].value);
      s += i === args.length - 1 ? ")," : ",";
    } else if (!args[i].args) {
      s += args[i].regen();
      s += i === args.length - 1 ? ")," : ",";
    } else {
      if (args[i].fun) {
        if (!args[i].fun.name) {
          s += "(";
        } else {
          s += args[i].fun.name + "(";
        }
      } else if (!args[i].fun) {
        s += "[";
      } else {
        s += "'missing rule'";
      }

      s = _build(args[i], s, count + 1);
      if (args[i].fun) {
        s += "*";
      } else if (!args[i].fun) {
        s += "],";
      }
    }
  }

  if (count === 0) {
    let closing = s.search(/,\*\*/);
    if (closing) {
      s = s.replace(/,\*\*/g, "),");
    }

    let commas = s.search(/,\*/);
    if (commas !== -1) {
      s = s.replace(/,\*/g, ",");
    }

    if (s.slice(-1) === ",") {
      s = s.slice(0, -1);
    }
  }

  logger.debug("146 _build => " + s);
  return s;
}

function _valueWithoutHeaders(ast, value) {
  logger.debug("_valueWithoutHeaders");
  logger.debug("ast: " + ast.regen());
  logger.debug("value: " + value);

  if (ast.nd) {
    logger.debug("156 _valueWithoutHeaders => " + value);
    return value;
  }

  if (ast.elems) {
    // Data Type
    logger.debug("162 _valueWithoutHeaders => " + value);
    return value;
  }

  // not generally correct: ad-hoc patch to fix negative numbers
  if (ast.is_unary_minus) {
    logger.debug("168 _valueWithoutHeaders => " + value);
    return value;
  }

  let newValue = "";

  if (ast.fun) {
    newValue += "(";
  }

  newValue += _build(ast, "", 0);

  if (value.startsWith("[")) {
    newValue = "[" + newValue + "]";
  }

  if (ast.fun) {
    newValue += ")";
  }

  logger.debug("188 _valueWithoutHeaders => " + newValue);
  return newValue;
}

/**
 * @description
 * Function created to remove duplicated headers in struct values
 *
 * Perversion: check the messy ad-hoc code to deal with structs
 *
 * @param {String} value
 * @returns string containing the value of the expression
 */
function sheiBeStyle(value) {
  logger.debug("sheiBeStyle: " + value);
  const withStructs = value.indexOf("struct {") !== -1;
  if (!withStructs || value.startsWith("/.") || value.startsWith("fn")) {
    logger.debug("sheiBeStyle => " + value);
    return value;
  } else {
    // Interpreting structs (tuples not included)
    const ast1 = parse(value, "sheiBeStyle::sheiBeStyle ast1");
    if (ast1.is_type()) {
      logger.debug("sheiBeStyle => " + value);
      return value;
    }

    let val = _valueWithoutHeaders(ast1, value);

    const ast2 = parse(val, "sheiBeStyle::sheiBeStyle ast2");
    if (ast2.is_error()) {
      val = _build_aux_value(ast1);
    }

    logger.debug("sheiBeStyle => " + val);
    return val;
  }
}

function serialize(ast) {
  if (ast.is_type()) {
    logger.debug("" + ast.regen() + " is a type!");
    return ast.regen();
  }

  if (ast.is_list()) {
    logger.debug("" + ast.regen(",") + " is a list!");
    let first = true;
    let result = "";
    for (let arg of ast.elems) {
      if (first) {
        result += serialize(arg);
        first = false;
      } else {
        result += "," + serialize(arg);
      }
    }
    return result;
  }

  if (ast.is_enclosed_list()) {
    logger.debug("" + ast.regen() + " is an enclosed list!");
    const result = "[" + serialize(ast.args) + "]";
    return result;
  }

  if (ast.is_call()) {
    logger.debug("" + ast.regen() + " is a function call!");
    const fun = ast.fun;
    if (!fun) {
      // Aguanta marrano! This is the path for sugared operators
      return ast.regen();
    }
    if (fun.is_type()) {
      logger.debug("" + ast.regen() + " is a constructor!");
      const result = "[" + serialize(ast.args) + "]";
      logger.debug("result: " + result);
      return result;
    }
    return ast.regen();
  }

  const value = ast.regen();
  return sheiBeMode ? sheiBeStyle(value) : value;
}

export default function interpretValue(value) {
  logger.debug("interpretValue: " + value);
  if (sheiBeMode) {
    return sheiBeStyle(value);
  }
  const ast = parse(value, "281 interpretValue::interpretValue");
  return serialize(ast);
}
