///
/// model.js
///
/// LRQS 5
///
/// Symbol state values: Failed ("F"), Pending ("P"), Validated ("V")
///

// import logger from "../logger/logger";

// Action types

const ADD_SYMBOL = "ADD_SYMBOL";
const UPDATE_SYMBOL_STATE = "UPDATE_SYMBOL_STATE";
const UPDATE_SYMBOL_DECLARED_TYPE = "UPDATE_SYMBOL_DECLARED_TYPE";
const UPDATE_SYMBOL_INFERRED_TYPE = "UPDATE_SYMBOL_INFERRED_TYPE";
const DELETE_SYMBOL = "DELETE_SYMBOL";

// Action constructors

export function addSymbol(symbol, state) {
  return { type: ADD_SYMBOL, symbol, state };
}

export function updateSymbolState(symbol, state) {
  return { type: UPDATE_SYMBOL_STATE, symbol, state };
}

export function updateSymbolDeclaredType(symbol, declared_type) {
  return { type: UPDATE_SYMBOL_DECLARED_TYPE, symbol, declared_type };
}

export function updateSymbolInferredType(symbol, inferred_type) {
  return { type: UPDATE_SYMBOL_INFERRED_TYPE, symbol, inferred_type };
}

export function deleteSymbol(symbol) {
  return { type: DELETE_SYMBOL, symbol };
}

// Reducer

function reducer(state = {}, action) {
  switch (action.type) {
    case ADD_SYMBOL: {
      const result = {
        ...state,
        [action.symbol]: {
          symbol: action.symbol,
          state: action.state,
          // type: undefined,
          declared_type: undefined,
          inferred_type: undefined,
        },
      };
      return result;
    }

    case UPDATE_SYMBOL_STATE: {
      return {
        ...state,
        [action.symbol]: {
          ...state[action.symbol],
          ...action.state,
        },
      };
    }

    case UPDATE_SYMBOL_DECLARED_TYPE: {
      return {
        ...state,
        [action.symbol]: {
          ...state[action.symbol],
          ...action.declared_type,
        },
      };
    }

    case UPDATE_SYMBOL_INFERRED_TYPE: {
      // horrible, but that's the nature of the structure we inherited
      // const type = action.inferred_type.inferred_type;
      return {
        ...state,
        [action.symbol]: {
          ...state[action.symbol],
          ...action.inferred_type,
          // type,
        },
      };
    }

    case DELETE_SYMBOL: {
      const result = { ...state };
      delete result[action.symbol];
      return result;
    }

    default:
      // console.error(`model.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
