import React from "react";

function toArray(obj) {
  let array = [];
  // iterate backwards ensuring that length is a UInt32
  for (let i = obj.length >>> 0; i--; ) {
    array[i] = obj[i];
  }
  return array;
}

class Droppable extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      over: false,
    };
  }

  componentDidMount() {
    // TODO: Listen for window resize?
    let node = this.refs.droppable;
    this.position = {
      top: node.offsetTop + 5,
      left: node.offsetLeft + 5,
      right: node.offsetLeft + node.offsetWidth - 5,
      bottom: node.offsetTop + node.offsetHeight - 5,
    };
  }

  filterProps() {
    let forbidden = ["types"];
    return Object.keys(this.props).reduce((p, c) => {
      if (!forbidden.includes(c)) {
        p[c] = this.props[c];
      }
      return p;
    }, {});
  }

  onDragOver(e) {
    e.preventDefault();
    if (!this.allowed(e.dataTransfer.types)) return;
    if (typeof this.props.onDragOver === "function") this.props.onDragOver(e);
  }

  onDragEnter(e) {
    e.preventDefault();
    if (this.state.over) return;
    if (!this.allowed(e.dataTransfer.types)) return;
    if (typeof this.props.onDragEnter === "function") this.props.onDragEnter(e);
    this.setState({ over: true });
  }

  onDragLeave(e) {
    e.preventDefault();
    if (!this.allowed(e.dataTransfer.types)) return;
    let over = true;
    if (e.clientX <= this.position.left || e.clientX >= this.position.right)
      over = false;
    if (e.clientY <= this.position.top || e.clientY >= this.position.bottom)
      over = false;
    if (over) return;
    this.setState({ over: false });
    if (typeof this.props.onDragLeave === "function") this.props.onDragLeave(e);
  }

  onDrop(e) {
    e.preventDefault();
    if (!this.allowed(e.dataTransfer.types)) return;
    this.setState({ over: false });
    let data = !this.props.types
      ? null
      : [].concat(this.props.types).reduce((d, type) => {
          d[type] = e.dataTransfer.getData(type);
          return d;
        }, {});
    if (typeof this.props.onDrop === "function") this.props.onDrop(data, e);
  }

  allowed(attemptingTypes) {
    const _attemptingTypes = toArray(attemptingTypes);
    if (!this.props.types) return true;
    return [].concat(this.props.types).reduce((sum, type) => {
      if (_attemptingTypes.indexOf(type) >= 0) return true;
      return sum;
    }, false);
  }

  render() {
    let classes = "Droppable";
    if (this.state.over) classes += " over";
    return (
      <div
        ref="droppable"
        className={classes}
        {...this.filterProps()}
        onDrop={this.onDrop.bind(this)}
        onDragOver={this.onDragOver.bind(this)}
        onDragEnter={this.onDragEnter.bind(this)}
        onDragLeave={this.onDragLeave.bind(this)}
        onDragExit={this.onDragLeave.bind(this)}
      >
        {this.props.children}
      </div>
    );
  }
}

export default Droppable;
