///
/// getStore.js: Redux root reducer
///
/// LRQS 5
///

import { combineReducers } from "redux";
import { createStore } from "redux";

import areaLabel from "./areaLabel";
import dragAndDrop from "./dragAndDrop";
import editing from "./editing";
import model from "./model";
import modelDetails from "./modelDetails";
import serverMessage from "./serverMessage";
import tabs from "./tabs";
import tabContentCollection from "./tabContentCollection";
import tabSelected from "./tabSelected";
import ui from "./ui";

// Load redux development extension, if available
const REDUX_DEV_EXTENSION = window.__REDUX_DEVTOOLS_EXTENSION__;
const reduxDevExtension = REDUX_DEV_EXTENSION && REDUX_DEV_EXTENSION();

// Create root reducer
const root = combineReducers({
  areaLabel,
  dragAndDrop,
  editing,
  model,
  modelDetails,
  serverMessage,
  tabs,
  tabContentCollection,
  tabSelected,
  ui,
});

// Define store
let store = null;

// Get store as defined by the root reducer
function getStore() {
  if (store === null) {
    store = createStore(root, reduxDevExtension);
  }
  return store;
}

export default getStore;
