import React, { Component } from "react";
import classnames from "classnames";
import Droppable from "./Droppable";

export default class DroppableGrid extends Component {
  constructor(props) {
    super(props);
    this.state = { draggedOver: "" };
  }

  _highlightDragged(elem) {
    if (this.state.draggedOver !== elem) {
      this.setState({ draggedOver: elem });
    }
  }

  render() {
    const { width, height, dragStatus, gridCols, gridRows } = this.props;

    const a = Array(gridCols)
      .fill(0)
      .map((_, y) => y); // y is the index

    let coords = [];
    for (let i = 0; i < gridRows; ++i) {
      coords = coords.concat(a.map(x => i + "-" + x));
    }

    return (
      <Droppable
        types={["drag", "definition", "expression"]}
        onDrop={this.props.onDrop}
        style={{ zIndex: dragStatus ? "0" : "-1" }}
      >
        {coords.map((elem, index) => {
          return (
            <div
              key={index}
              style={{ width, height }}
              onDragOver={() => this._highlightDragged(elem)}
              className={classnames(
                dragStatus && this.state.draggedOver === elem
                  ? "dropzone-active"
                  : "dropzone-inactive",
                "drop-child",
                this.props.showGridBorders ? "" : "hide"
              )}
            />
          );
        })}
      </Droppable>
    );
  }
}

