import React from "react";

import classnames from "classnames";
import ASCII from "../constants/ascii.js";
import CHANNEL from "../constants/channel";
import UI from "../constants/UI";

import grid from "./extra/gridx";
import ByRows from "./ByRows";
import ByColumns from "./ByColumns";
import TextInput from "./TextInput";

import logger from "../logger/logger";
import elems from "zll/elems";
import parse from "zll/Parser";

const STATUS_LOADING = 1;
const STATUS_LOADED = 2;

const CR = ASCII.CR;
const ESC = ASCII.ESC;

class Grid extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      loadedRowsMap: {},
      dataLayer: {},
      rows: 0,
      columns: 1,
      gridWidth: 0, // !! check this: did not observe behavior change
      gridHeight: UI.ROW_HEIGHT, // !! check this: did not observe behavior change
      orientation: "vertical",
      columnsWidth: [],
      isEditInputEnabled: false,
      text: "",
      monitored: [],
      visibleRowNumber: 24,
      contextMenuCoords: [],
      editingMode: this.props.startEdition,
    };

    this._cellRenderer = this._cellRenderer.bind(this);
    this._finishEditing = this._finishEditing.bind(this);
    this._getColumnWidth = this._getColumnWidth.bind(this);
    this._isRowLoaded = this._isRowLoaded.bind(this);
    this._loadMoreRows = this._loadMoreRows.bind(this);
    this._setNewListBounds = this._setNewListBounds.bind(this);
    this._updateCellValue = this._updateCellValue.bind(this);
    this._onEditGesture = this._onEditGesture.bind(this);
    this.onZoomIn = this.onZoomIn.bind(this);

    this.resetContextMenuCoords = this.resetContextMenuCoords.bind(this);
  }

  _focusActiveCell(cellID) {
    if (cellID !== "") {
      const cellDOM = document.getElementById(cellID);
      if (cellDOM !== null) {
        cellDOM.focus();
      }
    }
  }

  /****************************************************************/
  /*********************** React hooks ****************************/
  /****************************************************************/

  componentDidMount() {
    this._initialSetUp();
    this._setDOMgridWidth();
    this._focusActiveCell(this.props.editingCoords);
  }

  // !! removing it causes "max update depth" crash
  shouldComponentUpdate(nextProps, nextState) {
    // nextProps section
    if (
      nextProps.assignment !== this.props.assignment ||
      nextProps.data !== this.props.data ||
      nextProps.editingRowNumber !== this.props.editingRowNumber ||
      nextProps.editingColumnNumber !== this.props.editingColumnNumber ||
      nextProps.formula !== this.props.formula ||
      nextProps.formulaInput !== this.props.formulaInput ||
      nextProps.lowRow !== this.props.lowRow ||
      nextProps.orientation !== this.props.orientation ||
      nextProps.showIndexes !== this.props.showIndexes ||
      nextProps.startEdition !== this.props.startEdition ||
      nextProps.type !== this.props.type ||
      nextProps.uppRow !== this.props.uppRow ||
      nextProps.usesDataLayer !== this.props.usesDataLayer ||
      nextProps.wideEditor !== this.props.wideEditor
    ) {
      return true;
    }

    // nextState section
    if (
      nextState.columns !== this.state.columns ||
      nextState.contextMenuCoords !== this.state.contextMenuCoords ||
      nextState.dataLayer !== this.state.dataLayer ||
      nextState.editingMode !== this.state.editingMode ||
      nextState.gridWidth !== this.state.gridWidth ||
      nextState.rows !== this.state.rows ||
      nextState.text !== this.state.text
    ) {
      return true;
    }

    // All commented improve performance
    // All of the if blocks from here to the end originally returned false
    // Then why bother? Loks like Monica wanted to return true, but ... couldn't
    // Testing confirmed that's the case: "Maximum update depth exceeded"
    // This clearly needs some redesign from first principles

    /*
    if (
      nextProps.channel !== this.props.channel ||
      nextProps.editAreaValue !== this.props.editAreaValue ||
      nextProps.expression !== this.props.expression ||
      nextProps.finishEditingStatus !== this.props.finishEditingStatus ||
      nextProps.gridName !== this.props.gridName ||
      nextProps.headerLabels !== this.props.headerLabels ||
      nextProps.lazyArray !== this.props.lazyArray ||
      nextProps.minimumBatchSize !== this.props.minimumBatchSize ||
      nextProps.modelDetailsActions !== this.props.modelDetailsActions ||
      nextProps.rowCount !== this.props.rowCount ||
      nextProps.send !== this.props.send ||
      nextProps.type !== this.props.type ||
      nextProps.usesDataLayer !== this.props.usesDataLayer ||
      nextProps.zoomIn !== this.props.zoomIn
    ) {
      // Not yest independently tested
      return true;
    }
    */

    /*
    if (
      nextState.columnsWidth !== this.state.columnsWidth ||
      nextState.gridHeight !== this.state.gridHeight ||
      nextState.loadedRowsMap !== this.state.loadedRowsMap ||
      nextState.orientation !== this.state.orientation ||
      nextState.isEditInputEnabled !== this.state.isEditInputEnabled // !!
    ) {
      // Independently tested => Error: Maximum update depth exceeded.
      return true;
    }
    */

    return false;
  }

  componentDidUpdate(prevProps, prevState) {
    const {
      assignment,
      data,
      editAreaValue,
      editingCoords,
      editingColumnNumber,
      editingRowNumber,
      formula,
      gridName,
      lowRow,
      minimumBatchSize,
      orientation,
      rowCount,
      uppRow,
      usesDataLayer,
      wideEditor,
    } = this.props;

    const { isEditInputEnabled } = this.state;

    if (!wideEditor && wideEditor !== prevProps.wideEditor) {
      this._focusActiveCell(editingCoords);
    }

    let ast = parse(data, "Grid::componentDidUpdate 185");
    let initialData = elems("componentDidUpdate", ast);

    // !! brute force patch
    if (!initialData) {
      return;
    }

    let columnCount = grid.getColumnCount(initialData);

    // AUTOMATIZATION
    if (
      assignment !== prevProps.assignment &&
      assignment.channel === gridName
    ) {
      let action = assignment.action;
      let row = assignment.rowIndex;
      let column = assignment.columnIndex;
      let value = assignment.value;

      if (action === "select") {
        this._onEditGesture(row, column);
      } else if (action === "edit") {
        this._changeInputValue(value);
      } else if (action === "commit") {
        this._updateCellValue(value, row, column);
        editAreaValue({
          action: "",
          label: "",
          expression: "",
          channel: CHANNEL.NONE,
          rowIndex: -1,
          columnIndex: -1,
          value: "",
        });
      }
    }

    // Show input with formula
    if (formula && editingRowNumber !== -1 && editingColumnNumber !== -1) {
      // let inputID = gridName;
      // let input = document.getElementById(inputID);
      if (!isEditInputEnabled) {
        this.setState({ isEditInputEnabled: true });
        this.props.editFormulaInput(formula);
      }
    }

    if (orientation !== this.state.orientation) {
      this._initialSetUp();
      this._setDOMgridWidth();
      return;
    }

    if (this.state.orientation === "horizontal") {
      let subGrid = grid.get_subgrid(
        data,
        orientation,
        lowRow,
        uppRow,
        usesDataLayer
      );
      let rows = grid.getCoord("row", orientation, uppRow + 1, columnCount);
      let columns = grid.getCoord(
        "column",
        orientation,
        uppRow + 1,
        columnCount
      );

      this._setNewListBounds(0, rows - 1, 0, columns, subGrid, 0);

      if (rowCount !== this.state.rows) return;
      if (this.props.columnCount !== this.state.columns) return;
    }

    const subGrid = grid.get_subgrid(
      data,
      orientation,
      lowRow,
      uppRow,
      usesDataLayer
    );

    logger.debug(">>>", data);

    if (this.state.orientation === "vertical") {
      if (!usesDataLayer) {
        let columnsWidth = grid.get_columns_width(data);
        logger.debug("265 - columnsWidth", columnsWidth);
        let gridWidth = grid.set_grid_width(
          orientation,
          columnsWidth,
          uppRow + 1,
          minimumBatchSize,
          undefined // !! definitely not OK to pass the gridName here (unlike the other call)
        );
        this._setDOMgridWidth();
        this.setState({
          rows: initialData.length,
          columns: columnCount,
          columnsWidth: columnsWidth,
          gridHeight: grid.get_grid_height(
            initialData.length,
            this.state.visibleRowNumber
          ),
          gridWidth: gridWidth,
        });

        this._editDataLayer(
          0,
          initialData.length - 1,
          0,
          this.state.columns,
          subGrid
        );
      } else {
        if (prevProps.lowRow !== lowRow) {
          this._setNewListBounds(
            lowRow,
            uppRow,
            0,
            this.state.columns,
            subGrid,
            prevProps.lowRow
          );
        } else {
          this._editDataLayer(lowRow, uppRow, 0, this.state.columns, subGrid);
        }
      }
    }
  }

  /*******************************************************************/
  /*********************** internal functions ************************/
  /*******************************************************************/

  _setDOMgridWidth() {
    const { gridName, orientation } = this.props;

    let grid = document.getElementById(gridName + "-grid-" + orientation);
    grid.style.width =
      this.state.gridWidth + UI.ROW_INDEX_WIDTH + UI.FA_MARGIN + "px";
  }

  _initialSetUp() {
    const {
      channel,
      data,
      expression,
      gridName,
      lowRow,
      minimumBatchSize,
      orientation,
      rowCount,
      send,
      // type,
      uppRow,
      usesDataLayer,
    } = this.props;

    let ast = parse(data, "Grid::_initialSetUp 348");
    let initialData = elems("_initialSetUp", ast);

    let columnCount = grid.getColumnCount(initialData);

    let subGrid = grid.get_subgrid(
      data,
      orientation,
      lowRow,
      uppRow,
      usesDataLayer
    );

    let rows = grid.getCoord("row", orientation, uppRow + 1, columnCount);
    let columns = grid.getCoord("column", orientation, uppRow + 1, columnCount);

    let gridHeight = grid.get_grid_height(rows, this.state.visibleRowNumber);

    let columnsWidth = grid.get_columns_width(data);
    logger.debug("365 columnsWidth", columnsWidth);
    let gridWidth = grid.set_grid_width(
      orientation,
      columnsWidth,
      uppRow + 1,
      minimumBatchSize,
      gridName // !! apparently OK to pass the gridName here (unlike the other call)
    );

    this.setState({
      rows: orientation === "vertical" ? rowCount : columnCount,
      columns: orientation === "vertical" ? columnCount : rowCount,
      gridWidth: gridWidth,
      gridHeight: gridHeight,
      orientation: orientation,
      columnsWidth: columnsWidth,
    });

    if (usesDataLayer && expression.indexOf("[") === -1) {
      send("_initialSetUp", `${expression}'`, CHANNEL.SYSTEM);
      send(
        "_initialSetUp",
        `sys.view("${channel}", "update", [], ['${expression}']);`,
        CHANNEL.SYSTEM
      );
    }
    this._setNewListBounds(lowRow, uppRow, 0, columns, subGrid, lowRow);
  }

  _editDataLayer(startIndex, stopIndex, startColIndex, stopColIndex, subGrid) {
    const { orientation } = this.props;
    const { dataLayer, loadedRowsMap } = this.state;

    // Add the index of each elem that will be added to the data layer
    for (let i = startIndex; i <= stopIndex; i++) {
      loadedRowsMap[i] = STATUS_LOADED;
      if (!dataLayer[i]) {
        dataLayer[i] = [];
      }
    }

    let tempDataLayer = grid.update_data_layer(
      dataLayer,
      startIndex,
      stopIndex,
      startColIndex,
      stopColIndex,
      subGrid,
      orientation
    );

    for (let i = startIndex; i <= stopIndex; ++i) {
      for (let j = startColIndex; j < stopColIndex; ++j) {
        dataLayer[i][j] = tempDataLayer[i][j];
      }
    }
    // logger.debug("....", subGrid, dataLayer)
  }

  _setNewListBounds(
    startIndex,
    stopIndex,
    startColIndex,
    stopColIndex,
    subGrid,
    prevLowRow
  ) {
    const {
      // gridName,
      minimumBatchSize,
      orientation,
    } = this.props;

    const { dataLayer, loadedRowsMap } = this.state;

    if (orientation !== this.state.orientation) {
      for (let i = 0; i < stopColIndex; ++i) {
        delete loadedRowsMap[i];
        delete dataLayer[i];
      }
    }

    const indexes = grid.get_invisible_indexes(
      prevLowRow,
      startIndex,
      stopIndex,
      minimumBatchSize,
      dataLayer
    );

    for (let i of indexes) {
      delete loadedRowsMap[i];
      delete dataLayer[i];
    }

    this._editDataLayer(
      startIndex,
      stopIndex,
      startColIndex,
      stopColIndex,
      subGrid
    );

    logger.debug("loadmore: ", startIndex, stopIndex);
    logger.debug("Buffer: ", dataLayer);
  }

  _finishEditing(input) {
    const {
      channel,
      // expression,
      gridName,
      editingColumnNumber,
      editingRowNumber,
      finishEditingStatus,
      modelDetailsActions,
      send,
    } = this.props;

    finishEditingStatus();
    // resume updates
    send("_finishEditing", `sys.view("${channel}", "resume", ?, ?)`, channel);

    modelDetailsActions.editElemDetails(channel, { editingRowNumber: -1 });
    modelDetailsActions.editElemDetails(channel, { editingColumnNumber: -1 });
    if (input !== null) {
      input.blur();
    }

    const cellID =
      gridName + "-" + editingRowNumber + "-" + editingColumnNumber;
    this._focusActiveCell(cellID);

    // this.state.activeElement.focus();
  }

  /* When the user stops editing a cell, it returns to its 'show value mode' */
  _onBlur(inputID) {
    let input = document.getElementById(inputID);
    this._finishEditing(input);
  }

  _onKeyUp(text, row, column, inputID, event) {
    if (event.keyCode === CR) {
      this._updateCellValue(text, row, column);
    } else if (event.keyCode === ESC) {
      this._onBlur(inputID);
    }
  }

  _onChange(e) {
    // this.setState({ text: e.target.value });
    this.props.editFormulaInput(e.target.value);
  }

  _changeInputValue(value) {
    // this.setState({ text: value });
    this.props.editFormulaInput(value);
  }

  _onMouseOver(row, column, event) {
    const { gridName, orientation } = this.props;
    let grid = document.getElementById(gridName + "-grid-" + orientation);
    grid.setAttribute("title", "[" + row + ", " + column + "]");
  }

  _getCellIndex(row, column) {
    const { orientation, type } = this.props;
    const { columns } = this.state;
    return grid.get_cell_index(row, column, orientation, type, columns); // cell index. i.e., [0]
  }

  onZoomIn(row, column) {
    const { expression, gridName, type, zoomIn } = this.props;
    // const { dataLayer } = this.state;

    let cell = this._getCellIndex(row, column);

    cell =
      type === "multi-dim-grid"
        ? cell // zoom-in to the specific cell the user clicked
        : `[${row}]`; // zoom-in to the row

    const newExpression = expression + cell;

    zoomIn(expression, newExpression, gridName);

    this._resetDataLayer();
  }

  // start editing mode
  _onEditGesture(row, column, event) {
    const {
      channel,
      expression,
      gridName,
      lazyArray,
      modelDetailsActions,
      orientation,
      requestFormulaToUpdate,
      send,
      toggleWideEditor,
      type,
      updateEditingCoords,
    } = this.props;

    logger.debug(`event ${event}`);
    logger.debug(`type ${type}`);
    logger.debug(`expression ${expression}`);

    // !! This is a patch "fix" to issue #62
    // !! It shows that returning prevents the anomaly
    // !! Proper solution is to return if 'expression' does not denote an lvalue
    if (type === "plain") {
      return;
    }

    const { columns, dataLayer } = this.state;

    // !! check code in grid.js: canDrill looks wrong!
    const canDrill = grid.cell_is_composite(row, column, dataLayer);
    const zoomable = canDrill && type !== "0-grid" && type !== "1-grid";
    if (zoomable) {
      // if cell is zoomable, edit mode is not entered
      return;
    }

    toggleWideEditor(false);

    const cellID = gridName + "-" + row + "-" + column;
    updateEditingCoords(cellID);
    modelDetailsActions.editElemDetails(channel, { editingRowNumber: row });
    modelDetailsActions.editElemDetails(channel, {
      editingColumnNumber: column,
    });
    this.setState({ isEditInputEnabled: false });

    if (lazyArray) {
      this.setState({ isEditing: true });
      requestFormulaToUpdate(expression, channel);
      // suspend asynchronous updates
      send(
        "_onEditGesture",
        `sys.view("${channel}", "suspend", ?, ?);`,
        channel
      );
    } else {
      // cell index. i.e., [0]
      const cell = grid.get_cell_index(row, column, orientation, type, columns);
      requestFormulaToUpdate(expression + cell, channel);
    }
  }

  _resetDataLayer() {
    const { columns, dataLayer, rows } = this.state;

    for (let i = 0; i < rows; ++i) {
      for (let j = 0; j < columns; ++j) {
        dataLayer[i][j] = [];
      }
    }
  }

  // Send a request to the server to update the edited cell of the grid
  _updateCellValue(text, row, column) {
    const {
      channel,
      expression,
      gridName,
      orientation,
      assign,
      type,
    } = this.props;

    const { columns } = this.state;

    let inputID = String(gridName);
    let input = document.getElementById(inputID);
    let cell = grid.get_cell_index(row, column, orientation, type, columns);

    let lhs = !this.props.lazyArray ? `${expression + cell}` : expression;
    let rhs = text;

    assign(channel, lhs, rhs);

    this._finishEditing(input);
  }

  // Base case: send(`${gridName}[${startIndex} .. ${stopIndex + minimumBatchSize}]`); prevRowStart < startIndex
  _requestGridSubset(channel, min, max, prevRowStart, prevRowStop) {
    const { expression, send } = this.props;

    if (expression.indexOf("[") !== -1) {
      // !! is_array_index()
      return;
    }

    const old_gridSubset = `${expression}[${prevRowStart} .. ${prevRowStop}]`;
    const new_gridSubset = `${expression}[${min} .. ${max}]`;

    // Request snapshot
    send("_requestGridSubset", new_gridSubset, channel);

    // Update view
    let monitoredCopy = this.state.monitored;
    for (let elem of monitoredCopy) {
      logger.debug(`elem: ${elem}`);
      let subset = monitoredCopy.pop();
      send(
        "_requestGridSubset",
        `sys.view("${channel}", "update", ['${subset}'], []);`,
        CHANNEL.SYSTEM
      );
    }

    monitoredCopy.push(new_gridSubset);
    this.setState({ monitored: monitoredCopy });

    send(
      "_requestGridSubset",
      `sys.view("${channel}", "update", ['${old_gridSubset}'], ['${new_gridSubset}']);`,
      CHANNEL.SYSTEM
    );

    /*
    send(
      "_requestGridSubset",
      `sys.view("${channel}", "update", ['${old_gridSubset}'], []);`,
      CHANNEL.SYSTEM
    );
    
    send(
      "_requestGridSubset",
      `sys.view("${channel}", "update", [], ['${new_gridSubset}']);`,
      CHANNEL.SYSTEM
    );
    */
  }

  /*******************************************************************************/
  /***************** react-virtualized predefined functions **********************/
  /*******************************************************************************/

  _isRowLoaded({ index }) {
    const { loadedRowsMap } = this.state;
    return !!loadedRowsMap[index]; // STATUS_LOADING or STATUS_LOADED
  }

  _loadMoreRows({ startIndex, stopIndex }) {
    const { gridName, lowRow, minimumBatchSize, uppRow } = this.props;

    const { loadedRowsMap } = this.state;

    let min = grid.get_min_dl_value(startIndex, minimumBatchSize, lowRow);
    let max = grid.get_max_dl_value(
      startIndex,
      stopIndex,
      minimumBatchSize,
      lowRow
    );
    this._requestGridSubset(gridName, min, max, lowRow, uppRow);

    for (let i = startIndex; i <= stopIndex; i++) {
      loadedRowsMap[i] = STATUS_LOADING;
    }
  }

  // !! ??
  _onDivKeyUp(row, column, event) {
    const { channel, formula, modelDetailsActions } = this.props;

    const { editingMode } = this.state;

    if (event.keyCode === CR && !formula) {
      if (!editingMode) {
        this._onEditGesture(row, column);
      } else {
        this.setState({ editingMode: false });
        modelDetailsActions.editElemDetails(channel, { startEdition: false });
      }
    }
  }

  // !! ??
  resetContextMenuCoords() {
    this.setState({ contextMenuCoords: [] });
  }

  _renderCell(params, dataLayerValue) {
    if (grid.isFloat(dataLayerValue)) {
      dataLayerValue = Number(dataLayerValue).toFixed(9);
    }

    return (
      <div
        style={{
          padding: "4px",
          color: dataLayerValue === "EXPAND" ? "transparent" : "inherit",
        }}
        onMouseOver={this._onMouseOver.bind(
          this,
          params.rowIndex,
          params.columnIndex
        )}
      >
        {dataLayerValue === "?"
          ? ""
          : dataLayerValue === "EXPAND"
          ? "-"
          : dataLayerValue}
      </div>
    );
  }

  _cellRenderer(params) {
    const {
      editFormulaInput,
      editingColumnNumber,
      editingRowNumber,
      formula,
      formulaInput,
      gridName,
      lazyArray,
      toggleWideEditor,
      type,
      wideEditor,
    } = this.props;

    const { columns, dataLayer, loadedRowsMap } = this.state;

    const inputID = gridName;

    // ?? for unknown reasons, 'formulaInput' works better than 'formula' here
    // ?? it does not corrupt non l-type PlainValue editing (sheibe1.sym)
    const editingMode =
      formula &&
      !formulaInput.startsWith('invalid("#TYPE!"') &&
      editingRowNumber === params.rowIndex &&
      editingColumnNumber === params.columnIndex;

    const displayMode = !editingMode;

    // !! Altered on 20191219 added brute force, step by step, dataLayerValue checking
    // using !dataLayerValue in the condition does not work well - reason unknown
    // Behavior considerably improved!
    let dataLayerValue = dataLayer;
    // logger.debug("dataLayerValue", dataLayerValue);
    if (!dataLayerValue) return;
    dataLayerValue = dataLayerValue[params.rowIndex];
    if (!dataLayerValue) return;
    // logger.debug("dataLayerValue[params.rowIndex]", dataLayerValue);
    dataLayerValue = dataLayerValue[params.columnIndex];
    // logger.debug("dataLayerValue[params.rowIndex][params.columnIndex]", dataLayerValue);
    if (!dataLayerValue) return;

    // special handling for invalid values: display error code
    if (typeof dataLayerValue === "string") {
      /// patch to prevent parsing errors with @interaction
      if (dataLayerValue.indexOf("@@") !== 0) {
        // @@ Exceptions happen here when adding rows in TMX
        const ast = parse(dataLayerValue, "Grid::_cellRenderer 815");
        if (ast && ast.fun && ast.fun.name === "invalid") {
          dataLayerValue = ast.args.elems[0].value;
        }
      }
    }

    const valueWithoutFormat = dataLayerValue;
    const expanded =
      params.columnIndex === 0 && columns >= 1 && dataLayerValue === "EXPAND";

    const cellID = gridName + "-" + params.rowIndex + "-" + params.columnIndex;
    let emptyRowArrOfArr = false;

    if (type === "2-grid") {
      if (dataLayerValue === "EXPAND") {
        emptyRowArrOfArr = true;
      }
    }

    if (loadedRowsMap[params.rowIndex] !== 2) {
      // load an empty cell
      return (
        <div
          id={inputID}
          className="grid-cells"
          key={params.key}
          style={params.style}
          tabIndex={1}
        />
      );
    } else {
      return (
        <div
          className={classnames(
            "grid-cells",
            lazyArray && formula
              ? params.rowIndex === editingRowNumber
                ? params.columnIndex === editingColumnNumber
                  ? "lazy-array-edit"
                  : "lazy-array-row"
                : "lazy-array-row"
              : null,
            type === "1-grid"
              ? params.rowIndex === editingRowNumber && formula
                ? params.columnIndex === editingColumnNumber
                  ? "lazy-row"
                  : null
                : null
              : null,
            params.rowIndex === editingRowNumber && formula
              ? params.columnIndex === editingColumnNumber
                ? "focus-div"
                : null
              : null,
            grid.isFloat(valueWithoutFormat) || grid.isInt(valueWithoutFormat)
              ? "grid-cell-right"
              : null,
            expanded ? "full-grid-width" : null
          )}
          onContextMenu={() => {
            this.props.updateEditingCoords(cellID);
            this._focusActiveCell(cellID);
            this.setState({
              contextMenuCoords: [params.rowIndex, params.columnIndex],
            });
          }}
          id={cellID}
          key={params.key}
          onKeyUp={this._onDivKeyUp.bind(
            this,
            params.rowIndex,
            params.columnIndex
          )}
          onClick={
            emptyRowArrOfArr
              ? () => alert("emptyRowArrOfArr")
              : // !! this rebinding is necessary (check horrors.sym) but why?
                this._onEditGesture.bind(
                  this,
                  params.rowIndex,
                  params.columnIndex
                )
          }
          // ref={"focused"} // refs are deprecated - luckily, this one seems useless
          // spy={spy("chimes")} // spy used to verify line above was executed
          style={params.style}
          tabIndex={1}
        >
          {displayMode ? (
            this._renderCell(params, dataLayerValue)
          ) : (
            <TextInput
              inputID={inputID}
              editFormulaInput={editFormulaInput}
              formulaInput={formulaInput}
              onEscape={this._onBlur.bind(this, inputID)}
              onSave={(text) =>
                this._updateCellValue(text, params.rowIndex, params.columnIndex)
              }
              placeholder={"formula"}
              showMessage={this.props.showMessage}
              text={formulaInput}
              toggleWideEditor={toggleWideEditor}
              wideEditor={wideEditor}
            />
          )}
        </div>
      );
    }
  }

  _getColumnWidth(params) {
    const { orientation } = this.props;
    const { columnsWidth } = this.state;

    let width = orientation === "vertical" ? columnsWidth[params.index] : 100;
    if (!width) return 100;
    return width;
  }

  _onGridRowsRendered(section, onRowsRendered) {
    onRowsRendered({
      startIndex: section.rowStartIndex,
      stopIndex: section.rowStopIndex,
      overscanStartIndex: section.rowOverscanStartIndex,
      overscanStopIndex: section.rowOverscanStopIndex,
    });
  }

  /****************************************************************/
  /*********************** render grid ****************************/
  /****************************************************************/

  render() {
    const {
      formula,
      gridName,
      headerLabels,
      lazyArray,
      orientation,
    } = this.props;

    return (
      <div
        className={classnames("grid-rv", {
          lazyEdit: lazyArray && formula,
        })}
        id={gridName + "-grid-" + orientation}
      >
        {orientation === "vertical" ? (
          <ByRows
            {...this.props}
            {...this.state}
            _cellRenderer={this._cellRenderer}
            _isRowLoaded={this._isRowLoaded}
            _getColumnWidth={this._getColumnWidth}
            _loadMoreRows={this._loadMoreRows}
            _onGridRowsRendered={this._onGridRowsRendered}
            editLazyMode={lazyArray && formula}
            headerLabels={headerLabels ? headerLabels : null}
            onZoomIn={this.onZoomIn}
            resetContextMenuCoords={this.resetContextMenuCoords}
            canZoomOut={this.props.canZoomOut}
            zoomOut={this.props.zoomOut}
          />
        ) : (
          <ByColumns
            {...this.props}
            {...this.state}
            _isRowLoaded={this._isRowLoaded}
            _loadMoreRows={this._loadMoreRows}
            _getColumnWidth={this._getColumnWidth}
            _cellRenderer={this._cellRenderer}
            _onGridRowsRendered={this._onGridRowsRendered}
            onZoomIn={this.onZoomIn}
            canZoomOut={this.props.canZoomOut}
            zoomOut={this.props.zoomOut}
          />
        )}
      </div>
    );
  }
}

export default Grid;
