import React from "react";

// external
import ASCII from "../constants/ascii.js";
import CHANNEL from "../constants/channel";
import elems from "zll/elems";
import logger from "../logger/logger";
import panel from "../panel/extra/tabContentDispatchProperties";
import parse from "zll/Parser";
import UI from "../constants/UI.js";

// extra
import flushUXM from "./extra/uxm";
import getSessionHash from "./extra/getSessionHash";
import interpretValue from "./extra/interpretValue";
import parsing from "./extra/parsing";

// components
import Center from "./Center";
import CommandLineContainer from "./CommandLineContainer";
import ErrorBoundary from "./ErrorBoundary";
import HeaderContainer from "../header/HeaderContainer";

// constants
const DEFAULT_TAB_LABEL = "View 0";
const TAB = String.fromCharCode(ASCII.TAB);
const ETB = String.fromCharCode(ASCII.ETB);

// default batch size (of what?)
const DEFAULT_BATCH_SIZE = 64;

const CYCLE = 500;
const M = 1; // Math.trunc(1000 / CYCLE);

// default ZenSheet Server port to connect to
const defaultPort = 9997;

// ZS requests' sequence counter
let sequence = 0;

// Forced debugging overrides
const forceC2S = false;
const forceS2C = false;

class Main extends React.Component {
  constructor(props) {
    super(props);

    // Bind methods here.
    // Note: It is only necessary to bind event handlers and functions passed down as properties
    // Note: It is not recommended to use bind when passing props
    // Why and how to bind methods in your React component classes?:
    // http://reactkungfu.com/2015/07/why-and-how-to-bind-methods-in-your-react-component-classes/

    this._addExpressionArea = this._addExpressionArea.bind(this);
    this._connect = this._connect.bind(this);
    this._createMCArea = this._createMCArea.bind(this);
    this._dispatch = this._dispatch.bind(this);
    this._getViewIndex = this._getViewIndex.bind(this);
    this._loadUXM = this._loadUXM.bind(this);
    this._receive = this._receive.bind(this);
    this._removeExpressionArea = this._removeExpressionArea.bind(this);
    this._runScript = this._runScript.bind(this);
    this._setStructHeaderIDs = this._setStructHeaderIDs.bind(this);
    this._unsubExpression = this._unsubExpression.bind(this);
    this._updateAreaHeight = this._updateAreaHeight.bind(this);
    this._vdom = this._vdom.bind(this);

    // Public methods: passed to childen as props
    this.addDefinition = this.addDefinition.bind(this);
    this.assign = this.assign.bind(this);
    this.changeExpressionInArea = this.changeExpressionInArea.bind(this);
    this.createDefaultTab = this.createDefaultTab.bind(this);
    this.cycle = this.cycle.bind(this);
    this.deleteAllTabs = this.deleteAllTabs.bind(this);
    this.handleSubscriptions = this.handleSubscriptions.bind(this);
    this.load = this.load.bind(this);
    this.monitor = this.monitor.bind(this);
    this.runMonteCarlo = this.runMonteCarlo.bind(this);
    this.reload = this.reload.bind(this);
    this.reset = this.reset.bind(this);
    this.runInteraction = this.runInteraction.bind(this);
    this.save = this.save.bind(this);
    this.send = this.send.bind(this);
    this.setCreateMC = this.setCreateMC.bind(this);
    this.tabContentAction = this.tabContentAction.bind(this);
    this.tabLabelAction = this.tabLabelAction.bind(this);
    this.tick = this.tick.bind(this);
    this.toggleGridBorders = this.toggleGridBorders.bind(this);
    this.togglePalette = this.togglePalette.bind(this);
    this.undefine = this.undefine.bind(this);
    this.updateFeedback = this.updateFeedback.bind(this);

    // communicator initialization

    this.state = {
      modelName: "",
      createMC: false,
      cycle: 0,
      rtl: 0,
      monitor: 0,
      scriptCommands: [],
      showGridBorders: false,
      showPalette: true,
      suppressUX: false,
    };

    this.location = window.location;
    this.protocol = this.location.protocol;
    this.wRoot = this.location.host;
    this.wHost = this.location.hostname;
    this.wPort = this.location.port;
    this.session = getSessionHash(this.location.href);

    console.log("protocol:" + this.location.protocol);
    console.log("pathname:" + this.location.pathname);

    if (this.protocol === "file:") {
      const end = this.location.pathname.indexOf("/index.html");
      this.wRoot = this.location.pathname.substring(0, end);
    }

    console.log("wRoot:" + this.wRoot);

    const workerUrl = this.protocol + "//" + this.wRoot + "/ws.worker.js";

    logger.debug("protocol: " + this.protocol);
    logger.debug("wRoot: " + this.wRoot);
    logger.debug("wHost: " + this.wHost);
    logger.debug("wPort: " + this.wPort);
    logger.debug("session: " + this.session);
    logger.debug("workerUrl: " + workerUrl);

    console.log("workerUrl:" + workerUrl);

    this.communicator = new Worker(workerUrl);

    // Message received from the worker thread
    this.communicator.onmessage = this._receive;

    this.cached = null;

    this.next = () => {
      if (this.state.cycle % M === 0) {
        this.cached = this._vdom();
      }
      ++this.state.cycle;
      setTimeout(this.next, CYCLE);
    };

    setTimeout(this.next, CYCLE);
  }

  // ******** Connection ******** //

  async _connect() {
    try {
      if (!this.session) {
        this.zHost = this.protocol === "file:" ? "localhost" : this.wHost;
        this.zPort = defaultPort;
      } else {
        // The moment of truth: get host and port from the portal
        const postMessage = {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ session_hash: this.session }),
        };

        // const portal = "http://zensheet-portal.us-east-2.elasticbeanstalk.com/";
        const portal = "https://portal.zensheet.com/";
        const portalUrl = portal + "connectionInformation";

        const response = await fetch(portalUrl, postMessage);
        const serverResponse = await response.json();
        const parsedResponse = JSON.parse(serverResponse);

        if (parsedResponse.error) {
          const message = `Could not connect to ${portalUrl}: ${parsedResponse.error}`;
          throw new Error(message);
        }

        this.zHost = parsedResponse.converter_host;
        this.zPort = String(parsedResponse.converter_port);
      }

      // ZenSheet's Websocket URL
      this.zsUrl = "ws://" + this.zHost + ":" + this.zPort;
      logger.debug("connecting to ZenSheet server on " + this.zsUrl);
      this.communicator.postMessage(this.zsUrl);
    } catch (error) {
      alert(error);
      logger.error(error);
      return false;
    }
    return true;
  }

  _unsubExpression(expression) {
    const { tabs, allTabsContent, dispatch } = this.props;

    for (let i = 0; i < tabs.length; ++i) {
      for (const channel of Object.keys(allTabsContent[i])) {
        if (allTabsContent[i][channel].expression === expression) {
          panel(i)(dispatch).removeTabElem(channel);
          this.send(
            "_unsubExpression",
            `sys.view("${channel}", "update", ['${expression}'], []);`,
            CHANNEL.ZS_VIZ
          );
        }
      }
    }
  }

  //
  // subscriptions
  //
  // Handle views and subscriptions: assumes viewName will always be the channel number
  //

  handleSubscriptions(viewName, unSubExpr, subExpr) {
    const { model } = this.props;
    const channel = viewName;

    if (subExpr !== "" && subExpr !== "@MC") {
      // !! optimization opportunity
      this.send("handleSubscriptions", `ltype(${subExpr})`, channel);
      // send("handleSubscriptions", `rtype(${subExpr})`, channel);
    }

    const rtype = model[subExpr] ? model[subExpr].rtype : "";
    if (rtype === "type") {
      return;
    }

    unSubExpr = unSubExpr ? "'" + unSubExpr + "'" : "";
    subExpr = subExpr ? "'" + subExpr + "'" : "";
    this.send(
      "handleSubscriptions",
      `sys.view("${viewName}", "update", [${unSubExpr}], [${subExpr}]);`,
      CHANNEL.SYSTEM
    );
  }

  //
  // Area stuff
  //

  changeExpressionInArea(oldExpression, newExpression, channel) {
    const { dispatch, modelDetailsActions, selected } = this.props;

    channel = Number(channel);

    if (oldExpression !== newExpression) {
      modelDetailsActions.resetElemDetails(channel);
      panel(selected)(dispatch).resetElemProps(channel);
    }

    panel(selected)(dispatch).editExpression(channel, {
      expression: newExpression,
    });

    this.handleSubscriptions(channel, oldExpression, newExpression);
  }

  // @description
  // This method sends a message to the server requesting the value of an expression.
  //
  // When the value is an array, it is requested after knowing the length of
  // the array because it is necessary to know first the number of rows to
  // request a subset of the array to the server
  _requestValue(channel, expression, rowCount, minimumBatchSize) {
    const { model, modelDetails } = this.props;

    let request = expression;

    // model[expression] is null if the area label is ltype(anything)
    const rtype = model[expression] ? model[expression].rtype : "unknown";

    if (rtype === "type") {
      request = "structural(" + expression + ")";
    }

    this.send("_requestValue", request, channel);

    // logger.debug("rc mbs", rowCount, minimumBatchSize);
    // !! formerly: if (rowCount > 0 && rowCount > minimumBatchSize) {
    if (rowCount > minimumBatchSize) {
      const min = modelDetails[channel].subLimits[0];
      const max = modelDetails[channel].subLimits[1];

      request = `${expression}[${min} .. ${max}]`;
      this.send(
        "_requestValue",
        `sys.view("${channel}", "update", ['${expression}'], ['${request}']);`,
        CHANNEL.ZS_VIZ
      );
    }
  }

  updateFeedback(feedback) {
    this.props.uiActions.editCommandLineResponse(feedback);
  }

  // ******** Model Management ******** //

  // @description
  // Add to the model list all symbols already defined in the server.
  //
  // @param {String} symbols: List of all the variable names
  // @returns null

  _loadSym(symbols) {
    const { model } = this.props;

    for (const v in symbols) {
      const symbol = symbols[v];
      if (!model[symbol]) {
        // "V" stands for Validated
        this._addSymbol(symbol, "V");
      }
      if (symbol === "@uxm") {
        this.deleteAllTabs();
        // !! watch out: we can't use the CHANNEL.ZS_VIZ here!
        this.send("_loadSym", "@uxm", CHANNEL.SYSTEM_MODEL);
      }
    }
  }

  _loadUXM(ast) {
    logger.debug("_loadUXM: " + ast.regen());
    const modelName = ast.args.elems[0].value;
    const groups = ast.args.elems[1].args.elems;

    for (const group of groups) {
      if (group.args) {
        const tabName = group.args.elems[0].value;
        this.tabLabelAction(tabName, "add");
        let tabContent = group.args.elems[1].args.elems;
        if (group.args.elems[1].args.elems !== 0) {
          tabContent = group.args.elems[1].args.elems;
          for (const area of tabContent) {
            const expression = area.args.elems[0].value;
            const coords = area.args.elems[1].args.elems.map(e => e.value);
            this.tabContentAction(tabName, "sub", expression, -1, coords);
          }
        }
      }
    }
    // Mirroring these two while we learn REDUX
    this.props.uiActions.updateModelName(modelName);
    this.setState({ modelName });
  }

  // @description
  // When the client receives a SYM notification from the server,
  // model list (state) is updated, so the UI can
  // show the new symbol in the model list.
  //
  // @param {String} symbol: Symbol name
  // @param {String} state: E, P, M
  // @returns null

  _addSymbol(symbol, state) {
    this.props.modelActions.addSymbol(symbol, state);
    this.send("_addSymbol", `ltype(${symbol})`, CHANNEL.SYSTEM_LTYPE);
  }

  _deleteExpression(expression) {
    const {
      tabs,
      allTabsContent,
      modelActions,
      modelDetailsActions,
      tabLabelsActions,
      dispatch,
    } = this.props;

    for (let i = 0; i < tabs.length; ++i) {
      for (const channel of Object.keys(allTabsContent[i])) {
        if (allTabsContent[i][channel].expression === expression) {
          panel(i)(dispatch).removeTabElem(channel);

          const tabID = tabs[i].id;
          let tabsCopy = tabs[i].childrenViews;
          tabsCopy = tabsCopy.filter(elem => elem !== channel);
          tabLabelsActions.updateChildrenViews(tabID, tabsCopy);

          this.send(
            "_deleteExpression",
            `sys.view("${channel}", "remove", ?, ?);`,
            channel
          );
          modelDetailsActions.deleteElemDetails(channel);
        }
      }
    }

    modelActions.deleteSymbol(expression);
  }

  // ******************************************** //

  _runScript(ast) {
    const commands = elems("_runScript", ast).map(elem => elem.value);
    this.setState({ scriptCommands: commands });
  }

  _removeExpressionArea(expression) {
    const command = `@@ content - '${expression}'`;
    logger.debug(command);
    this.setState({ scriptCommands: [command] });
  }

  _addExpressionArea(expression) {
    const command = `@@ content + '${expression}'`;
    logger.debug(command);
    this.setState({ scriptCommands: [command] });
  }

  _setUIdefaultState() {
    this.deleteAllTabs();
    this.createDefaultTab();
  }

  _createMCTab() {
    const { tabLabelsActions } = this.props;
    tabLabelsActions.addLabel(DEFAULT_TAB_LABEL);
  }

  _createMCArea() {
    const { allTabsContent, dispatch, modelDetailsActions, tabs } = this.props;

    let index = -1;
    let tabIndex = -1;
    for (const tab of tabs) {
      ++index;
      if (tab.label === "Monte Carlo") {
        tabIndex = index;
      }
    }
    if (tabIndex === -1) return;

    const MCPanel = allTabsContent[tabIndex];

    let create = true;
    for (const key of Object.keys(MCPanel)) {
      if (MCPanel[key].expression === "@MC") {
        create = false;
      }
    }

    if (create) {
      // it must be done in this order: it is critical!
      modelDetailsActions.resetElemDetails(CHANNEL.SYSTEM_MC);
      panel(tabIndex)(dispatch).addTabElem(CHANNEL.SYSTEM_MC, "@MC");
    }
  }

  _interpretFormula(message, channel, expression) {
    const { allTabsContent, selected, dispatch, editActions } = this.props;
    const formula = parsing.result(message);

    // Warning: originally undefined - added to compile
    const rowCount = 1;

    if (allTabsContent[selected][channel]) {
      const areaExpression = allTabsContent[selected][channel].expression;
      if (areaExpression.indexOf("formula") !== -1) {
        if (areaExpression === expression) {
          this._requestValue(channel, expression, rowCount, DEFAULT_BATCH_SIZE);
        } else {
          panel(selected)(dispatch).editElemValue(channel, { value: formula });
        }
        return;
      }
    }

    if (formula === "#VALUE!") {
      // !! show error message (formula can't be edited)
    } else {
      editActions.setEditingStatus(true, channel, formula, expression);
      this.props.uiActions.editFormulaInput(formula);
    }
  }

  // ************************************************************************ //
  // ********************** Arrays visualization props ********************** //
  // ************************************************************************ //

  _getViewIndex(channel, tabs) {
    for (const i in tabs) {
      const children = tabs[i].childrenViews;
      for (const j in children) {
        if (children[j] === channel) {
          return i;
        }
      }
    }
  }

  _updateAreaHeight(caller, channel, rowCount, gridCols) {
    logger.debug(
      `_updateAreaHeight(${caller}, channel: ${channel}, ${rowCount}, ${gridCols})`
    );
    const { tabs, tabLabelsActions } = this.props;
    const tabIndex = Number(this._getViewIndex(channel, tabs));
    if (!tabs[tabIndex]) {
      return;
    }

    let found = false;
    // adjust the height of the area
    const layoutCopy = tabs[tabIndex].layout.map(g => {
      // Warning: == is required here (for now) because (perversely) one is an int and the other a string
      if (g.i === "" + channel) {
        found = true;
        const adjusted = this._getAreaHeight(g.h, rowCount);
        return { ...g, h: adjusted };
      } else {
        return g;
      }
    });

    // Hypothesis: found is always true (area already added to the layou before reaching this)

    if (!found) {
      const w = Math.floor(gridCols / 2);
      const x = Math.floor(w / 2);
      layoutCopy.push({
        i: channel,
        x: x,
        y: Infinity,
        w: w,
        h: this._getAreaHeight(UI.MIN_AREA_HEIGHT, rowCount),
        minW: 1,
        minH: 1,
      });
    }

    tabLabelsActions.editLayout(tabs[tabIndex].id, layoutCopy);
  }

  // @description
  // This procedure sets the default parameters needed
  // to render an expression when the user asks to monitor it in a view.
  //
  // @param {String}  message: The message received by the server
  // @param {Integer} channel
  // @param {String}  expression
  // @param {String}  value
  // @param {Integer} rowCount
  // @param {Integer} minimumBatchSize
  // @returns null

  _setArrayLayout(channel, expression, value, rowCount, minimumBatchSize) {
    logger.debug(`_setArrayLayout:`);
    logger.debug(`channel: ${channel}`);
    logger.debug(`expression: ${expression}`);
    logger.debug(`value: ${value}`);

    const { modelDetails, modelDetailsActions } = this.props;

    const limits = parsing.get_array_limits(
      channel,
      expression,
      rowCount,
      minimumBatchSize
    );
    const prevRowCount = modelDetails[channel].rowCount;
    const dataLayerLen = minimumBatchSize * 2;
    const bufferUsed = !prevRowCount
      ? limits[1] < rowCount - 1
      : prevRowCount > dataLayerLen;

    modelDetailsActions.editElemDetails(channel, { subLimits: limits });

    if (!prevRowCount || !bufferUsed) {
      modelDetailsActions.editElemDetails(channel, { rowCount: rowCount });
      if (!prevRowCount || prevRowCount < rowCount) {
        this._updateAreaHeight("Main 640", channel, rowCount, null); // Q1AH - area height
      }
    }

    // If value details props were reseted
    if (!modelDetails[channel].orientation) {
      this._setDefaultCustomProps(channel, value, minimumBatchSize, bufferUsed);
    }

    if (!modelDetails[channel].headerLabels && parsing.with_structs(value)) {
      this._setStructHeaderIDs(channel, value, prevRowCount);
    }
  }

  _restrictX(lo, x, hi) {
    // logger.debug(`_restrictX(${lo}, ${x}, ${hi})`);
    const result = x > hi ? hi : x < lo ? lo : x;
    // logger.debug(`_restrictX(${lo}, ${x}, ${hi}) => ${result}`);
    return result;
  }

  _getAreaHeight(prevHeight, rowCount) {
    logger.debug(
      `_getAreaHeight(previous: ${prevHeight}, current: ${rowCount})`
    );
    const minHeight = UI.MIN_AREA_HEIGHT;
    const maxHeight = UI.MAX_AREA_HEIGHT;
    const adjusted = Math.round(
      (UI.TOP_EXTRA + UI.ROW_HEIGHT * (rowCount + 1) + UI.BOTTOM_EXTRA) /
        UI.RGL_SZ
    );
    const clamped = this._restrictX(minHeight, adjusted, maxHeight);
    const result = clamped > prevHeight ? clamped : prevHeight;
    logger.debug(
      `_getAreaHeight(previous: ${prevHeight}, current: ${rowCount}) => ${result}`
    );
    return result;
  }

  // Props that can be manually changed by the user
  _setDefaultCustomProps(channel, value, minimumBatchSize, bufferUsed) {
    const { modelDetails, modelDetailsActions } = this.props;

    if (modelDetails[channel].type.base === "TBD") {
      const visualizeAs = modelDetails[channel].type.visualizeAs;
      const base = value.startsWith("[") ? "array" : "struct";
      const type = { visualizeAs: visualizeAs, base: base };
      modelDetailsActions.editElemDetails(channel, { type: type });
    }

    const orientation = "vertical";
    modelDetailsActions.editElemDetails(channel, { orientation: orientation });
    modelDetailsActions.editElemDetails(channel, {
      minimumBatchSize: minimumBatchSize,
    });
    modelDetailsActions.editElemDetails(channel, { showIndexes: true });
    modelDetailsActions.editElemDetails(channel, { usesDataLayer: bufferUsed });
  }

  _setStructHeaderIDs(channel, value, rowCount) {
    const { modelDetailsActions } = this.props;

    const ast = parse(value, "Main::_setStructHeaderIDs 872");
    const headerIDs = parsing.get_header_ids(ast);
    if (headerIDs.length > 0) {
      modelDetailsActions.editElemDetails(channel, { headerLabels: headerIDs });
    }

    const areaHeight = isNaN(rowCount) ? 4 : rowCount + 1; // !!
    this._updateAreaHeight("Main 709", channel, areaHeight, value); // Q1AH - areaHeight
  }

  // ******************************************** //

  _interpretLtype(expression, message, channel) {
    const {
      allTabsContent,
      dispatch,
      modelDetails,
      modelDetailsActions,
      selected,
      uiActions,
    } = this.props;

    const ltype = parsing.result(message);
    const heuristic_type = parsing.get_type(ltype);
    const vis = heuristic_type.visualizeAs;
    const areaExists = allTabsContent[selected][channel] !== undefined;

    if (channel === CHANNEL.SYSTEM) {
      uiActions.updateEditingLtype(ltype);
      return;
    }

    let rowCount = modelDetails[channel]
      ? modelDetails[channel].rowCount
      : undefined;

    modelDetailsActions.editElemDetails(channel, { ltype: ltype });
    modelDetailsActions.editElemDetails(channel, { type: heuristic_type });

    if (areaExists) {
      const areaExpression = allTabsContent[selected][channel].expression;
      if (expression) {
        if (areaExpression.indexOf("ltype") !== -1) {
          // special case when the user adds ltype as an expression
          if (areaExpression === expression) {
            this._requestValue(
              channel,
              expression,
              rowCount,
              DEFAULT_BATCH_SIZE
            );
          } else {
            panel(selected)(dispatch).editElemValue(channel, { value: ltype });
          }
          return;
        }
      }
    }

    const STOP_INSANITY = true;
    if (!STOP_INSANITY) {
      if (vis.indexOf("grid") !== -1 && !rowCount && expression !== "@uxm") {
        if (vis === "multi-dim-grid") {
          this.send("_interpretLtype", `dim(${expression})[0]`, channel);
        } else {
          if (heuristic_type.base === "struct") {
            rowCount = parsing.get_struct_fields_length(ltype);
            this._setArrayLayout(
              channel,
              expression,
              String(rowCount),
              rowCount,
              DEFAULT_BATCH_SIZE
            );
            this._requestValue(
              channel,
              expression,
              rowCount,
              DEFAULT_BATCH_SIZE
            );
          } else {
            const request = `length(${expression})`;
            logger.debug("heuristic_type.base: " + heuristic_type.base);
            logger.debug("request: " + request);
            logger.debug("expression: " + expression);
            logger.debug("ltype: " + ltype);
            // !! AD-HOC AND INCORRECT PATCH (to handle string)
            if (heuristic_type.base === "TBD") {
              this._requestValue(channel, expression, 1, DEFAULT_BATCH_SIZE);
            } else {
              // Get number of rows of the array (however, a lazy var could have an array value)
              // Changed channel to fix issue #94 ... however the whole block appears insane
              this.send("_interpretLtype", request, CHANNEL.ZS_VIZ); // originally channel
            }
          }
        }
      } else {
        this._requestValue(channel, expression, rowCount, DEFAULT_BATCH_SIZE);
      }
    }
  }

  /* Experimental finding: converts raw array data into a rendered grid */
  _updatePropDetails(message, channel, expression, value, ast) {
    // !! warning: highly treacherous
    const { modelDetails, modelDetailsActions } = this.props;

    if (!modelDetails[channel]) {
      // ad-hoc error handling
      logger.error(`no model details for channel ${channel}`);
      logger.error(`_updatePropDetails(${message}, ${channel}, ${expression})`);
      return;
    }

    const prevRowCount = modelDetails[channel].rowCount;
    const elements = elems("_updatePropDetails", ast);
    if (elements) {
      const rowCount = elements.length;
      this._setArrayLayout(
        channel,
        expression,
        value,
        rowCount,
        DEFAULT_BATCH_SIZE
      );
    } else if (prevRowCount) {
      // reset props if the expression changed its ltype (what is this ???)
      modelDetailsActions.resetElemValueDetails(channel);
    }
  }

  _updateValue(message, channel, expression, value, ast) {
    const { allTabsContent, dispatch, tabs } = this.props;

    const tabIndex = this._getViewIndex(channel, tabs);
    if (!tabIndex) return; // !!

    const targetPanel = allTabsContent[tabIndex];
    const area = targetPanel[channel];

    this._updatePropDetails(message, channel, expression, value, ast);

    const iValue = interpretValue(value);

    if (area && area.value !== iValue) {
      panel(tabIndex)(dispatch).editElemValue(channel, { value: iValue });
    }
  }

  ///
  /// response handlers
  ///

  _handleMonteCarloResponse(message) {
    const { selected, allTabsContent, dispatch } = this.props;

    if (allTabsContent[selected][CHANNEL.SYSTEM_MC]) {
      panel(selected)(dispatch).editElemValue(CHANNEL.SYSTEM_MC, {
        value: message,
      });
    }
  }

  _handleSysDescribe(symbol, type) {
    // we replaced sys.describe with ltype
    // the price we paid: we don't have the declared type
    // errors were happening because of @interaction
    // sys.describe is poorly defined
    // check: https://github.com/Liondance/ZCC/issues/53

    this.props.modelActions.updateSymbolDeclaredType(symbol, {
      declared_type: type, // note: this is often true, and always a harmless lie
    });
    this.props.modelActions.updateSymbolInferredType(symbol, {
      inferred_type: type,
    });
  }

  // !! unspecific name
  _dispatchAsync(tag, expression) {
    const { model } = this.props;

    if (tag === "SYU") {
      this._deleteExpression(expression);
      if (Object.keys(model).length === 1) {
        this._setUIdefaultState();
      }
    } else {
      let state = "";
      if (tag === "SYE") {
        state = "F";
      } else if (tag === "SYP") {
        state = "P";
      } else if (tag === "SYM") {
        state = "V";
        if (expression === "@uxm") {
          this.reload("_dispatchAsync");
        }
      }

      // !! check this (_dispatchAsync)
      if (expression.indexOf(".") !== -1) {
        expression = expression.slice(".")[0];
      }

      if (!model[expression]) {
        this._addSymbol(expression, state);
      } else {
        this.props.modelActions.updateSymbolState(expression, { state: state });
      }
    }
  }

  _dispatch(tag, message, channel, expression, value) {
    logger.debug(
      `dispatch(tag ${tag}, message ${message}, channel ${channel}, exp ${expression}, val ${value})`
    );

    if (tag === "ERROR") {
      logger.debug("_dispatch aborted");
      return;
    }

    const { modelDetailsActions } = this.props;

    const arg = message.slice(0, message.indexOf("("));

    if (arg === "symbols") {
      const symbols = parsing.parse_symbols(parsing.result(message));
      this._loadSym(symbols);
    } else if (arg === "ltype") {
      this._interpretLtype(expression, message, channel);
    } else if (arg === "rtype") {
      modelDetailsActions.editElemDetails(channel, { rtype: value });
    } else if (arg === "formula") {
      this._interpretFormula(message, channel, expression);
    } else if (value) {
      const ast = parse(value, "Main::dispatch 1040");
      if (ast.is_error()) {
        console.error(`failed: ${value}`);
        return;
      }
      if (arg === "value") {
        this._updateValue(message, channel, expression, value, ast);
        return;
      }
      if (tag === "OK" || tag === "ASY") {
        if (
          channel === CHANNEL.SYSTEM &&
          message.indexOf("@interaction") !== -1
        ) {
          this._runScript(ast);
        } else if (
          channel === CHANNEL.SYSTEM_MODEL &&
          message.indexOf("@uxm") !== -1
        ) {
          this._loadUXM(ast);
        } else if (
          // Added channel condition to fix issue #94 but check STOP_INSANITY!
          channel === CHANNEL.ZS_VIZ &&
          (message.indexOf("length(") !== -1 || message.indexOf("dim(") !== -1)
        ) {
          // When the variable is an array !! BUT SOMETIMES IT'S NOT!
          const rowCount = parsing.get_array_length(message);
          logger.debug(ast);
          logger.debug(ast.regen());
          logger.debug(ast.is_string());
          logger.debug(`message = ${message}: rowCount = ${rowCount}`); //  ^ ${modelDetails[channel].orientation}`);
          this._setArrayLayout(
            channel,
            expression,
            value,
            rowCount,
            DEFAULT_BATCH_SIZE
          );
          this._requestValue(channel, expression, rowCount, DEFAULT_BATCH_SIZE);
        } else {
          this._updateValue(message, channel, expression, value, ast);
        }
      }
    }
  }

  ///
  /// Entry point for messages received from the ws communicator
  ///
  _receive(envelope) {
    const message = envelope.data;

    if (this.props.logS2C || forceS2C) {
      logger.log(`S2C server => ${message}`);
    }

    const splittedResponse = message.split(TAB);
    const header = splittedResponse[0]; // contains channel and sequence numbers
    const response = splittedResponse[1];

    const channel = Number(header.substring(0, header.indexOf("[")));
    const tag = parsing.get_tag(response);
    const msg = message.substring(message.indexOf(":") + 2, message.length); // message without tag

    const expression = parsing.get_expression(tag, msg);
    const value = parsing.get_rhs(msg);

    if (tag === "WSW") {
      const message = parse(msg).value;
      if (message.startsWith("ERROR:") || UI.MODE === UI.DEVELOPER) {
        this.updateFeedback(message);
      }
      return;
    }

    if (channel === CHANNEL.SYSTEM && message.indexOf("@RTL@") !== -1) {
      // @@ Ugly! Brute force parsing!
      logger.debug(`tag ${tag} msg ${msg}`);
      const ast = parse(msg);
      logger.debug(ast.args[0].args.elems[1].value);
      const then = ast.args[1].args.elems[1].value;
      const rtl = Date.now() - then;
      this.setState({ rtl });
      return;
    }

    if (channel === CHANNEL.ASY) {
      this._dispatchAsync(tag, expression);
      return;
    }

    if (message.indexOf("mc.params") !== -1) {
      this._createMCArea();
      return;
    }

    // Monte Carlo server response //
    const mc0 = response.search(/ASY: [(]/);
    if (mc0 !== -1) {
      const mc1 = response.search(/[=][=][>] [[][(][?]/);
      if (mc1 !== -1) {
        const ast = parse(response, "Main::_receive 883");
        const elems = ast.args[0].args[1].args;
        if (!elems) {
          logger.warn("undefined elems handling MC response");
        } else {
          this._handleMonteCarloResponse(response);
          return;
        }
      }
    }

    if (channel === CHANNEL.SYSTEM_LTYPE) {
      this._handleSysDescribe(expression, value);
      return;
    }

    if (channel !== CHANNEL.ZS_VIZ) {
      if (
        message.indexOf("mc.run") === -1 &&
        message.indexOf("sys.view") === -1 &&
        message.indexOf("sys.reset") === -1
      ) {
        this._dispatch(tag, msg, channel, expression, value);
      }
    }

    if (channel === CHANNEL.ZS_VIZ || CHANNEL.START <= channel) {
      if (tag !== "ASY") {
        this.updateFeedback(response);
      }
    }
  }

  // ******************************************** //

  send(origin, message, channel) {
    if (!message) {
      logger.error(`${origin}: tried to send undefined message`);
      return;
    }

    if (!channel) {
      logger.error(`${origin}: tried to send ${message} on undefined channel`);
      return;
    }

    if (message === "SUX") {
      this.setState({ suppressUX: !this.state.suppressUX });
      this.reload("send");
      return;
    }

    const request = `${channel}[${++sequence}]${TAB}${message}${ETB}`;
    if (this.props.logC2S || forceC2S) {
      logger.log(`C2S ${origin} => ${request}`);
    }

    // Send request to server
    this.communicator.postMessage(request);
  }

  monitor(delay) {
    // note that delay is undefined when called by timeout
    if (delay === -1) {
      // turn it off!
      this.setState({ monitor: 0 });
    } else {
      // if delay is undefined and this.state.monitor === 0 we dont arm the timeout
      delay = delay || this.state.monitor;
      if (delay) {
        const now = Date.now();
        const message = `("@RTL@", ${now})`;
        this.send("monitor", message, CHANNEL.SYSTEM);
        setTimeout(this.monitor, delay);
      }
      this.setState({ monitor: delay });
    }
  }

  reload(caller) {
    logger.debug(`${caller} => reload => symbols()`);
    this.deleteAllTabs();
    this.send(caller, "symbols()", CHANNEL.SYSTEM_MODEL);
  }

  load(modelName) {
    if (!modelName) {
      this.reload("load");
    } else {
      this.reset();
      const command = `sys.load("./Out/${modelName}.sym");`;
      this.send("load", command, CHANNEL.ZS_VIZ);
      // Mirroring these two while we learn REDUX
      this.props.uiActions.updateModelName(modelName);
      this.setState({ modelName });
    }
  }

  save(modelName) {
    flushUXM(this, modelName);
    const command = `sys.save("./Out/${modelName}.sym");`;
    this.send("save", command, CHANNEL.ZS_VIZ);
    // Mirroring these two while we learn REDUX
    this.props.uiActions.updateModelName(modelName);
    this.setState({ modelName });
  }

  togglePalette() {
    this.setState({ showPalette: !this.state.showPalette });
  }

  tick() {
    this.send("tick", "sys.tick()", CHANNEL.ZS_VIZ);
  }

  cycle(rate) {
    const command = `sys.cycle(${rate});`;
    this.send("cycle", command, CHANNEL.ZS_VIZ);
  }

  undefine(symbol) {
    const variable = this.props.model[symbol];
    if (variable && variable.state !== "V") {
      this.send("undefine", `var ${symbol};`, CHANNEL.ZS_VIZ);
    }
    this._unsubExpression(symbol); // Workaround for (IMO) server-side issue
    const command = `sys.undefine('${symbol}');`;
    this.send("undefine", command, CHANNEL.ZS_VIZ);
  }

  reset() {
    this.send("reset", "sys.reset()", CHANNEL.ZS_VIZ);
    // Mirroring these two while we learn REDUX
    this.props.uiActions.updateModelName("");
    this.setState({ modelName: "" });
  }

  runInteraction() {
    this.send("runInteraction", "@interaction", CHANNEL.SYSTEM);
  }

  addDefinition(symbol, definition, addArea) {
    this.send("addDefinition", definition, CHANNEL.ZS_VIZ);
    if (addArea) {
      this._addExpressionArea(symbol);
    }
  }

  tabLabelAction(label, action) {
    if (label !== "") {
      this.props.uiActions.tabLabelAction({ label: label, action: action });
    }
  }

  tabContentAction(label, action, expression, channel, coords) {
    if (label !== "") {
      this.props.uiActions.tabContentAction({
        label: label,
        action: action,
        expression: expression,
        channel: channel,
        coordinates: coords,
      });
    }
  }

  createDefaultTab() {
    const { tabLabelsActions, tabContentsActions, selectedActions } =
      this.props;

    tabLabelsActions.addLabel(DEFAULT_TAB_LABEL);
    tabContentsActions.addTabContent();
    selectedActions.editSelected(0);

    return DEFAULT_TAB_LABEL;
  }

  // Delete all the variables of the model and all the model views
  deleteAllTabs() {
    const {
      allTabsContent,
      selectedActions,
      tabContentsActions,
      tabLabelsActions,
      tabs,
    } = this.props;

    for (let i = tabs.length - 1; i >= 0; --i) {
      const panel = allTabsContent[i];
      const channels = Object.keys(panel);

      for (let j = 0; j < channels.length; ++j) {
        this._deleteExpression(panel[channels[j]].expression);
      }

      tabContentsActions.deleteTabContent(i);
      tabLabelsActions.deleteLabel(tabs[i].label);
    }

    selectedActions.editSelected(-1);
  }

  assign(channel, lhs, rhs) {
    const { editingLtype } = this.props;

    if (!rhs) {
      rhs = "?";
    }

    const ast = parse(rhs, "Main::assign");
    if (!ast || ast.is_error()) {
      const error = `ERROR: syntax error: '${rhs}'`;
      this.updateFeedback(error);
      return;
    }

    const quote = "'";
    const ltype = editingLtype;

    if (ltype.startsWith("lazy")) {
      rhs = quote + rhs + quote;
    }

    const assignment = lhs + " := " + rhs + ";";
    this.send("assign", assignment, channel || CHANNEL.ZS_VIZ);
  }

  runMonteCarlo(p) {
    logger.debug(
      `${p.buckets} ${p.lowerBound} ${p.upperBound} ${p.iterations}`
    );
  }

  // Grid borders

  toggleGridBorders() {
    this.setState({ showGridBorders: !this.state.showGridBorders });
  }

  // ********* React Lifecycle Methods ********** //

  async componentDidMount() {
    const connected = await this._connect();

    if (connected) {
      // Load model in memory
      this.load(null);
      // Subscribe to model changes
      this.send(
        "load",
        `sys.view(".", "create", ${CHANNEL.SYSTEM_MODEL}, ?);`,
        CHANNEL.SYSTEM_MODEL
      );
    }
  }

  componentDidUpdate() {}

  componentWillUnmount() {
    alert("Tragedy! Main will unmount");
  }

  setCreateMC(flag) {
    this.setState({ createMC: flag });
  }

  _vdom() {
    const { model, modelDetails, uiActions } = this.props;
    const cache = (
      <div>
        <ErrorBoundary>
          <HeaderContainer
            setCreateMC={this.setCreateMC}
            cycle={this.cycle}
            load={this.load}
            modelName={this.state.modelName}
            monitor={this.monitor}
            reload={this.reload}
            reset={this.reset}
            rtl={this.state.rtl}
            runInteraction={this.runInteraction}
            save={this.save}
            tick={this.tick}
            toggleGridBorders={this.toggleGridBorders}
          />
        </ErrorBoundary>

        <ErrorBoundary>
          <Center
            addDefinition={this.addDefinition}
            assign={this.assign}
            changeExpressionInArea={this.changeExpressionInArea}
            createDefaultTab={this.createDefaultTab}
            createMC={this.state.createMC}
            setCreateMC={this.setCreateMC}
            handleSubscriptions={this.handleSubscriptions}
            model={model}
            modelDetails={modelDetails}
            runMonteCarlo={this.runMonteCarlo}
            send={this.send}
            showGridBorders={this.state.showGridBorders}
            showModelList={true}
            showPalette={this.state.showPalette}
            suppressUX={this.state.suppressUX}
            undefine={this.undefine}
            updateFeedback={this.updateFeedback}
          />
        </ErrorBoundary>

        <ErrorBoundary>
          <CommandLineContainer
            assign={this.assign}
            editActions={this.editActions}
            formulaInput={this.formulaInput}
            scriptCommands={this.state.scriptCommands}
            send={this.send}
            tabContentAction={this.tabContentAction}
            tabLabelAction={this.tabLabelAction}
            toggleResponseBar={uiActions.toggleResponseBar}
          />
        </ErrorBoundary>
      </div>
    );
    return cache;
  }

  render() {
      this.next();
      return this.cached;
  }
}

export default Main;
