/// LRQS 3
///

import React from "react";
import ValueContainer from "./ValueContainer";
import TextInput from "./TextInput";

import logger from "../logger/logger";

// execution counter (performance measure) set to null to disable
let counter = null;

///
/// AreaLabel
///

function AreaLabel(props) {
  const _handleSave = (newExpression) => {
    const { channel, expression, updateAreaLabel } = props;
    newExpression = newExpression.trim();
    updateAreaLabel(expression, newExpression, channel);
  };

  const _cancelEdit = () => {
    props.stopEditingAreaLabel();
  };

  const {
    canZoomOut,
    editingAreaLabel,
    expression,
    showLabel,
    startEditingAreaLabel,
  } = props;

  return (
    <div
      className="elem-name"
      style={{ display: showLabel ? "block" : "none" }}
    >
      {editingAreaLabel ? (
        <TextInput
          inputID={"label-input"}
          editFormulaInput={props.editFormulaInput}
          formulaInput={props.formulaInput}
          onSave={(text) => _handleSave(text)}
          onEscape={() => _cancelEdit()}
          placeholder={"symbol or general expression"}
          text={props.formulaInput}
          toggleWideEditor={() => {
            return true;
          }}
        />
      ) : (
        <div
          className="area-label"
          onDoubleClick={() => startEditingAreaLabel()}
        >
          {canZoomOut ? (
            <i
              className="fa fa-arrow-left"
              style={{ marginLeft: "2px", marginRight: "4px" }}
            />
          ) : null}
          {expression}
        </div>
      )}
    </div>
  );
}

///
/// DefinedArea
///

let monitor = {};

function DefinedArea(props) {
  const {
    areaLabel,
    disableRunButton,
    elem,
    elemDetails,
    formulaInput,
    renderValue,
    assign,
    send,
    showLabel,
    showMessage,
    startEditingAreaLabel,
    stopEditingAreaLabel,
    uiActions,
    updateAreaLabel,
    zoomIn,
    zoomOut,
  } = props;

  const canZoomOut = elem.stack.length > 0;

  if (counter) {
    ++counter;
    if (!monitor[elem.expression]) {
      monitor[elem.expression] = 1;
    } else {
      ++monitor[elem.expression];
    }

    if (counter % 10 === 0) {
      logger.log(counter);
      logger.log(monitor);
    }
  }

  return (
    <div style={{ position: "relative", height: "100%" }}>
      {showLabel ? (
        <AreaLabel
          canZoomOut={canZoomOut}
          channel={elem.channel}
          editFormulaInput={uiActions.editFormulaInput}
          editingAreaLabel={
            areaLabel.isEditing && areaLabel.channel === elem.channel
          }
          expression={elem.expression}
          formulaInput={formulaInput}
          showLabel={showLabel}
          startEditingAreaLabel={startEditingAreaLabel}
          stopEditingAreaLabel={stopEditingAreaLabel}
          updateAreaLabel={updateAreaLabel}
          zoomOut={zoomOut}
        />
      ) : null}

      {renderValue ? (
        <ValueContainer
          canZoomOut={canZoomOut}
          channel={elem.channel}
          disableRunButton={disableRunButton}
          elem={elem}
          elemDetails={elemDetails}
          assign={assign}
          send={send}
          showLabel={showLabel}
          showMessage={showMessage}
          startEditingAreaLabel={startEditingAreaLabel}
          zoomIn={zoomIn}
          zoomOut={zoomOut}
        />
      ) : null}
    </div>
  );
}

export default DefinedArea;
