import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as uiActions from "../state/ui";

import MonteCarloSetUp from "./MonteCarloSetUp";

function mapStateToProps(state) {
  return {
    allTabsContent: state.tabContentCollection,
    selected: state.tabSelected.selected,
    buckets: state.ui["montecarlo"].buckets,
    iterations: state.ui["montecarlo"].iterations,
    lowerBound: state.ui["montecarlo"].lowerBound,
    upperBound: state.ui["montecarlo"].upperBound,
    runMC: state.ui["toggle"].runMC,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    uiActions: bindActionCreators(uiActions, dispatch),
    dispatch,
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(MonteCarloSetUp);
