// site url example:
// http://ec2-18-191-71-17.us-east-2.compute.amazonaws.com/?sessionHash=5b6d0f229a334b1a581a9f1b

function getSessionHash(url) {
  let siteUrl = url.endsWith("#") ? url.slice(0, -1) : url;

  const hashQuery = "?sessionHash=";
  const queryStart = siteUrl.indexOf(hashQuery);
  if (queryStart < 0) {
    return null;
  }

  const hashStart = queryStart + hashQuery.length;
  const hashEnd = siteUrl.length;

  const sessionHash = siteUrl.substring(hashStart, hashEnd);
  return sessionHash;
}

export default getSessionHash;
