import React from "react";
import ASCII from "../constants/ascii";
import parse from "zll/Parser";
// import logger from "../logger/logger";

let mapKeys = {};

const { CR, DC1, ESC, UC_A } = ASCII;

export default function TextInput(props) {
  function CtrlA() {
    // press Ctrl+A to activate wide editor
    return mapKeys[DC1] && mapKeys[UC_A];
  }

  function handleChange(e) {
    const value = e.target.value;
    props.editFormulaInput(value);
  }

  function handleKeyUp(e) {
    const text = e.target.value.trim() || "?";
    if (e.which === CR) {
      const ast = parse(text, "TextInput::handleKeyUp");
      if (!ast || ast.is_error()) {
        props.showMessage(`syntax error: '${text}'`);
      } else {
        props.onSave(text);
        props.editFormulaInput("");
      }
    } else if (e.which === ESC) {
      props.onEscape();
      props.editFormulaInput("");
      mapKeys[e.keyCode] = e.type === "keyup";
    }
  }

  function handleKeyDown(e) {
    const { inputID, toggleWideEditor } = props;
    const input = document.getElementById(inputID);
    mapKeys[e.keyCode] = e.type === "keydown";
    if (CtrlA()) {
      toggleWideEditor(true);
      input.blur();
      mapKeys = {};
    }
  }

  function handleBlur(e) {
    props.onEscape();
    if (!CtrlA()) {
      props.editFormulaInput("");
    }
    mapKeys = {};
  }

  return (
    <input
      className="area-input"
      id={props.inputID}
      type="text"
      tabIndex={1} // !!
      autoFocus={true}
      placeholder={props.placeholder}
      value={props.formulaInput}
      onBlur={handleBlur}
      onChange={handleChange}
      onKeyDown={handleKeyDown}
      onKeyUp={handleKeyUp}
    />
  );
}
