/// ByRows.js
///
/// LRQS 2
///

/* https://bvaughn.github.io/react-virtualized/
 *
 * Grid component renders tabular data with virtualization along the vertical and
 * horizontal axes. Row heights and column widths must be calculated ahead of time
 * and specified as a fixed size or returned by a getter function.
 *
 * InfiniteLoader component manages just-in-time data fetching to ensure that the all visible
 * rows have been loaded. It also uses a UI.THRESHOLD to determine how early to pre-fetch
 * rows (before a user scrolls to them).
 *
 * ScrollSync is a HOC that simplifies the process of synchronizing scrolling between
 * two or more virtualized components.
 */

import React from "react";
import classnames from "classnames";
import { ContextMenuTrigger } from "react-contextmenu";
import { Grid, InfiniteLoader, ScrollSync } from "react-virtualized";
import GridContextMenu from "./GridContextMenu";

import Comment from "./../gui/Comment";
import ContextMenu from "../gui/ContextMenu";
import UI from "../constants/UI";

export default class ByRows extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};
    this.state.leftGridWidth = this._setLeftGridWidth();

    this._renderHeaderCell = this._renderHeaderCell.bind(this);
    this._renderLeftSideCell = this._renderLeftSideCell.bind(this);
  }

  _setLeftGridWidth() {
    const { base, headerLabels } = this.props;
    let leftGridWidth = UI.ROW_INDEX_WIDTH;

    if (headerLabels && base === "struct") {
      let largestLen = 3;
      for (let i = 0; i < headerLabels.length; ++i) {
        const strLen = headerLabels[i].length;
        if (strLen > largestLen) {
          largestLen = strLen;
        }
      }
      leftGridWidth = 12 * largestLen;
    }
    return leftGridWidth;
  }

  componentDidUpdate(prevProps) {
    const { formula, rowCount } = this.props;

    // logger.debug(`ByRows::componentDidUpdate(${prevProps})`);
    // Do not update the grid when editing a formula
    if (formula) return;

    if (rowCount !== prevProps.rowCount) {
      this._setLeftGridWidth();
    }
    this.refs.scrollSyncComponent.forceUpdate();
  }

  _renderLeftSideCell({ columnIndex, key, rowIndex, style }) {
    const { base, headerLabels } = this.props;

    return (
      <div key={key} style={style}>
        {headerLabels && base === "struct"
          ? // Q3IL - rowIndex is the index label
            headerLabels[rowIndex]
          : `${rowIndex}`}{" "}
      </div>
    );
  }

  _renderLeftHeaderCellNone({ columnIndex, key, rowIndex, style }) {
    return <div className="headerCell" key={key} style={style} />;
  }

  _renderHeaderCell({ columnIndex, key, rowIndex, style }) {
    return this._renderLeftHeaderCell(columnIndex, key, rowIndex, style);
  }

  _renderLeftHeaderCell(columnIndex, key, rowIndex, style) {
    const { headerLabels } = this.props;

    return (
      <div className="headerCell" key={key} style={style}>
        {`${headerLabels[columnIndex]}`}
      </div>
    );
  }

  render() {
    const {
      _cellRenderer,
      _getColumnWidth,
      _isRowLoaded,
      _loadMoreRows,
      _onGridRowsRendered,
      base,
      canZoomOut,
      channel,
      columns,
      contextMenu,
      contextMenuCoords,
      editLazyMode,
      expression,
      gridHeight,
      gridWidth,
      headerLabels,
      minimumBatchSize,
      orientation,
      rows,
      send,
      showIndexes,
      type,
      updateCurrentContextMenu,
      zoomOut,
    } = this.props;

    const display = showIndexes ? "inline" : "none";

    return (
      <ScrollSync ref="scrollSyncComponent">
        {({ onScroll, scrollLeft, scrollTop }) => (
          <div className="Table">
            <div
              className={"LeftSideGridContainer"}
              style={{
                position:
                  headerLabels && base !== "struct" ? "absolute" : "relative",
                left: 0,
                top: 0,
              }}
            >
              <Grid
                cellRenderer={this._renderLeftHeaderCellNone}
                className="HeaderGrid"
                columnCount={1}
                columnWidth={this.state.leftGridWidth}
                height={UI.ROW_HEIGHT}
                rowCount={1}
                rowHeight={UI.ROW_HEIGHT}
                width={this.state.leftGridWidth}
              />
            </div>

            {headerLabels && base !== "struct" ? (
              <div
                className={classnames({ lazyEdit: editLazyMode })}
                style={{ height: "30px", width: gridWidth, display: display }}
              >
                <Grid
                  cellRenderer={this._renderHeaderCell}
                  className="HeaderGrid"
                  columnCount={columns}
                  columnWidth={_getColumnWidth}
                  height={UI.ROW_HEIGHT}
                  rowCount={1}
                  rowHeight={UI.ROW_HEIGHT}
                  scrollLeft={scrollLeft}
                  style={{ left: this.state.leftGridWidth }}
                  width={gridWidth}
                />
              </div>
            ) : null}

            <div
              className={classnames(
                "LeftSideGridContainer",
                { lazyEdit: editLazyMode },
                "left-grid"
              )}
              style={{ display: display }}
            >
              <Grid
                cellRenderer={this._renderLeftSideCell}
                className="LeftSideGrid"
                columnCount={1}
                columnWidth={this.state.leftGridWidth}
                height={gridHeight + 10}
                rowHeight={UI.ROW_HEIGHT}
                rowCount={rows}
                scrollTop={scrollTop}
                width={this.state.leftGridWidth}
                canZoomOut={this.props.canZoomOut}
                zoomOut={this.props.zoomOut}
              />
            </div>

            <div
              className={classnames({ lazyEdit: editLazyMode }, "right-grid")}
              style={{ marginLeft: this.state.leftGridWidth }}
            >
              <ContextMenuTrigger id={channel + "gridContextMenu"}>
                <InfiniteLoader
                  // ref="infiniteLoader"
                  isRowLoaded={_isRowLoaded}
                  loadMoreRows={_loadMoreRows}
                  minimumBatchSize={minimumBatchSize}
                  rowCount={rows}
                  THRESHOLD={UI.THRESHOLD}
                >
                  {({ onRowsRendered, registerChild }) => (
                    <Grid
                      cellRenderer={_cellRenderer}
                      className={""}
                      columnCount={columns}
                      columnWidth={_getColumnWidth}
                      height={gridHeight + 10}
                      onScroll={onScroll}
                      onSectionRendered={section =>
                        _onGridRowsRendered(section, onRowsRendered)
                      }
                      overscanRowCount={8}
                      ref={registerChild}
                      rowCount={rows}
                      rowHeight={UI.ROW_HEIGHT}
                      width={gridWidth}
                      canZoomOut={this.props.canZoomOut}
                      zoomOut={this.props.zoomOut}
                    />
                  )}
                </InfiniteLoader>
              </ContextMenuTrigger>
              <Comment>
                <ContextMenu
                  id={channel + "gridContextMenu"}
                  handler={this._handleClick}
                  menu={[
                    { item: "item 0 ", description: "ZContextMenu Item 0" },
                    { item: "item 1 ", description: "ZContextMenu Item 1" },
                    { item: "item 2 ", description: "ZContextMenu Item 2" },
                    { item: "item 3 ", description: "ZContextMenu Item 3" },
                    { item: "item 4 ", description: "ZContextMenu Item 4" },
                    { divider: true },
                    { item: "item 5 ", description: "ZContextMenu Item 5" },
                    { item: "item 6 ", description: "ZContextMenu Item 6" },
                    { item: "item 7 ", description: "ZContextMenu Item 7" },
                    { item: "item 8 ", description: "ZContextMenu Item 8" },
                    { item: "item 9 ", description: "ZContextMenu Item 9" },
                    { divider: true },
                    { item: "item X ", description: "The Extra Item" },
                  ]}
                />
              </Comment>
              <GridContextMenu
                base={this.props.base}
                canZoomOut={canZoomOut}
                channel={channel}
                columnIndex={contextMenuCoords[1]}
                columns={columns}
                // columns={this.props.columns}
                contextMenu={contextMenu}
                expression={expression}
                id={channel + "gridContextMenu"}
                dataLayer={this.props.dataLayer}
                gridName={this.props.gridName}
                orientation={orientation}
                // orientation={this.props.orientation}
                onZoomIn={this.props.onZoomIn}
                resetContextMenuCoords={this.props.resetContextMenuCoords}
                rowIndex={contextMenuCoords[0]}
                rtype={this.props.rtype}
                send={send}
                type={type}
                updateCurrentContextMenu={updateCurrentContextMenu}
                zoomOut={zoomOut}
              />
            </div>
          </div>
        )}
      </ScrollSync>
    );
  }
}
