///
/// CommandLine.js
///
/// ZS command line component (created by Main)
///
/// LRQS 4 - needs more review
///

import React from "react";
import classnames from "classnames";

import parsing from "./extra/parsing";
import interpretValue from "./extra/interpretValue";

import ASCII from "../constants/ascii.js";
import CHANNEL from "../constants/channel";
import logger from "../logger/logger";
import parse from "zll/Parser";

let interval = 256;
let commands = [];

const CR = ASCII.CR;
const ESC = ASCII.ESC;

const debugging = true;

class CommandLine extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      channel: CHANNEL.ZS_VIZ,
      expression: "",
      value: "",
      response: "",
    };

    this._changeValueWithFormula = this._changeValueWithFormula.bind(this);
    this._handleRequest = this._handleRequest.bind(this);
    this._onChangeValue = this._onChangeValue.bind(this);
    this._onCommandLineKeyUp = this._onCommandLineKeyUp.bind(this);
    this._onBlur = this._onBlur.bind(this);
    this._resetCommandLineValue = this._resetCommandLineValue.bind(this);
    this._recycleResponse = this._recycleResponse.bind(this);
    this._sendRequest = this._sendRequest.bind(this);
    this._timerHandler = this._timerHandler.bind(this);
    this.timer = null;
  }

  componentDidUpdate(prevProps) {
    const {
      commandLineResponse,
      errmsg,
      scriptCommands,
      wideEditor,
    } = this.props;

    if (commandLineResponse !== prevProps.commandLineResponse) {
      this.setState({ response: commandLineResponse });
    }

    if (scriptCommands !== prevProps.scriptCommands) {
      commands = scriptCommands;
      this.timer = setTimeout(this._timerHandler, interval);
    }

    if (errmsg !== prevProps.errmsg) {
      logger.debug("errmsg !== prevProps.errmsg: " + errmsg);
    }

    if (wideEditor && wideEditor !== prevProps.wideEditor) {
      this._changeValueWithFormula(
        prevProps.editingChannel,
        prevProps.editingExpression
      );
      this.refs.Command.focus();
    }
  }

  _resetCommandLineValue() {
    const { uiActions } = this.props;

    uiActions.toggleWideEditor(false);
    this.setState({ channel: CHANNEL.ZS_VIZ, expression: "", value: "" });
  }

  _sendRequest() {
    const request = this.refs.Command.value;
    const ast = parse(request, "CommandLine::_sendRequest");
    if (!ast || ast.is_error()) {
      const error = `ERROR: syntax error: '${request}'`;
      this.setState({ response: error });
    } else {
      if (debugging) {
        logger.debug(request);
        const value = ast.regen();
        logger.debug(value);
        const result = interpretValue(value);
        logger.debug(result);
      }
      this._handleRequest(request);
    }
  }

  // Review _onBlur and _onCommandLineKeyUp functionality

  _onBlur() {
    const { assign, wideEditor } = this.props;
    const { channel, expression: lhs, value: rhs } = this.state;

    if (wideEditor) {
      assign(channel, lhs, rhs);
      this._resetCommandLineValue();
    }
  }

  _onCommandLineKeyUp(event) {
    const { assign, wideEditor } = this.props;
    const { channel, expression: lhs, value: rhs } = this.state;

    if (event.keyCode === CR) {
      if (wideEditor) {
        assign(channel, lhs, rhs);
        this._resetCommandLineValue();
      } else {
        this._sendRequest();
      }
    } else if (event.keyCode === ESC) {
      if (wideEditor) {
        this._resetCommandLineValue();
      }
    }
  }

  /****************************************************************************/

  _updateAreaLabel(request, action, expression, channel, start, end) {
    const { areaLabel, areaLabelActions } = this.props;

    let newExpression = expression;

    if (action === "edit_label" || action === "commit_label") {
      channel = areaLabel.channel;
      expression = areaLabel.oldExpression;

      newExpression =
        action === "edit_label"
          ? request.substring(start, end)
          : areaLabel.newExpression;
    }

    areaLabelActions.edit(true, action, channel, expression, newExpression);
  }

  _updateAreaValue(action, activeLabelName, str, ch) {
    const { uiActions, assignment } = this.props;

    let label = assignment.label; // tab label
    let expression = assignment.expression;
    let channel = assignment.channel;
    let rowIndex = assignment.rowIndex;
    let columnIndex = assignment.columnIndex;
    let value = assignment.value;

    if (action === "select") {
      label = activeLabelName;
      expression = str;
      channel = ch;

      if (parsing.is_array_index(expression)) {
        rowIndex = parsing.get_row_index(expression);
        columnIndex = parsing.get_column_index(expression);
      }
    } else if (action === "edit") {
      value = str;
    }

    uiActions.editAreaValue({
      action: action,
      label: label,
      expression: expression,
      channel: channel,
      rowIndex: rowIndex,
      columnIndex: columnIndex,
      value: value,
    });

    if (action === "commit") {
      uiActions.resetAreaValue();
    }
  }

  _setMCparams(request) {
    logger.log(request);

    const { uiActions } = this.props;

    const start = request.indexOf("[") + 1;
    const end = request.indexOf("]");
    const args = request.substring(start, end).replace(/'/g, "");
    const argsArray = args.split(", ");

    uiActions.editBuckets(parseInt(argsArray[0]));
    uiActions.editLowerBound(argsArray[1]);
    uiActions.editUpperBound(argsArray[2]);
    uiActions.editIterations(parseInt(argsArray[3]));
    uiActions.editInterval(parseInt(argsArray[4]));
  }

  _handleUInteractionCommand(request) {
    const {
      allTabsContent,
      selected,
      send,
      tabContentAction,
      tabLabelAction,
      tabs,
      uiActions,
    } = this.props;

    const command = request.split(" ");
    const action = command[1]; // action
    const activePanel = allTabsContent[selected] || {};
    const start = request.indexOf("'") + 1;
    const end = request.indexOf("'", start);
    const expression = request.substring(start, end);
    const channel = parsing.get_expr_channel(expression, activePanel);

    let label = tabs[selected] ? tabs[selected].label : "";

    const tabLabelInteraction =
      action === "add" || action === "switch" || action === "remove";
    const tabContentInteraction = action === "content";
    const areaLabelInteraction =
      action === "select_label" ||
      action === "edit_label" ||
      action === "commit_label";
    const areaValueInteraction =
      action === "select" || action === "edit" || action === "commit";

    if (action === "test") {
      const name = request.substring(start, end);
      send("_handleUInteractionCommand", name, CHANNEL.SYSTEM); // !! special case (warning: no channel originally)
    } else if (action === "default_delay") {
      const mseconds = command[2];
      interval = Number(mseconds);
      uiActions.updateDefaultDelay(interval);
    } else if (action === "delay") {
      const mseconds = command[2];
      interval = Number(mseconds);
    } else if (tabLabelInteraction) {
      label = request.substring(start, end);
      tabLabelAction(label, action);
    } else if (tabContentInteraction) {
      const contentAction =
        request.indexOf("content +") !== -1 ? "sub" : "unsub";
      const coordinates = [];
      tabContentAction(label, contentAction, expression, channel, coordinates);
    } else if (areaLabelInteraction) {
      this._updateAreaLabel(request, action, expression, channel, start, end);
    } else if (areaValueInteraction) {
      this._updateAreaValue(action, label, expression, channel);
    } else if (action === "params") {
      this._setMCparams(request);
    } else if (action === "run_MC") {
      uiActions.runMCButton(true);
    } else if (action === "zoom_out") {
      uiActions.updateZoomOutChannel(Number(channel));
    }
  }

  _handleRequest(request) {
    const { allTabsContent, send, tabs } = this.props;

    if (request.startsWith("@@")) {
      this._handleUInteractionCommand(request);
    } else {
      if (request.indexOf("mc.") !== -1) {
        let tabLabel = "Monte Carlo";
        let startRep = request.indexOf('"') + 1; // !! Rep
        let endRep = request.lastIndexOf('"');
        let expression = request.substring(startRep, endRep).trim();

        let index = -1;
        let tabIndex = -1;

        // OO flag for improvement
        for (const tab of tabs) {
          ++index;
          if (tab.label === tabLabel) {
            tabIndex = index;
          }
        }

        if (tabIndex === -1) {
          // !! a possibility missing in original code
          alert("MCPanel tab not found in mc request");
          return;
        }

        const MCPanel = allTabsContent[tabIndex];

        let channel = null;

        for (const key of Object.keys(MCPanel)) {
          if (MCPanel[key].expression === expression) {
            channel = MCPanel[key].channel;
          }
        }

        if (!channel) {
          alert("null channel in MC request");
          return;
        }

        // !! suspicious: looks ugly
        if (expression !== channel) {
          request = request.replace(
            '"' + expression + '"',
            '"' + channel + '"'
          );
        }
      }
      send("_handleRequest", request, CHANNEL.ZS_VIZ);
    }
  }

  /************************************************************************/

  _recycleResponse() {
    this.setState({ response: "" });
  }

  /************************************************************************/

  _changeValueWithFormula(channel, expression) {
    this.setState({
      channel: channel,
      expression: expression,
      value: this.props.formulaInput,
    });
  }

  _onChangeValue(event) {
    const inputValue = event.target.value;

    this.setState({
      value: inputValue,
    });
  }

  _timerHandler() {
    const first = commands.shift();

    if (first) {
      if (first.indexOf("delay") !== -1) {
        let command = first.split(" ");
        let mseconds = command[2];
        interval = mseconds;
      } else {
        interval = this.props.defaultDelay;
      }

      this._handleRequest(first);
      this.timer = setTimeout(this._timerHandler, interval);
    } else {
      this.setState({ response: "Automated interaction completed" });
    }
  }

  render() {
    const { wideEditor } = this.props;
    const { response } = this.state;

    const resBgColor = response.startsWith("ERROR:") ? "#f0a0a0" : "#a0f0a0";

    return (
      <div id="bottom">
        <footer id="control">
          <div className="command-line-container">
            <div id="prompt" style={{ display: wideEditor ? "block" : "none" }}>
              <p>{this.state.expression}</p>
            </div>
            <input
              className={classnames("in", wideEditor ? "wide-editor" : "")}
              id="command-line"
              onBlur={this._onBlur}
              onChange={this._onChangeValue}
              onKeyUp={this._onCommandLineKeyUp}
              ref="Command"
              style={{ borderLeft: wideEditor ? "4px solid #5e7be6" : "none" }}
              type="text"
              value={this.state.value}
            />
          </div>

          <div className="command-line-container">
            <input
              className={"in"}
              id="respone-line"
              ref="Response"
              readOnly
              style={{ borderLeft: "none", backgroundColor: resBgColor }}
              type="text"
              value={response}
            />
            <button
              style={{ paddingTop: "0" }}
              className="fa fa-recycle header-icon"
              onClick={this._recycleResponse}
            />
          </div>

          <div className="copyright">Copyright &copy; Lakebolt Research</div>
        </footer>
      </div>
    );
  }
}

export default CommandLine;
