import CHANNEL from "../constants/channel";

// Action types

const SET_EDITING_STATUS = "SET_EDITING_STATUS";
const RESET_EDITING_STATUS = "RESET_EDITING_STATUS";

// Action constructors

export function setEditingStatus(editing, channel, formula, expression) {
  // Called by Main.js
  return { type: SET_EDITING_STATUS, editing, channel, formula, expression };
}

export function resetEditingStatus() {
  return { type: RESET_EDITING_STATUS };
}

// Reducer

const INITIAL_STATE = {
  editing: false,
  channel: CHANNEL.NONE,
  formula: undefined,
  expression: "",
};

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case SET_EDITING_STATUS:
      return {
        editing: action.editing,
        channel: action.channel,
        formula: action.formula,
        expression: action.expression,
      };

    case RESET_EDITING_STATUS:
      return {
        editing: false,
        channel: CHANNEL.NONE,
        formula: undefined,
        expression: "",
      };

    default:
      // console.error(`editing.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
