// Action types

const EDIT_TAB_SELECTED = "EDIT_TAB_SELECTED";

// Action constructors

export function editSelected(number) {
  return { type: EDIT_TAB_SELECTED, number };
}

// Reducer

function reducer(state = { selected: -1 }, action) {
  switch (action.type) {
    case EDIT_TAB_SELECTED:
      return {
        selected: action.number,
      };

    default:
      // console.error(`tabSelected.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
