///
/// TokenCodes.js
///
/// Lilly token codes (enumeration)
///
/// LRQS 7
///

let tkc = 256;

// Token codes (enumeration)

const TC = {
  BOOLEAN: ++tkc,
  INTEGER: ++tkc,
  DOUBLE: ++tkc,
  STRING: ++tkc,
  SYMBOL: ++tkc,
  ERROR: ++tkc,

  OP_FIRST: tkc + 1,

  META_YIELDS: ++tkc,

  COLONCOLON: ++tkc,
  ASSIGN: ++tkc,
  DEFINE: ++tkc,

  LARROW: ++tkc,
  RARROW: ++tkc,
  YIELDS: ++tkc,
  LAMBDA: ++tkc,
  OR: ++tkc,
  AND: ++tkc,
  LT: ++tkc,
  LE: ++tkc,
  NE: ++tkc,
  EQ: ++tkc,
  GE: ++tkc,
  GT: ++tkc,
  DOTDOT: ++tkc,
  PLUS: ++tkc,
  MINUS: ++tkc,
  BWOR: ++tkc,
  BWXOR: ++tkc,
  STAR: ++tkc,
  SLASH: ++tkc,
  PERCENT: ++tkc,
  BWAND: ++tkc,
  RAISED: ++tkc,
  BWNOT: ++tkc,
  EXCLAMATION: ++tkc,
  COLON: ++tkc,
  DOT: ++tkc,
  LBRACE: ++tkc,
  RBRACE: ++tkc,
  LBRACKET: ++tkc,
  RBRACKET: ++tkc,
  LPAREN: ++tkc,
  RPAREN: ++tkc,
  SQUOTE: ++tkc,
  COMMA: ++tkc,
  NOT: ++tkc,
  QUESTION: ++tkc,
  SEMICOLON: ++tkc,

  OP_LAST: tkc + 1,

  // Reserved words

  BOOL_T: ++tkc,
  INT_T: ++tkc,
  DOUBLE_T: ++tkc,
  STRING_T: ++tkc,
  ARRAY_T: ++tkc,
  STRUCT_T: ++tkc,
  FUNCTION_T: ++tkc,
  VAR_T: ++tkc,
  LAZY_T: ++tkc,

  END: -1,
};

module.exports = TC;
