///
/// TabContent.js
///
/// Component created by TabPanel
///
/// LRQS 5
///

import React from "react";
import GridLayout from "react-grid-layout";

import AreaContainer from "./AreaContainer";
import DroppableGrid from "../gui/DroppableGrid";
import MonteCarloSetUpContainer from "./MonteCarloSetUpContainer";
import VariableDefinition from "../dialogs/VariableDefinition";

import CHANNEL from "../constants/channel";
import labelsAux from "./tabLabelsX";
import logger from "../logger/logger";
import panel from "./extra/tabContentDispatchProperties";
import UI from "../constants/UI";

class TabContent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      varDefType: null,
      showDialog: false,
      activeLayout: [],
      disabledRun: false,
      gridRows: null,
      gridCols: null, // number of columns per row for the react-grid-layout component
      channelNumber: CHANNEL.START,
    };

    this.onDrop = this.onDrop.bind(this);
    this._openDialog = this._openDialog.bind(this);
    this.closeDialog = this.closeDialog.bind(this);
    this.submitDialog = this.submitDialog.bind(this);
    this._variableDefinitionDialog = this._variableDefinitionDialog.bind(this);
    this._renderArea = this._renderArea.bind(this);
    this.disableRunButton = this.disableRunButton.bind(this);
    this.onLayoutChange = this.onLayoutChange.bind(this);
    this.removeArea = this.removeArea.bind(this);
    this.resize = this.resize.bind(this);
    this.zoomIn = this.zoomIn.bind(this);
    this.zoomOut = this.zoomOut.bind(this);
    this._updateLayout = this._updateLayout.bind(this);
  }

  componentDidMount() {
    this.resize();
    window.addEventListener("resize", this.resize);
  }

  resize() {
    const component = this.refs.tabsContent;
    if (!component) {
      // it happens when no tabs are present
      // alert(`component is ${component} in TabContent::resize`);
      return;
    }
    const clientW = component.clientWidth;
    const clientH = component.clientHeight;
    this.setState({ gridRows: Math.floor(clientH / UI.RGL_SZ) });
    this.setState({ gridCols: Math.floor(clientW / UI.RGL_SZ) });
  }

  componentDidUpdate(prevProps) {
    const { contentAction, zoomOutChannel } = this.props;
    const scrollHeight = this.refs.tabsContent.scrollHeight;
    logger.debug(`scrollHeight = ${scrollHeight}`);

    if (contentAction !== prevProps.contentAction) {
      const action = contentAction.action;
      const label = contentAction.label;
      const expression = contentAction.expression;
      const channel = contentAction.channel;
      const coords = contentAction.coordinates;

      switch (action) {
        case "sub":
          this._newTabContent(label, expression, coords);
          break;
        case "unsub":
          this.removeArea(label, expression, channel);
          break;
        default:
          logger.error(
            `TabContent::componentDidUpdate: unexpected action ${action}`
          );
      }
    }

    if (zoomOutChannel !== prevProps.zoomOutChannel) {
      this.zoomOut(zoomOutChannel);
    }
  }

  _newTabContent(tabLabel, expression, coords) {
    logger.debug(`_newTabContent(${tabLabel}, ${expression}, ${coords})`);
    const channel = this.state.channelNumber;

    this._createView(channel, expression);
    this._createArea(channel, expression, tabLabel);
    if (coords.length > 0) {
      this._updateLayout(channel, coords, tabLabel);
    }

    if (!expression) {
      this.props.areaLabelActions.toggleEditingStatus(true, channel);
    }
  }

  _updateLayout(channel, coords, tabLabel) {
    const { tabs, tabLabelsActions } = this.props;
    const tabIndex = labelsAux.getTabIndex(tabLabel, tabs);

    const x = coords[0];
    const y = coords[1];
    const w = coords[2];
    const h = coords[3];

    let layoutCopy = tabs[tabIndex].layout;
    let existsArea = false;

    layoutCopy = layoutCopy.map((g) => {
      if (g.i === channel) {
        existsArea = true;
        return { ...g, x: x, y: y, w: w, h: h };
      } else {
        return g;
      }
    });

    if (layoutCopy.length === 0 || !existsArea) {
      layoutCopy.push({
        i: String(channel),
        x: x,
        y: y,
        w: w,
        h: h,
        minW: 1,
        minH: 1,
      });
    }

    tabLabelsActions.editLayout(tabs[tabIndex].id, layoutCopy);
  }

  _createView(channel, expression) {
    const { createView, handleSubscriptions } = this.props;

    createView(channel);
    if (expression) {
      handleSubscriptions(channel, "", expression);
    }
    this.setState({ channelNumber: channel + 1 });
  }

  _createArea(channel, expression, tabLabel) {
    const {
      tabs,
      selected,
      tabLabelsActions,
      dispatch,
      resumeView,
      modelDetailsActions,
    } = this.props;

    let tabIndex = labelsAux.getTabIndex(tabLabel, tabs);
    if (tabIndex === -1) return;

    panel(tabIndex)(dispatch).addTabElem(channel, expression);

    let tabID = tabs[tabIndex].id;
    let tabsCopy = tabs[tabIndex].childrenViews;
    tabsCopy.push(channel);
    tabLabelsActions.updateChildrenViews(tabID, tabsCopy); // views of the panel

    modelDetailsActions.resetElemDetails(channel);

    if (selected === tabIndex) {
      resumeView(channel);
    }
  }

  // Data is an object with 3 possible keys: drag, definition, or expression
  // Those are the drag types accepted
  onDrop(data) {
    const { tabs, selected } = this.props;

    if (data.definition) {
      this.setState({ showDialog: true });
      this.setState({ varDefType: data.definition });
      return;
    }

    const tabLabel = tabs[selected]
      ? tabs[selected].label
      : this.props.createDefaultTab();

    // The expression is in data.drag and not in data.expression: it's a drag!
    const expression = data.drag;
    this._newTabContent(tabLabel, expression, []);
  }

  // Remove element from the view
  removeArea(tabLabel, expression, channel) {
    const {
      allTabsContent,
      dispatch,
      modelDetailsActions,
      removeView,
      tabLabelsActions,
      tabs,
    } = this.props;

    const tabIndex = labelsAux.getTabIndex(tabLabel, tabs);
    if (tabIndex === -1) return;

    const activePanel = allTabsContent[tabIndex];
    const tabID = tabs[tabIndex].id;
    let tabsCopy = tabs[tabIndex].childrenViews;

    if (channel === CHANNEL.NONE) {
      for (let ch in activePanel) {
        if (activePanel[ch].expression === expression) {
          channel = ch;
        }
      }
      if (channel === CHANNEL.NONE) return;
    }

    if (
      activePanel[channel] &&
      activePanel[channel].expression === expression
    ) {
      modelDetailsActions.deleteElemDetails(channel);
      panel(tabIndex)(dispatch).removeTabElem(channel);

      tabsCopy = tabsCopy.filter((elem) => elem !== channel);
      tabLabelsActions.updateChildrenViews(tabID, tabsCopy);

      if (tabLabel === "Monte Carlo") {
        if (expression !== "@MC") {
          panel(tabIndex)(dispatch).removeTabElem("@MC");
        }
      }

      removeView(channel, expression);
    }
  }

  /// zoom section begin

  _zoom(stackCopy, unsubExp, subExp, channel) {
    const {
      selected,
      dispatch,
      modelDetailsActions,
      handleSubscriptions,
    } = this.props;

    panel(selected)(dispatch).resetElemProps(channel);
    panel(selected)(dispatch).editExpression(channel, { expression: subExp });
    panel(selected)(dispatch).editElemStack(channel, { stack: stackCopy });

    modelDetailsActions.resetElemDetails(channel);

    handleSubscriptions(channel, unsubExp, subExp);
  }

  zoomOut(channel) {
    const { allTabsContent, selected } = this.props;

    let stackCopy = allTabsContent[selected][channel].stack;
    let expression = allTabsContent[selected][channel].expression;
    let parent = stackCopy.pop();

    this._zoom(stackCopy, expression, parent, channel);
  }

  zoomIn(parent, expression, channel) {
    const { allTabsContent, selected } = this.props;

    let stackCopy = allTabsContent[selected][channel].stack;
    stackCopy.push(parent);

    this._zoom(stackCopy, parent, expression, channel);
  }

  /// zoom section end

  onLayoutChange(layout) {
    const { tabs, selected } = this.props;

    // adjust the height and width - beware type mismatch requiring number to string conversion for h
    layout = layout.map((g) => {
      if (g.h < 2) {
        return {
          ...g,
          h: g.i === "" + CHANNEL.SYSTEM_MC ? UI.MC_HEIGHT : UI.MIN_AREA_HEIGHT,
          w: this.state.gridCols,
        };
      } else {
        return g;
      }
    });

    this.setState({ activeLayout: layout });
    if (tabs[selected]) {
      this.props.tabLabelsActions.editLayout(tabs[selected].id, layout);
    }
  }

  disableRunButton(disable) {
    this.setState({ disabledRun: disable });
  }

  _renderArea(key, channel, activePanel) {
    const { changeExpressionInArea, dispatch, modelDetails, selected, send, showMessage } = this.props;

    const { gridRows, gridCols } = this.state;

    return (
      <AreaContainer
        gridRes={UI.RGL_SZ}
        gridRows={gridRows}
        gridCols={gridCols}
        key={key}
        disableRunButton={this.disableRunButton}
        elem={activePanel[channel]}
        elemDetails={modelDetails[channel]}
        changeExpressionInArea={changeExpressionInArea}
        removeArea={this.removeArea}
        removeTabElem={panel(selected)(dispatch).removeTabElem}
        assign={this.props.assign}
        send={send}
        showMessage={showMessage}
        zoomIn={this.zoomIn}
        zoomOut={this.zoomOut}
      />
    );
  }

  _renderAreaList(activePanel, contextMenu) {
    let list = [];
    for (const key of Object.keys(activePanel)) {
      const channel = key;
      const objKey = channel;
      // @@ contextMenu appears to have no effect on the context menu!
      // const style = { zIndex: channel === contextMenu ? 1 : 0 }; // zIndex excluded
      // const style = { position: "relative", zIndex: 0 }; // zIndex excluded
      const style = {};
      list.push(
        <div key={objKey} style={style}>
          {this._renderArea(key, channel, activePanel)}
        </div>
      );
    }
    return list;
  }

  ///
  /// Variable Definition Dialog
  ///

  _openDialog() {
    this.setState({ showDialog: true });
  }

  closeDialog() {
    this.setState({ showDialog: false });
  }

  submitDialog(payload, addArea) {
    const { symbol, definition } = payload;
    if (definition) {
      this.props.addDefinition(symbol, definition, addArea);
    }
    this.closeDialog();
  }

  _variableDefinitionDialog() {
    return (
      <VariableDefinition
        model={this.props.model}
        type={this.state.varDefType}
        showMessage={this.props.showMessage}
        onClose={this.closeDialog}
        onSubmit={this.submitDialog}
        addArea={true}
      ></VariableDefinition>
    );
  }

  render() {
    const {
      allTabsContent,
      areaLabel,
      contextMenu,
      dragStatus,
      editing,
      selected,
      send,
      showMessage,
      tabs,
      runMonteCarlo,
    } = this.props;

    const {
      showDialog,
      disabledRun,
      gridRows,
      gridCols,
      channelNumber,
    } = this.state;

    const activePanel = allTabsContent[selected] || [];
    const editingAreaLabel = areaLabel.isEditing;
    const editingAreaValue = editing;
    const editingArea = editingAreaLabel || editingAreaValue;

    return (
      <div
        className="tabs-content"
        ref="tabsContent"
        style={{ position: "relative" }}
      >
        {0 < tabs.length && selected !== -1 && tabs[selected] ? (
          tabs[selected].label === "Monte Carlo" ? (
            <MonteCarloSetUpContainer
              disabledRun={disabledRun}
              channelNumber={channelNumber}
              showMessage={showMessage}
              send={send}
              runMonteCarlo={runMonteCarlo}
            />
          ) : null
        ) : null}

        {showDialog ? this._variableDefinitionDialog() : null}

        <GridLayout
          className="layout"
          cols={gridCols}
          compactType={null}
          onLayoutChange={this.onLayoutChange}
          layout={tabs[selected] ? tabs[selected].layout : []}
          margin={[0, 0]}
          rowHeight={UI.RGL_SZ} // assumes we have a grid of squares
          width={gridCols * UI.RGL_SZ} // width of the react-grid-layout container
          isDraggable={true}
          // draggableHandle workaround: https://github.com/STRML/react-grid-layout/issues/240
          draggableHandle={editingArea ? ".doesnt-exist" : null}
        >
          {this._renderAreaList(activePanel, contextMenu)}
        </GridLayout>
        <DroppableGrid
          width={UI.RGL_SZ}
          height={UI.RGL_SZ}
          onDrop={this.onDrop}
          dragStatus={dragStatus}
          gridRes={UI.RGL_SZ}
          gridRows={gridRows}
          gridCols={gridCols}
          showGridBorders={this.props.showGridBorders}
        />
      </div>
    );
  }
}

export default TabContent;
