import React from "react";
import { ContextMenuTrigger } from "react-contextmenu";
import ContextMenu from "../gui/ContextMenu";

import Comment from "../gui/Comment";
import FileDialog from "../dialogs/File-Dialog-1";
import UI from "../constants/UI";

const cycles = [2000, 1000, 500, 333, 250, 200, 125, 100];

/*
const options = cycles.map(cycle => ({
  value: cycle.toString(),
  label: cycle.toString() + " ms",
}));
*/

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      monitor: true,
      showModal: false,
      showCaptions: true,
      showReload: true,
      showReset: true,
      action: "",
      cycle: cycles[2],
    };

    this._performAction = this._performAction.bind(this);
    this._closeModal = this._closeModal.bind(this);
    this._handleClick = this._handleClick.bind(this);
    this._onTickClick = this._onTickClick.bind(this);
    this._onCycleChange = this._onCycleChange.bind(this);
    this._onCycleClick = this._onCycleClick.bind(this);
    this._onReloadClick = this._onReloadClick.bind(this);
    this._onResetClick = this._onResetClick.bind(this);
    this._toggleMonitor = this._toggleMonitor.bind(this);
  }

  // Modal

  _openModal(action) {
    this.setState({ showModal: true, action: action });
  }

  _closeModal() {
    this.setState({ showModal: false });
  }

  _performAction(modelName) {
    const { action } = this.state;

    while (modelName.endsWith(".sym")) {
      modelName = modelName.substr(0, modelName.length - 4);
    }

    if (modelName !== "") {
      if (action === "save") {
        this.props.save(modelName);
      } else if (action === "load") {
        this.props.load(modelName);
      }
    }

    this._closeModal();
  }

  // File operations

  modal() {
    const { showModal, action } = this.state;

    return (
      <FileDialog
        modelName={this.props.modelName}
        show={showModal}
        action={action}
        onClose={this._closeModal}
        onAction={this._performAction}
      ></FileDialog>
    );
  }

  // Tick / Cycle

  _onTickClick() {
    this.props.cycle(0);
    this.props.tick();
  }

  _onCycleClick() {
    const cycle = this.state.cycle;
    // console.log(`this.props.cycle(${cycle})`);
    this.props.cycle(cycle);
  }

  _onCycleChange(option) {
    // console.log(` Selected: ${option}`);
    const cycle = parseInt(option.value);
    // console.log(`this.props.cycle(${cycle})`);
    this.props.cycle(cycle);
    this.setState({ cycle });
  }

  // Reset / Reload

  _onReloadClick() {
    this.props.reload("Header::_onReloadClick");
  }

  _onResetClick() {
    this.props.reset();
    this.setState({ action: "" });
  }

  // Latency Monitor

  _toggleMonitor() {
    const monitor = !this.state.monitor;
    const delay = monitor ? 2000 : -1;
    this.setState({ monitor });
    this.props.monitor(delay);
  }

  // Context Menu Experiments (remove)

  _handleClick(event, data) {
    alert(`Clicked on ${data.item}`);
  }

  // Header

  render() {
    const { modelName, toggleGridBorders, setCreateMC } = this.props;

    const { showCaptions, showReload, showReset } = this.state;

    return (
      <div id="header-section">
        <header>
          <ul className="right-header-menu" style={{ float: "right" }}>
            <Comment> Title </Comment>
            <li
              style={{
                float: "left",
                paddingRight: "128px",
                fontSize: "24px",
                color: "#aa9933",
              }}
            >
              {`ZenSheet™ Studio α ${modelName ? ` { ${modelName} }` : ""}`}
            </li>
            <Comment> Test </Comment>
            <li onClick={this.props.runInteraction}>
              <i
                style={{ paddingTop: "0" }}
                className="fa fa-play-circle-o header-icon"
              />
              {showCaptions ? <span>Test</span> : null}
            </li>
            <Comment> Load </Comment>
            {UI.INTERMEDIATE <= UI.MODE ? (
              <li onClick={this._openModal.bind(this, "load")}>
                <i
                  style={{ paddingTop: "0" }}
                  className="fa fa-upload header-icon"
                />
                {showCaptions ? <span>Load</span> : null}
              </li>
            ) : null}
            <Comment> Save </Comment>
            <li onClick={this._openModal.bind(this, "save")}>
              <i
                style={{ paddingTop: "0" }}
                className="fa fa-download header-icon"
              />
              {showCaptions ? <span>Save</span> : null}
            </li>
            <Comment> Tick </Comment>
            <li onClick={this._onTickClick}>
              <i
                style={{ paddingTop: "0" }}
                className="fa fa-clock-o header-icon"
              />
              {showCaptions ? <span>Tick</span> : null}
            </li>
            <Comment> Cycle </Comment>
            <li onClick={this._onCycleClick}>
              <i
                style={{ paddingTop: "0" }}
                className="fa fa-bolt header-icon"
              />
              {showCaptions ? <span>Cycle</span> : null}
            </li>

            <Comment> Cycle Selection </Comment>
            <Comment>
            </Comment>
            <Comment> RTL </Comment>
            <li onClick={this._toggleMonitor}>
              <label> RTL </label>
              <input
                disabled
                type="text"
                value={this.state.monitor ? this.props.rtl : "OFF"}
                style={{
                  width: "64px",
                  textAlign: "right",
                  backgroundColor: "black",
                  color: "red",
                }}
              />
            </li>
            <Comment> Reload </Comment>
            {UI.INTERMEDIATE <= UI.MODE && showReload ? (
              <li id="refresh-fa" onClick={this._onReloadClick}>
                <i
                  style={{ paddingTop: "0", color: "#ffad33" }}
                  className="fa fa-refresh header-icon"
                />
                {showCaptions ? <span>Reload</span> : null}
              </li>
            ) : null}
            <Comment> Reset </Comment>
            {showReset ? (
              <li id="reset-fa" onClick={this._onResetClick}>
                <i
                  style={{ paddingTop: "0" }}
                  className="fa fa-undo header-icon"
                />
                {showCaptions ? <span>Reset</span> : null}
              </li>
            ) : null}
            <Comment> Snapping Grid </Comment>
            {true ? (
              <li id="snapping-grid" onClick={toggleGridBorders}>
                <i
                  className="fa fa-th"
                  style={{ paddingTop: "0", color: "#ffad33" }}
                />
              </li>
            ) : null}
            <Comment> MC </Comment>
            {true ? (
              <li id="montecarlo" onClick={() => setCreateMC(true)}>
                <i
                  className="fa fa-bar-chart"
                  style={{ paddingTop: "0", color: "#ffad33" }}
                />
              </li>
            ) : null}

            <Comment> Settings </Comment>
            <li>
              <ContextMenuTrigger id="zcontext-menu">
                <i
                  style={{ paddingTop: "0" }}
                  className="fa fa-cogs header-icon"
                />
              </ContextMenuTrigger>
            </li>
            <ContextMenu
              id="zcontext-menu"
              handler={this._handleClick}
              menu={[
                { item: "item 0 ", description: "ZContextMenu Item 0" },
                { item: "item 1 ", description: "ZContextMenu Item 1" },
                { item: "item 2 ", description: "ZContextMenu Item 2" },
                { item: "item 3 ", description: "ZContextMenu Item 3" },
                { item: "item 4 ", description: "ZContextMenu Item 4" },
                { divider: true },
                { item: "item 5 ", description: "ZContextMenu Item 5" },
                { item: "item 6 ", description: "ZContextMenu Item 6" },
                { item: "item 7 ", description: "ZContextMenu Item 7" },
                { item: "item 8 ", description: "ZContextMenu Item 8" },
                { item: "item 9 ", description: "ZContextMenu Item 9" },
                { divider: true },
                { item: "item X ", description: "The Extra Item" },
              ]}
            />
          </ul>
        </header>
        {this.modal()}
      </div>
    );
  }
}

export default Header;

