import React from "react";
import * as d3 from "d3";

let messages = [];

class MonteCarlo extends React.Component {
  constructor(props) {
    super(props);
    this.paintHistGraph = this.paintHistGraph.bind(this);
    this.timer = null;
  }

  componentDidMount() {
    messages.push(this.props.message);
    this.paintHistGraph(0);
    this.props.disableRunButton(false);
  }

  componentDidUpdate(prevProps, prevState) {
    const { message } = this.props;

    if (prevProps.message !== this.props.message) {
      if (messages.length === 0) {
        messages.push(message);
        this.paintHistGraph(0);
      } else {
        messages.push(message);
      }
    }
  }

  componentWillUnmount() {
    clearTimeout(this.timer);
  }

  // build array of key-value pairs
  getData(itMessage) {
    const chunks = itMessage.split("==> [")[1].split("]")[0].split(",");

    let i = 0;
    let buckets = [];
    while (i < chunks.length) {
      const key = i === 0 ? "-oo" : parseFloat(chunks[i].split("(")[1].trim());
      const value = parseInt(chunks[i + 2].split(")")[0].trim());
      const obj = { key: key, value: value };
      buckets.push(obj);
      i += 3;
    }

    return buckets;
  }

  paintHistGraph(index) {
    const { variableName } = this.props;

    let NUM_ADDED_TO_DOMAIN = null;
    let MARGIN_TOP = null;
    let MARGIN_LEFT = null;
    let barWidth = null;
    let labelX = null;
    let labelY = null;
    let id = null;
    let vis = null;
    let height = null;
    let y = null;
    let linesWidth = null;
    let yAxis = null;
    let chart = null;
    let bar = null;

    if (index !== messages.length) {
      let itMessage = messages[index];

      if (itMessage[1] !== "}") {
        let node = document.getElementById("bar" + variableName);
        if (node != null) {
          node.parentNode.removeChild(node);
        }

        let array = this.getData(itMessage); // Histogram data

        let tmpMax = Math.max.apply(
          Math,
          array.map(function (d) {
            return d.value;
          })
        );

        NUM_ADDED_TO_DOMAIN = 50;
        MARGIN_TOP = 100;
        MARGIN_LEFT = 100;

        barWidth = 34.5;

        labelX = 5;
        labelY = 270;
        id = "bar" + variableName;

        vis = d3
          .select("#histogram" + variableName)
          .append("svg")
          .attr("class", "chart")
          .attr("style", "padding-top: 20px")
          .attr("id", id);

        height = 330 * 2;

        y = d3.scaleLinear().range([height / 2, 0]);
        y.domain([0, parseInt(tmpMax) + NUM_ADDED_TO_DOMAIN]);

        linesWidth = -(array.length * barWidth);

        yAxis = d3.axisLeft(y).tickSizeOuter(linesWidth).tickPadding(10);

        chart = d3
          .select("#" + id)
          .attr("width", barWidth * array.length + MARGIN_LEFT)
          .attr("height", height / 1.4);

        bar = chart
          .selectAll("g")
          .data(array)
          .enter()
          .append("g")
          .attr("transform", function (d, i) {
            return `translate(${i * barWidth}, ${MARGIN_TOP})`;
          })
          .attr("stroke", "#fff");

        chart
          .append("g")
          .attr("class", "y axis")
          .call(yAxis)
          .attr("transform", `translate(${MARGIN_LEFT}, 0)`)
          .append("text")
          .attr("transform", "rotate(-90)")
          .attr("y", -65)
          .attr("x", -120)
          .attr("stroke", "#fff")
          .attr("dy", ".71em")
          .style("font-size", "16px")
          .text("Frequency"); // y axis label

        bar
          .append("rect") // Each bar of the histogram
          .attr("height", function (d) {
            return height / 2 - y(d.value);
          })
          .attr("width", barWidth)
          .attr("y", function (d) {
            return y(d.value);
          })
          .attr("transform", `translate(${MARGIN_LEFT}, ${-MARGIN_TOP})`)
          .attr("fill", function (d) {
            return "rgb(163, " + (325 - Math.floor(d.value / 0.1)) + ", 0)";
          });

        bar
          .append("text") // Values of the x axis
          .attr("y", 0)
          .attr("x", barWidth / 2)
          .attr("dx", ".80em")
          .style("font-size", "13px")
          .attr("stroke", "#fff")
          .attr(
            "transform",
            `rotate(-90) translate(${-(labelY + 50)}, ${labelX + 100})`
          )
          .text(function (d) {
            if (d.key === "-oo") {
              return "";
            } else {
              return d.key.toFixed(5);
            }
          });

        vis
          .append("text") // Histogram name //
          .attr("text-anchor", "middle") // ...
          .attr(
            "transform",
            "translate(" +
              (barWidth * array.length + MARGIN_TOP) / 2 +
              "," +
              -10 +
              ")"
          ) // Position
          .attr("stroke", "#fff") // Color white
          .text(variableName); // Histogram label

        document
          .getElementById("histogram" + variableName)
          .setAttribute("style", "display: block");

        this.timer = setTimeout(this.paintHistGraph, 200, index + 1);
      }
    } else {
      messages = null;
      messages = [];
    }
  }

  render() {
    const { variableName } = this.props;
    return <div className="histogram" id={"histogram" + variableName} />;
  }
}

export default MonteCarlo;
