// Action types

const PROGRAMMATIC_EDIT_AREA_LABEL = "PROGRAMMATIC_EDIT_AREA_LABEL";
const TOGGLE_EDITING_AREA_LABEL_STATUS = "TOGGLE_EDITING_AREA_LABEL_STATUS";
const RESET_EDITING_AREA_LABEL = "RESET_EDITING_AREA_LABEL";

// Action constructors

export function edit(isEditing, action, channel, oldExpression, newExpression) {
  return {
    type: PROGRAMMATIC_EDIT_AREA_LABEL,
    isEditing,
    action,
    channel,
    oldExpression,
    newExpression,
  };
}

export function toggleEditingStatus(isEditing, channel) {
  return { type: TOGGLE_EDITING_AREA_LABEL_STATUS, isEditing, channel };
}

export function resetState() {
  return { type: RESET_EDITING_AREA_LABEL };
}

// Reducer

const INITIAL_STATE = {
  isEditing: false,
  action: "",
  channel: undefined,
  oldExpression: "",
  newExpression: "",
};

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case PROGRAMMATIC_EDIT_AREA_LABEL:
      return {
        isEditing: action.isEditing,
        action: action.action,
        channel: action.channel,
        oldExpression: action.oldExpression,
        newExpression: action.newExpression,
      };

    case TOGGLE_EDITING_AREA_LABEL_STATUS:
      return {
        ...state,
        isEditing: action.isEditing,
        channel: action.channel,
      };

    case RESET_EDITING_AREA_LABEL:
      return INITIAL_STATE;

    default:
      // console.error(`areaLabel.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
