// AST List

const ListPrototype = {
  is_list: () => true,
  length: function () {
    return this.elems.length;
  },
  regen: function (separator, terminator) {
    let result = "";
    for (const ast of this.elems) {
      if (result !== "") {
        result += separator;
      }
      result += ast.regen(0);
      if (terminator) {
        result += terminator;
      }
    }
    return result;
  },
  tree: function (separator) {
    let result = "";
    for (const ast of this.elems) {
      if (result !== "") {
        result += separator;
      }
      result += ast.tree();
    }
    return result;
  },
};
Object.setPrototypeOf(ListPrototype, AST);

// Basic Types Prototypes

