const StringPrototype = {
  is_string: function () {
    return true;
  },

  escape: function (s) {
    let r = this.value.replace(/\\/g, "\\\\");
    r = r.replace(/"/g, '\\"');
    return r;
  },

  regen: function () {
    return '"' + this.escape(this.value) + '"';
  },

  tree: function () {
    return "String(" + this.escape(this.value) + ")";
  },
};
Object.setPrototypeOf(StringPrototype, AST);

