const LambdaPrototype = {
  is_lambda: function () {
    return true;
  },
  regen: function () {
    const parlist = this.parlist.regen(", ");
    const rettype = this.rettype.regen();
    const exp = this.exp.regen();
    return "fn(" + parlist + ") => " + rettype + " -> " + exp;
  },
  tree: function () {
    const parlist = this.parlist.tree(", ");
    const rettype = this.rettype.tree();
    const exp = this.exp.tree();
    return "fn(" + parlist + ", " + rettype + ", " + exp + ")";
  },
};
Object.setPrototypeOf(LambdaPrototype, AST);

const CallPrototype = {
  is_call: function () {
    return true;
  },
  regen: function () {
    const args = this.args.regen(", ");
    return this.fun.regen() + "(" + args + ")";
  },
  tree: function () {
    const args = this.args.tree(", ");
    return "Apply(" + this.fun.tree() + ", [" + args + "])";
  },
};
Object.setPrototypeOf(CallPrototype, AST);

const SubPrototype = {
  is_sub: function () {
    return true;
  },
  regen: function () {
    const sub = this.sub.regen(", ");
    return this.array.regen() + "[" + sub + "]";
  },
  tree: function () {
    const sub = this.sub.tree(", ");
    return "Sub(" + this.array.tree() + ", [" + sub + "])";
  },
};
Object.setPrototypeOf(SubPrototype, AST);

const BracePrototype = {
  is_brace: function () {
    return true;
  },
  regen: function () {
    const set = this.set.regen(", ");
    return this.fun.regen() + "{" + set + "}";
  },
  tree: function () {
    const set = this.set.tree(", ");
    return "Brace(" + this.fun.tree() + ", [" + set + "])";
  },
};
Object.setPrototypeOf(BracePrototype, AST);

const InfixBinaryPrototype = {
  is_infix_binary: function () {
    return true;
  },
  lhs: function () {
    return this.args[0];
  },
  rhs: function () {
    return this.args[1];
  },
  regen: function (prio) {
    let result = "";
    const enclose = this.prio < prio;
    if (enclose) {
      result += "(";
    }
    result +=
      this.lhs().regen(this.prio - 1) +
      " " +
      this.op +
      " " +
      this.rhs().regen(this.prio + 1);
    if (enclose) {
      result += ")";
    }
    return result;
  },
  tree: function () {
    return (
      "(" + this.op + " " + this.lhs().tree() + " " + this.rhs().tree() + ")"
    );
  },
};
Object.setPrototypeOf(InfixBinaryPrototype, CallPrototype);

const AssignPrototype = {
  is_assign: function () {
    return true;
  },
  regen: function () {
    const op = this.lazy ? ":-" : ":=";
    return this.lhs.regen() + " " + op + " " + this.rhs.regen() + ";";
  },
  tree: function () {
    const op = this.lazy ? ":-" : ":=";
    return "Assign(" + this.lhs.tree() + " " + op + " " + this.rhs.tree() + ")";
  },
};
Object.setPrototypeOf(AssignPrototype, AST);

const SymDefPrototype = {
  is_symdef: function () {
    return true;
  },
  regen: function () {
    if (!this.init.is_null()) {
      const op = this.lazy ? ":-" : ":=";
      return (
        this.type.regen() +
        " " +
        this.name.regen() +
        " " +
        op +
        " " +
        this.init.regen() +
        ";"
      );
    } else if (!this.name.is_null()) {
      return this.type.regen() + " " + this.name.regen() + ";";
    } else {
      return this.type.regen() + ";";
    }
  },
  tree: function () {
    if (this.init) {
      const op = this.lazy ? ":-" : ":=";
      return (
        "Def(" +
        this.type.tree() +
        " " +
        this.name.tree() +
        " " +
        op +
        " " +
        this.init.tree() +
        ")"
      );
    } else {
      return "Def(" + this.type.tree() + " " + this.name.tree() + ")";
    }
  },
};
Object.setPrototypeOf(SymDefPrototype, AST);

const ParDefPrototype = {
  is_pardef: function () {
    return true;
  },
  regen: function () {
    let type = this.type.regen();
    let name = this.name.regen();
    type = type === "?" ? "" : type;
    name = name === "?" ? "" : name;
    return type + " " + name;
  },
  tree: function () {
    return "Par(" + this.type.tree() + ", " + this.name.tree() + ")";
  },
};
Object.setPrototypeOf(ParDefPrototype, AST);

