import elems from "./tabContent";

// Action types

const ADD_CONTENT = "ADD_CONTENT";
const DELETE_CONTENT = "DELETE_CONTENT";

// Action constructors

export function addTabContent() {
  return { type: ADD_CONTENT };
}

export function deleteTabContent(index) {
  return { type: DELETE_CONTENT, index: index };
}

// Reducer

function reducer(state = [{}], action) {
  if (
    action.type === "ADD_TAB_ELEM" ||
    action.type === "REMOVE_TAB_ELEM" ||
    action.type === "EDIT_EXPRESSION" ||
    action.type === "EDIT_ELEM_VALUE" ||
    action.type === "EDIT_ELEM_STACK" ||
    action.type === "REMOVE_DEFINITION_TYPE" ||
    action.type === "RESET_ELEM_PROPS"
  ) {
    return [
      ...state.slice(0, action.index),
      elems(state[action.index], action),
      ...state.slice(Number(action.index) + 1),
    ];
  }

  switch (action.type) {
    case ADD_CONTENT:
      return [...state, {}];

    case DELETE_CONTENT:
      return state.filter((elem, index) => index !== action.index);

    default:
      // console.error(`tabCCollection.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
