import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as selectedActions from "../state/tabSelected";
import * as tabLabelsActions from "../state/tabs";
import * as tabContentsActions from "../state/tabContentCollection";

import TabLabels from "./TabLabels";

function mapStateToProps(state) {
  return {
    allTabsContent: state.tabContentCollection,
    labelAction: state.ui["info"].labelAction,
    model: state.model,
    selected: state.tabSelected.selected,
    tabs: state.tabs,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    selectedActions: bindActionCreators(selectedActions, dispatch),
    tabLabelsActions: bindActionCreators(tabLabelsActions, dispatch),
    tabContentsActions: bindActionCreators(tabContentsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(TabLabels);
