/// ModelList.js
///
/// model list component (created by Center via ModelListContainer)
///
/// LRQS 6
///

import React from "react";
import classnames from "classnames";

import Draggable from "../gui/Draggable";
import Droppable from "../gui/Droppable";
import Resizable from "../gui/mon-resizable";

function Item(props) {
  const { symbol, state } = props;

  function xColor(state) {
    switch (state) {
      case "F":
        return "#FF9977";
      case "P":
        return "#CCCCCC";
      case "V":
        return "#BBFF66";
      default:
        return "#FF3333";
    }
  }

  function xFontWeight(state) {
    switch (state) {
      case "F":
        return "bold";
      case "P":
      case "V":
      default:
        return "normal";
    }
  }

  return (
    <li
      style={{
        color: xColor(state),
        fontWeight: xFontWeight(state),
      }}
    >
      {symbol}
    </li>
  );
}

class ModelList extends React.Component {
  constructor(props) {
    super(props);

    this.state = { selectedOption: "A", showSpecials: false };

    this.onDragStart = this.onDragStart.bind(this);
    this.onDragEnd = this.onDragEnd.bind(this);
  }

  // Change to a form
  show(symbol) {
    const obj = this.props.model[symbol];
    this.props.updateFeedback(`${obj.symbol}: ${obj.inferred_type}`);
  }

  onDragStart() {
    this.props.dragActions.setDragStatus(true);
  }

  onDragEnd() {
    this.props.dragActions.setDragStatus(false);
  }

  _onDrop(data) {
    const expression = data.drag;
    this.props.undefine(expression);
  }

  render() {
    ///
    /// Note: Resizable now comes from mon-resizable
    /// all upgrades to re-resizable to major versions after 4 failed
    ///

    const { dragStatus, model, showSpecials } = this.props;

    return (
      <div id="model-section">
        <Resizable
          className="resizable-model-section"
          defaultSize={{
            width: 150,
          }}
        >
          <div className="header-label">expression</div>
          <Draggable
            data="expression"
            onDragStart={this.onDragStart}
            onDragEnd={this.onDragEnd}
            style={{ textAlign: "center" }}
            type="expression"
          >
            <li className={"add-expression"} style={{ color: "#BBFF66" }}>
              α * (β + γ)
            </li>
          </Draggable>
          <div className="header-label">
            symbols
          </div>
          <ul id="model-list">
            {Object.keys(model)
              .sort()
              .map((symbol) => {
                const special = symbol.startsWith("@");

                if (special && !showSpecials) {
                  return null;
                } else {
                  return (
                    <Draggable
                      key={symbol}
                      data={symbol}
                      onClick={() => this.show(symbol)}
                      onDragStart={this.onDragStart}
                      onDragEnd={this.onDragEnd}
                      type="drag"
                    >
                      <Item symbol={symbol} state={model[symbol].state} />
                    </Draggable>
                  );
                }
              })}
          </ul>
          <Droppable types={["drag"]} onDrop={this._onDrop.bind(this)}>
            <div
              className={classnames(
                "model-icons",
                dragStatus ? "drop-delete-active" : ""
              )}
            >
              <div style={{ display: "flex", justifyContent: "center" }}>
                <i className="fa fa-trash-o" style={{ cursor: "default" }} />
                <span style={{ paddingTop: "8px", fontSize: "14px" }}>
                  Delete
                </span>
              </div>
            </div>
          </Droppable>
        </Resizable>
      </div>
    );
  }
}

export default ModelList;
