///
/// getColumnCount.js
///
/// Determine the number of columns for the area grid
///
/// LRQS 4
///

export default function getColumnCount(data) {
  let columnCount = 1;

  let NR = data ? data.length : 0; // number of rows of the grid

  for (let i = 0; i < NR; ++i) {
    let current =
      data
        ? data[i]
          ? data[i].fun
            ? 1
            : data[i].args
              ? data[i].args.elems
                ? data[i].args.elems.length
                : 1
              : 1
          : 1
        : 1;

    columnCount = columnCount < current ? current : columnCount;
  }

  return columnCount;
}
