///
/// SimpleVar-Dialog.js
///
/// Modal dialog to define simple variable
///
/// LRQS 6
///

import React from "react";
import ReactModal from "react-modal";

import Initializer from "./Input";
import Lilly from "zll/Lang";
import customStyles from "../gui/custom-styles";

class SVD extends React.Component {
  constructor(props) {
    super(props);

    this.symbolInput = null;

    this.state = {
      type: props.type,
      symbol: Lilly.suggestedSymbol(props.type, props.model),
      aop: Lilly.isLazy(props.type) ? ":-" : ":=",
      initializer: Lilly.defaultValue(props.type),
    };

    this.preview = this.preview.bind(this);
    this.contains = this.contains.bind(this);
    this.onSymbolChange = this.onSymbolChange.bind(this);
    this.onInitChange = this.onInitChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.validate = this.validate.bind(this);
  }

  contains(model, symbol) {
    return model[symbol] && model[symbol].state !== "F";
  }

  preview() {
    const { type, symbol, aop, initializer } = this.state;

    if (!symbol) {
      return "";
    }
    let preview = `${type} ${symbol}`;
    if (initializer.length !== 0) {
      preview += ` ${aop} ${initializer}`;
    }
    preview += ";";
    return preview;
  }

  validate(symbol) {
    const { model, showMessage } = this.props;
    if (model[symbol] && model[symbol].state !== "F") {
      showMessage(`A variable named ${symbol} has already been defined`);
      return false;
    }
    return true;
  }

  onSymbolChange(event) {
    const symbol = event.target.value;
    this.setState({ symbol });
  }

  onInitChange(event) {
    this.setState({ initializer: event.target.value });
  }

  onSubmit(event) {
    event.preventDefault();
    const symbol = this.state.symbol;
    if (this.validate(symbol)) {
      const definition = this.preview();
      if (definition) {
        const payload = { definition, symbol };
        this.props.onSubmit(payload, this.props.addArea);
      } else {
        this.props.showMessage(`The definition is not complete`);
      }
    }
  }

  render() {
    const props = this.props;
    return (
      <ReactModal
        isOpen={true}
        containerClassName="modal-dialog"
        onRequestClose={props.onClose}
        style={customStyles}
        ariaHideApp={false}
      >
        <p style={{ textAlign: "center" }}>{"Define Variable"}</p>

        <form id="definition-pane">
          <i
            onClick={props.onClose}
            id="close-modal-fa-icon"
            className="fa fa-times"
            style={{ float: "right", color: "white" }}
          />

          <div className="definition-setup">
            <label htmlFor="type" className="definition-label">
              {"Type : "}
            </label>
            <input
              disabled
              type="text"
              id="type"
              value={this.state.type}
              style={{ width: "100%", color: "#ffad33" }}
            />
          </div>

          <div className="definition-setup">
            <label htmlFor="symbol" className="definition-label">
              {"Name : "}
            </label>
            <input
              autoFocus={true}
              type="text"
              id="symbol"
              value={this.state.symbol}
              // onBlur={this.validate}
              // onKeyUp={this.validate}
              onChange={this.onSymbolChange}
              style={{ width: "100%", color: "#ffad33" }}
            />
          </div>

          <Initializer
            label="Value : "
            value={this.state.initializer}
            onChange={this.onInitChange}
          />

          <div className="definition-setup">
            <label
              htmlFor="definition"
              style={{ fontSize: "14px", width: "70px", marginBottom: "5px" }}
            >
              {"Preview : "}
            </label>
            <input
              disabled={true}
              type="text"
              id="definition"
              className="definition-disabled-input"
              value={this.preview()}
              style={{ color: "black", height: "30px", marginRight: "1%" }}
            />
          </div>

          <div className="modal-buttons" style={{ float: "right" }}>
            <button type="submit" onClick={this.onSubmit}>
              OK
            </button>
          </div>
        </form>
      </ReactModal>
    );
  }
}

export default SVD;
