import React from "react";

import Comment from "../gui/Comment";

function AreaMenu(props) {
  const {
    canZoomOut,
    zoomOut,
    channel,
    showLabel,
    toggleShowLabel,
    handleRemove,
  } = props;

  return (
    <div className="pop-up-menu">
      {canZoomOut ? (
        <div className="pop-up-menu-item" onClick={event => zoomOut(channel)}>
          <i className="fa fa-arrow-left" />
          <span>"Zoom Out"</span>
        </div>
      ) : null}
      <div className="pop-up-menu-item" onClick={toggleShowLabel}>
        <i className={showLabel ? "fa fa-eye-slash" : "fa fa-eye"} />
        <span>{showLabel ? "Hide Label" : "Show Label"}</span>
      </div>
      <div className="pop-up-menu-item" onClick={handleRemove}>
        <i className="fa fa-times" />
        <span>Remove Area</span>
      </div>
      <Comment>
        <div className="pop-up-menu-item" onClick={null}>
          <i className="fa fa-gear" />
          <span>Setting 1</span>
        </div>
        <div className="pop-up-menu-item" onClick={null}>
          <i className="fa fa-gear" />
          <span>Setting 2</span>
        </div>
        <div className="pop-up-menu-item" onClick={null}>
          <i className="fa fa-gear" />
          <span>Setting 3</span>
        </div>
        <div className="pop-up-menu-item" onClick={null}>
          <i className="fa fa-gear" />
          <span>Setting 4</span>
        </div>
        <div className="pop-up-menu-item" onClick={null}>
          <i className="fa fa-gear" />
          <span>Setting 5</span>
        </div>
        <div className="pop-up-menu-item" onClick={null}>
          <i className="fa fa-gear" />
          <span>Setting 6</span>
        </div>
      </Comment>
    </div>
  );
}

export default AreaMenu;
