// Action types

const ADD_LABEL = "ADD_LABEL";
const DELETE_LABEL = "DELETE_LABEL";
const EDIT_LABEL = "EDIT_LABEL";
const EDIT_CHILDREN_VIEWS = "EDIT_CHILDREN_VIEWS";
const EDIT_LAYOUT = "EDIT_LAYOUT";

// Action constructors

export function addLabel(label) {
  return { type: ADD_LABEL, label };
}

export function deleteLabel(label) {
  return { type: DELETE_LABEL, label };
}

export function editLabel(id, label) {
  return { type: EDIT_LABEL, id, label };
}

// when a new area is added
export function updateChildrenViews(id, childrenViews) {
  return { type: EDIT_CHILDREN_VIEWS, id, childrenViews };
}

export function editLayout(id, layout) {
  return { type: EDIT_LAYOUT, id, layout };
}

// Reducer

function reducer(state = [], action) {
  switch (action.type) {
    case ADD_LABEL:
      return [
        ...state,
        {
          id: state.reduce((maxId, elem) => Math.max(elem.id, maxId), -1) + 1,
          label: action.label,
          childrenViews: [],
          layout: [],
        },
      ];

    case EDIT_LABEL:
      return state.map((elem) =>
        elem.id === action.id
          ? Object.assign({}, elem, { label: action.label })
          : elem
      );

    case DELETE_LABEL:
      return state.filter((elem) => elem.label !== action.label);

    case EDIT_CHILDREN_VIEWS:
      return state.map((elem) =>
        elem.id === action.id
          ? Object.assign({}, elem, { childrenViews: action.childrenViews })
          : elem
      );

    case EDIT_LAYOUT:
      return state.map((elem) =>
        elem.id === action.id
          ? Object.assign({}, elem, { layout: action.layout })
          : elem
      );

    default:
      // console.error(`tabs.js reducer: action ${action} unknown`);
      return state;
  }
}

export default reducer;
