import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as modelActions from "../state/model";
import * as modelDetailsActions from "../state/modelDetails";
import * as tabLabelsActions from "../state/tabs";
import * as selectedActions from "../state/tabSelected";
import * as tabContentsActions from "../state/tabContentCollection";
import * as uiActions from "../state/ui";
import * as editActions from "../state/editing";

import Main from "./Main";

function mapStateToProps(state) {
  return {
    allTabsContent: state.tabContentCollection,
    autoRefresh: state.ui["toggle"].autoRefresh,
    editingLtype: state.ui["info"].editingLtype,
    formulaInput: state.ui["info"].formula,
    logC2S: state.ui["toggle"].logC2S,
    logS2C: state.ui["toggle"].logS2C,
    model: state.model,
    modelDetails: state.modelDetails,
    modelName: state.ui["info"].modelName,
    selected: state.tabSelected.selected,
    showResponseBar: state.ui["toggle"].showResponseBar,
    tabs: state.tabs,
  };
}

// Uses the approach of pre-bind action creator to automatically call dispatch
// http://blog.isquaredsoftware.com/2016/10/idiomatic-redux-why-use-action-creators/
function mapDispatchToProps(dispatch) {
  return {
    modelActions: bindActionCreators(modelActions, dispatch),
    modelDetailsActions: bindActionCreators(modelDetailsActions, dispatch),
    tabLabelsActions: bindActionCreators(tabLabelsActions, dispatch),
    selectedActions: bindActionCreators(selectedActions, dispatch),
    tabContentsActions: bindActionCreators(tabContentsActions, dispatch),
    editActions: bindActionCreators(editActions, dispatch),
    uiActions: bindActionCreators(uiActions, dispatch),
    dispatch,
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);

// The function mapDispatchToProps wraps all the action creators
// with the dispatch method and passes them to the component.
