///
/// buildGrid.js
///
/// LRQS 2
///

/* Description: Function that given the data interpreted by the client parser,
 * builds an array of arrays that will be used to render the cells of the grid.

 * Border cases:
 *   If the parser doesn't return any value for a given cell of the array, it
 *   means that it is a formula "?" so it's replaced with an empty string.
 *   If the parser doesn't return a value but another subtree for a given cell,
 *   that means it is a recursive cell so it is regenerated to show a plain string.
 */
function buildGrid(initialData, maxI, maxJ, orientation) {
  let subGrid = [];
  for (let i = 0; i < maxI; ++i) {
    let obj = [];
    for (let j = 0; j < maxJ; ++j) {
      // logger.debug(i, initialData[i]);
      if (initialData[i]) {
        const cell =
          maxJ > 1 && initialData[i].args
            ? initialData[i].args.elems[j]
            : initialData[i];

        if (initialData[i]) {
          let keyLength = Object.keys(initialData[i]).length;
          if (cell) {
            if (j > 0 && !initialData[i].args) {
              obj[j] = undefined;
            } else {
              obj[j] = cell.value
                ? cell.value
                : keyLength === 0
                ? ""
                : cell.regen();
            }
          }

          if (j === 0) {
            if (initialData[i].args) {
              if (initialData[i].args.elems) {
                if (initialData[i].args.elems.length === 0) {
                  obj[j] = "EXPAND";
                }
              }
            }
          }
        }
      } else {
        obj[j] = "";
      }
    }
    if (maxJ === 0) {
      obj = "EXPAND";
    }
    subGrid.push(obj);
  }

  if (orientation === "vertical") return subGrid;

  let temp = [];

  for (let i = 0; i < maxJ; ++i) {
    temp[i] = [];
    for (let j = 0; j < maxI; ++j) {
      if (orientation === "vertical") {
        temp[i][j] = subGrid[i][j];
      } else if (orientation === "horizontal") {
        temp[i][j] = subGrid[j][i];
      }
    }
  }

  return temp;
}

export default buildGrid;
