import React from "react";

function Input(props) {
  return (
    <div className="definition-setup">
      <label className="definition-label">{props.label}</label>
      <input
        type="text"
        placeholder="expression"
        value={props.value}
        onChange={props.onChange}
        style={{ width: "100%" }}
      />
    </div>
  );
}

export default Input;
