import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as areaLabelActions from "../state/areaLabel";
import * as modelDetailsActions from "../state//modelDetails";
import * as uiActions from "../state//ui";

import Area from "./Area";

function mapStateToProps(state) {
  return {
    areaLabel: state.areaLabel,
    formulaInput: state.ui["info"].formula,
    model: state.model,
    selected: state.tabSelected.selected,
    tabs: state.tabs,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    areaLabelActions: bindActionCreators(areaLabelActions, dispatch),
    modelDetailsActions: bindActionCreators(modelDetailsActions, dispatch),
    uiActions: bindActionCreators(uiActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Area);
