import React from "react";

import PaletteContainer from "../palette/PaletteContainer";
import ModelListContainer from "../model-list/ModelListContainer";
import TabPanel from "../panel/TabPanel";

function Center(props) {
  const {
    // Palette
    showPalette,
    addDefinition,

    // ModelList
    showModelList,
    unsubExpression,
    updateFeedback,
    undefine,

    // TabPanel
    send,
    runMonteCarlo,
    // addDefinition, (also in Palette)
    changeExpressionInArea,
    createDefaultTab,
    createMC,
    setCreateMC,
    handleSubscriptions,
    model,
    modelDetails,
    assign,
    showGridBorders,
  } = props;

  return (
    <div id="center">
      {!showPalette ? null : <PaletteContainer addDefinition={addDefinition} />}

      {!showModelList ? null : (
        <ModelListContainer
          unsubExpression={unsubExpression}
          updateFeedback={updateFeedback}
          undefine={undefine}
        />
      )}

      <TabPanel
        send={send}
        runMonteCarlo={runMonteCarlo}
        addDefinition={addDefinition}
        changeExpressionInArea={changeExpressionInArea}
        createDefaultTab={createDefaultTab}
        createMC={createMC}
        setCreateMC={setCreateMC}
        handleSubscriptions={handleSubscriptions}
        model={model}
        modelDetails={modelDetails}
        assign={assign}
        showGridBorders={showGridBorders}
        suppressUX={props.suppressUX}
      />
    </div>
  );
}

export default Center;
