import parse from "zll/Parser";
import CHANNEL from "../../constants/channel";

function parse_symbols(s) {
  const start = s.indexOf("[") + 1;
  const end = s.lastIndexOf("]");
  const list = s.substring(start, end).split(",");
  const symbols = [];
  if (list[0] !== "") {
    for (let i = 0; i < list.length; ++i) {
      const name = list[i].replace(/"/g, "").trim();
      symbols.push(name);
    }
  }

  return symbols;
}

function result(str) {
  const pattern = "=> ";
  const index = str.indexOf(pattern);
  if (index === -1) {
    return "what?";
  }
  return str.substring(index + pattern.length);
}

function _argstart(str, fun) {
  const pattern = fun + "(";
  const index = str.indexOf(pattern);
  if (index === -1) {
    return -1;
  }
  return index + pattern.length;
}

function arg(str, fun) {
  const start = _argstart(str, fun);
  if (start === -1) {
    return null;
  }
  const end = str.lastIndexOf(")");
  const symbol = str.substring(start, end);
  return symbol;
}

function get_tag(message) {
  const start = 0;
  const end = message.indexOf(":");
  const tag = message.substring(start, end);
  return tag;
}

function _get_lhs(message) {
  if (message.indexOf("==>") !== -1) {
    return message.split(" ==> ")[0];
  } else if (message.indexOf(":-") !== -1) {
    return message.split(" :- ")[0];
  } else if (message.indexOf(":=") !== -1) {
    return message.split(" := ")[0];
  }
  return message;
}

function get_expression(tag, message) {
  const leftHandSide = _get_lhs(message);

  if (tag === "ACK") {
    // If a variable is defined via command line
    const arr = leftHandSide.split(" ");
    const result = arr[arr.length - 1];
    return result;
  }

  if (
    arg(message, "type") ||
    arg(message, "ltype") ||
    arg(message, "formula") ||
    arg(message, "value") ||
    arg(message, "length") ||
    arg(message, "dim")
  ) {
    const result = leftHandSide.substring(
      leftHandSide.indexOf("(") + 1,
      leftHandSide.lastIndexOf(")")
    );
    return result;
  }

  if (arg(message, "sys.describe") != null) {
    const result = leftHandSide.substring(
      leftHandSide.indexOf("(") + 2,
      leftHandSide.lastIndexOf(")") - 1
    );
    return result;
  }

  return leftHandSide;
}

function get_rhs(msg) {
  let rhs = "";

  if (msg.indexOf("==>") !== -1) {
    rhs = msg.split(" ==> ")[1];
  } else if (msg.indexOf(":-") !== -1) {
    rhs = msg.split(" :- ")[1];
  } else if (msg.indexOf(":=") !== -1) {
    rhs = msg.split(" := ")[1];
  }

  if (rhs.endsWith(";")) {
    rhs = rhs.substring(0, rhs.length - 1);
  }

  if (rhs.indexOf("\n") !== -1) {
    rhs = msg.substring(msg.indexOf("==>") + 4, msg.length);
  }

  return rhs;
}

function _get_vis(ltype) {
  const count = _get_count(ltype);
  const ast = parse(ltype, "utils::_get_vis 134");

  if (ltype.indexOf("[,") !== -1 || ast.nd) {
    const numDim = ast.nd;
    if (numDim > 1) {
      return "multi-dim-grid";
    }
  }

  if (
    ltype.indexOf("array") !== -1 ||
    ltype.indexOf("struct") !== -1 ||
    ltype === "var" ||
    ltype === "lazy var"
  ) {
    return count + "-grid";
  }

  if (ltype.indexOf("bool") !== -1) return "bool";
  if (ltype.indexOf("int") !== -1) return "int";

  if (ltype.startsWith("fun")) {
    return "check-r-value";
  }

  // Expressions
  if (ltype === "?") return "?";
  if (ltype === "#TYPE!") return "#TYPE!";

  return "plain";
}

function _get_base(ltype) {
  const ast = parse(ltype, "utils::_get_base 167");
  if (
    ast.elems || // struct
    (ast.type && ast.type.elems && !ast.nd)
  ) {
    // lazy struct
    return "struct";
  }

  if (ast.nd) {
    // array
    return "array";
  }

  return "TBD";
}

function get_type(ltype) {
  const visualizeAs = _get_vis(ltype);
  const base = _get_base(ltype);
  const type = { visualizeAs: visualizeAs, base: base };

  return type;
}

function get_header_ids(ast) {
  const h = ast.fun || ast.args.elems[0].fun;

  if (!h) {
    return [];
  }

  const elems = h.elems.elems;
  const headerIDs = [];

  for (const e of elems) {
    if (e.name.name) {
      headerIDs.push(e.name.name);
    }
  }

  return headerIDs;
}

function with_structs(value) {
  return value.indexOf("struct {") !== -1;
}

