// length(A) -row count requested
// vs
// length(A[0]) -column count requested
function get_array_length(message) {
  const ast = parse(message, "utils::get_array_length 237");
  const value = ast.rhs().regen();

  // !! PATCH because sometimes called on non-array
  if (message.indexOf("length") !== -1) {
    // This is useful in case orientation is horizontal
    // let subTree = ast.lhs().args.elems[0];
    // let depth = _get_depth(subTree, 0);
  }
  return value === "#VALUE!" ? 0 : Number(value);
}

function _get_count(ltype) {
  // warning formerly a file scoped variable updated across function calls :-(
  let counter = 0;

  function _count_types(obj, count) {
    if (Object.keys(obj).length === 0) {
      counter = count > counter ? count : counter;
      return count;
    } else if (obj.type && obj.nd) {
      // array
      _count_types(obj.type, count + 1);
    } else if (obj.type && obj.name) {
      // struct field
      _count_types(obj.type, count);
    } else if (obj.elems) {
      // struct
      for (let elem of obj.elems.elems) {
        _count_types(elem, count + 1);
      }
    } else {
      counter = count > counter ? count : counter;
      return count;
    }
  }

  const ast = parse(ltype, "utils::_get_count 284");

  if (Object.keys(ast).length !== 0) {
    _count_types(ast, 0);
  }

  if (counter > 2) {
    return 3;
  } else {
    return counter;
  }
}

/* To request a portion of the grid, the notation A[1..2] is used */
function _is_subgrid_requested(lhs) {
  return lhs.indexOf("..") !== -1;
}

function _get_low_row_index(lhs_tree) {
  let min = lhs_tree.sub.elems[0].args[0].value; // i.e., A[0..1]

  if (lhs_tree.array.sub) {
    if (lhs_tree.array.sub.elems[0].args) {
      const rowSlice = lhs_tree.array.sub.elems[0].args;
      min = rowSlice[0].value;
    }
  }

  return min;
}

function _get_upper_row_index(lhs_tree) {
  let max = lhs_tree.sub.elems[0].args[1].value;

  if (lhs_tree.array.sub) {
    if (lhs_tree.array.sub.elems[0].args) {
      const rowSlice = lhs_tree.array.sub.elems[0].args;
      max = rowSlice[1].value;
    }
  }

  return max;
}

function get_array_limits(channel, expression, rowCount, minimumBatchSize) {
  const lhs_tree = parse(expression, "utils::get_array_limits 329");
  const l_value = lhs_tree.regen();
  const bufferLimit = minimumBatchSize * 2 - 1;
  let min = 0;
  let max =
    rowCount > minimumBatchSize
      ? bufferLimit > rowCount - 1
        ? rowCount - 1
        : bufferLimit
      : rowCount > 0
      ? rowCount - 1
      : rowCount;

  if (_is_subgrid_requested(l_value)) {
    // i.e., A[0..2]
    min = _get_low_row_index(lhs_tree);
    max = _get_upper_row_index(lhs_tree);
  }

  return [min, max];
}

function is_array_index(lhs) {
  return lhs.search(/([A-Z|a-z|ñ])+(\S*)\[/) !== -1 ? true : false;
}

function get_row_index(expression) {
  const ast = parse(expression, "utils::get_row_index 356");
  let row = 0;

  if (typeof ast.array.sub === "undefined") {
    // 2D array
    row = ast.sub.elems[0].value;
  } else {
    row = ast.array.sub.elems[0].value;
  }

  return row;
}

function get_column_index(expression) {
  const ast = parse(expression, "utils::get_column_index 371");
  let column = 0;

  if (typeof ast.array.sub !== "undefined") {
    // like A[1][0]
    column = ast.sub.elems[0].value;
  } else if (ast.sub.elems.length === 2) {
    // 2D: like ws[0, 1]
    column = ast.sub.elems[1].value;
  }

  return column;
}

function get_expr_channel(str, activePanel) {
  let channel = CHANNEL.NONE;
  let rootStr = str;

  if (str.indexOf("[") !== -1) {
    rootStr = str.substring(0, str.indexOf("["));
  }

  for (const key of Object.keys(activePanel)) {
    const expr = activePanel[key].expression;
    const containsIndex = expr.indexOf("[") !== -1;
    let rootExpr = expr;

    if (containsIndex) {
      rootExpr = expr.substring(0, expr.indexOf("["));
    }
    if (
      (containsIndex && rootStr.indexOf(rootExpr) !== -1) || // match strings like s1[2][1]
      (!containsIndex && rootStr === rootExpr)
    ) {
      channel = String(activePanel[key].channel);
    }
  }

  return channel;
}

function get_resize_expr(message) {
  const lhs = _get_lhs(message);
  const ast = parse(lhs, "utils::get_resize_expr 413");

  if (ast.args.elems[0].name) {
    return ast.args.elems[0].name;
  } else {
    return ast.args.elems[0].array.name;
  }
}

function get_struct_fields_length(ltype) {
  const ast = parse(ltype, "utils::get_struct_fields_length 426");
  let length = 0;

  if (ast.elems) {
    length = ast.elems.elems.length;
  } else if (ast.type.elems) {
    length = ast.type.elems.elems.length;
  }

  return length;
}

const stuff = {
  parse_symbols: parse_symbols,
  result: result,
  arg: arg,
  get_tag: get_tag,
  get_expression: get_expression,
  get_rhs: get_rhs,
  get_type: get_type,
  get_header_ids: get_header_ids,
  with_structs: with_structs,
  get_array_length: get_array_length,
  get_array_limits: get_array_limits,
  is_array_index: is_array_index,
  get_row_index: get_row_index,
  get_column_index: get_column_index,
  get_expr_channel: get_expr_channel,
  get_resize_expr: get_resize_expr,
  get_struct_fields_length: get_struct_fields_length,
};

export default stuff;
