///
/// LRQS 3
///

import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as areaLabelActions from "../state//areaLabel";
import * as uiActions from "../state//ui";

import CommandLine from "./CommandLine";

function mapStateToProps(state) {
  return {
    allTabsContent: state.tabContentCollection,
    areaLabel: state.areaLabel,
    assignment: state.ui["assignment"],
    commandLineResponse: state.ui["info"].commandLineResponse,
    defaultDelay: state.ui["info"].defaultDelay,
    editingChannel: state.editing.channel,
    editingExpression: state.editing.expression,
    editingFormula: state.editing.formula,
    formulaInput: state.ui["info"].formula,
    selected: state.tabSelected.selected, // yes, this is necessary!
    showResponseBar: state.ui["toggle"].showResponseBar,
    tabs: state.tabs,
    wideEditor: state.ui["toggle"].wideEditor,
  };
}

// Uses the approach of pre-bind action creator to automatically call dispatch
// http://blog.isquaredsoftware.com/2016/10/idiomatic-redux-why-use-action-creators/
function mapDispatchToProps(dispatch) {
  return {
    areaLabelActions: bindActionCreators(areaLabelActions, dispatch),
    uiActions: bindActionCreators(uiActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(CommandLine);
