import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as editActions from "../state/editing";
import * as modelDetailsActions from "../state/modelDetails";
import * as uiActions from "../state/ui";

import Value from "./Value";

function mapStateToProps(state) {
  return {
    assignment: state.ui["assignment"],
    contextMenu: state.ui["info"].currentContextMenu,
    editing: state.editing.editing,
    editingChannel: state.editing.channel,
    editingCoords: state.ui["info"].editingCoords,
    editingFormula: state.editing.formula,
    formulaInput: state.ui["info"].formula,
    editingLtype: state.ui["info"].editingLtype,
    model: state.model,
    wideEditor: state.ui["toggle"].wideEditor,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    editActions: bindActionCreators(editActions, dispatch),
    modelDetailsActions: bindActionCreators(modelDetailsActions, dispatch),
    uiActions: bindActionCreators(uiActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Value);
