import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as dragAndDropActions from "../state/dragAndDrop";
import * as uiActions from "../state/ui";

import ModelList from "./ModelList";

function mapStateToProps(state) {
  return {
    model: state.model,
    dragStatus: state.dragAndDrop.dragStatus,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    dragActions: bindActionCreators(dragAndDropActions, dispatch),
    uiActions: bindActionCreators(uiActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ModelList);
