import React from "react";
import { ContextMenu, MenuItem } from "react-contextmenu";

import Modal from "../gui/Modal";

class GridContextMenu extends React.Component {
  constructor(props) {
    super(props);

    this.state = { showModal: false, action: "", position: "previous" };

    this._closeModal = this._closeModal.bind(this);
    this.inputValueChanged = this.inputValueChanged.bind(this);
    this.showModalDialog = this.showModalDialog.bind(this);
  }

  componentDidUpdate() {
    const { channel, contextMenu, updateCurrentContextMenu } = this.props;

    const prevContextMenu = contextMenu;
    if (prevContextMenu !== channel) {
      updateCurrentContextMenu(channel);
    }
  }

  insertRow(position, n) {
    const { channel, expression, rowIndex, send } = this.props;

    const exp = expression;
    const dim = 0; // dim === 0 => row, dim === 1 => col
    const pos = position === "above" ? rowIndex : rowIndex + 1;

    send("insertRow", `insert(${exp},${dim},${pos},${n})`, channel);
    this.props.resetContextMenuCoords();
  }

  insertColumn(position, n) {
    const {
      channel,
      columnIndex,
      expression,
      rowIndex,
      send,
      type,
    } = this.props;

    const exp = expression;
    const dim = type === "multi-dim-grid" ? 1 : 0;
    const pos = position === "left" ? columnIndex : columnIndex + 1;

    const cell = dim === 0 ? `[${rowIndex}]` : "";

    send("insertColumn", `insert(${exp}${cell},${dim},${pos},${n})`, channel);
    this.props.resetContextMenuCoords();
  }

  deleteRow(n) {
    const { channel, expression, rowIndex, send } = this.props;

    const exp = expression;
    const dim = 0; // dim === 0 => row, dim === 1 => col
    const pos = rowIndex;

    send("deleteRow", `delete(${exp},${dim},${pos},${n})`, channel);
    this.props.resetContextMenuCoords();
  }

  deleteColumn(n) {
    const {
      channel,
      columnIndex,
      expression,
      rowIndex,
      send,
      type,
    } = this.props;

    const exp = expression;
    const dim = type === "multi-dim-grid" ? 1 : 0;
    const pos = columnIndex;

    const cell = dim === 0 ? `[${rowIndex}]` : "";

    send("deleteColumn", `delete(${exp}${cell},${dim},${pos},${n})`, channel);
    this.props.resetContextMenuCoords();
  }

  showModalDialog(action) {
    this.setState({
      showModal: true,
      action: action,
    });
  }

  _closeModal() {
    this.setState({ showModal: false });
  }

  _saveModalChanges() {
    const { action, position } = this.state;
    const n = this.refs.N.value;

    if (action.startsWith("row")) {
      if (action.indexOf("insert") !== -1) {
        const pos = position === "previous" ? "above" : "below";
        this.insertRow(pos, n);
      } else {
        this.deleteRow(n);
      }
    } else {
      if (action.indexOf("insert") !== -1) {
        const pos = position === "previous" ? "left" : "right";
        this.insertColumn(pos, n);
      } else {
        this.deleteColumn(n);
      }
    }

    this._closeModal();
  }

  _modalHeader(action, gridHasColumns) {
    const direction = action.startsWith("row") ? "row" : "column";

    if (action.indexOf("insert") !== -1) {
      if (gridHasColumns) {
        return direction === "row" ? "Insert N rows" : "Insert N columns";
      } else {
        return "Insert N elements";
      }
    } else {
      if (gridHasColumns) {
        return direction === "row" ? "Delete N rows" : "Delete N columns";
      } else {
        return "Delete N elements";
      }
    }
  }

  inputValueChanged(e) {
    this.setState({ position: e.target.value });
  }

  _resizing(gridHasColumns) {
    return (
      <div>
        <div>
          <MenuItem onClick={this.insertRow.bind(this, "above", 1)}>
            Insert {gridHasColumns ? "row" : "element"} before
          </MenuItem>
          <MenuItem onClick={this.insertRow.bind(this, "below", 1)}>
            Insert {gridHasColumns ? "row" : "element"} after
          </MenuItem>
          <MenuItem onClick={this.showModalDialog.bind(this, "row-insert")}>
            Insert {gridHasColumns ? "rows" : "elements"} ...
          </MenuItem>
        </div>

        {gridHasColumns ? (
          <div>
            <MenuItem onClick={this.insertColumn.bind(this, "left", 1)}>
              Insert column left
            </MenuItem>
            <MenuItem onClick={this.insertColumn.bind(this, "right", 1)}>
              Insert column right
            </MenuItem>
            <MenuItem
              onClick={this.showModalDialog.bind(this, "column-insert")}
            >
              Insert columns ...
            </MenuItem>
          </div>
        ) : null}

        <div className={"delete-section"}>
          <MenuItem onClick={this.deleteRow.bind(this, 1)}>
            Delete {gridHasColumns ? "row" : "element"}
          </MenuItem>
          <MenuItem onClick={this.showModalDialog.bind(this, "row-delete")}>
            Delete {gridHasColumns ? "rows" : "elements"} ...
          </MenuItem>
        </div>

        {gridHasColumns ? (
          <div>
            <MenuItem onClick={this.deleteColumn.bind(this, 1)}>
              Delete column
            </MenuItem>
            <MenuItem
              onClick={this.showModalDialog.bind(this, "column-delete")}
            >
              Delete columns ...
            </MenuItem>
          </div>
        ) : null}
      </div>
    );
  }

  render() {
    const {
      canZoomOut,
      channel,
      id,
      rowIndex,
      columnIndex,
      onZoomIn,
      rtype,
      type,
      zoomOut,
    } = this.props;

    const { action, position, showModal } = this.state;

    const gridHasColumns = type === "multi-dim-grid";
    const symbolRtype = rtype || "";

    return (
      <div>
        <ContextMenu id={id}>
          {symbolRtype.startsWith("array") ||
          symbolRtype.startsWith("struct") ? (
            <div>
              <MenuItem onClick={onZoomIn.bind(this, rowIndex, columnIndex)}>
                Zoom In
              </MenuItem>
              {canZoomOut ? (
                <MenuItem onClick={event => zoomOut(channel)}>
                  Zoom Out
                </MenuItem>
              ) : null}
            </div>
          ) : null}

          {this.props.base !== "struct" &&
          type !== "0-grid" &&
          type !== "#TYPE!"
            ? this._resizing(gridHasColumns)
            : null}
        </ContextMenu>

        <Modal
          containerClassName="modal-dialog"
          closeOnOuterClick={true}
          show={showModal}
          onClose={this._closeModal}
        >
          <i
            onClick={this._closeModal}
            id="close-modal-fa-icon"
            className="fa fa-times"
          />
          <p>{this._modalHeader(action, gridHasColumns)}</p>
          <br />
          <div className="modal-body">
            <div className="setting-elem-container">
              <div className="setting-label">
                <label>N:</label>
              </div>

              <div className="setting-value">
                <input
                  type="number"
                  className={"input-value"}
                  ref="N"
                  placeholder="Insert a number"
                  defaultValue={1}
                />
              </div>
            </div>

            {action.indexOf("insert") !== -1 ? (
              <ButtonGroup
                action={action}
                handleChange={e => this.inputValueChanged(e)}
                value={position}
              />
            ) : null}
          </div>{" "}
          {/* .modal-body */}
          <br />
          <div className="modal-button">
            <button onClick={this._saveModalChanges.bind(this)}>Ok</button>
          </div>
        </Modal>
      </div>
    );
  }
}

class ButtonGroup extends React.Component {
  constructor(props) {
    super(props);

    this._handleInputChange = this._handleInputChange.bind(this);
  }

  _handleInputChange(e) {
    this.props.handleChange(e);
  }

  render() {
    const { action, value } = this.props;

    return (
      <div className={"setting-elem-container"}>
        <div className="setting-label">
          <p>Position:</p>
        </div>
        <div className="setting-value">
          <label style={{ color: "black" }}>
            <input
              type="radio"
              name="option"
              value="previous"
              onChange={this._handleInputChange}
              checked={value === "previous"}
            />
            {action.startsWith("row") ? "Above" : "Left"}
          </label>
          <label style={{ color: "black" }}>
            <input
              type="radio"
              name="option"
              value="next"
              onChange={this._handleInputChange}
              checked={value === "next"}
            />
            {action.startsWith("row") ? "Below" : "Right"}
          </label>
        </div>
      </div>
    );
  }
}

export default GridContextMenu;
