import CHANNEL from "../../constants/channel";

function genSchema(self) {
  self.send(
    "genSchema",
    "type @Coord :- struct {int x; int y; int w; int h; };",
    CHANNEL.SYSTEM_MODEL
  );
  self.send(
    "genSchema",
    "type @XArea :- struct {string exp; @Coord coord; };",
    CHANNEL.SYSTEM_MODEL
  );
  self.send(
    "genSchema",
    "type @Group :- struct {string label; array [] => @XArea xarea; };",
    CHANNEL.SYSTEM_MODEL
  );
  self.send(
    "genSchema",
    "type @UXM :- struct {string name; array [] => @Group group; };",
    CHANNEL.SYSTEM_MODEL
  );
}

// flushes visual model to the ZenSheet Server
function flushUXM(self, modelName) {
  const { allTabsContent, model, tabs } = self.props;

  // if missing, generate the UX schema and an empty UX model first
  if (!model["@UXM"]) {
    genSchema(self);
    const uxm = `@UXM @uxm := @UXM("${modelName}", []);`;
    self.send("flushUXM", uxm, CHANNEL.SYSTEM_MODEL);
  }

  // serialize groups
  const nGroups = tabs.length;
  let groups = "";

  for (let i = 0; i < nGroups; ++i) {
    groups += 0 < i ? "," : "";
    const label = tabs[i].label;
    groups += `@Group(`;
    groups += `"${label}", [`;
    const nAreas = tabs[i].childrenViews.length;
    let first = true;
    for (let j = 0; j < nAreas; ++j) {
      const channel = tabs[i].childrenViews[j];
      const xchan = allTabsContent[i][channel];
      if (xchan) {
        const expression = xchan.expression;
        let coords = tabs[i].layout.filter((area) => {
          return area.i === String(channel);
        });
        coords = coords[0];
        const x = String(coords.x);
        const y = String(coords.y);
        const w = String(coords.w);
        const h = String(coords.h);

        if (first) {
          first = false;
        } else {
          groups += ", ";
        }
        groups += `@XArea("${expression}", @Coord(${x}, ${y}, ${w}, ${h}))`;
      }
    }
    groups += "])";
  }

  // flush UX model
  const uxm = `@uxm := @UXM("${modelName}", [${groups}]);`;
  self.send("flushUXM", uxm, CHANNEL.SYSTEM_MODEL);
}

export default flushUXM;
